export default function Ajax(data = {}, pathname, success, error, isFull) {

    const formData = new FormData()

    Object.keys(data).forEach(key => {
        formData.append(key, data[key])
    })

    fetch(isFull === true ? pathname : 'https://php.mindclickon.com/app' + pathname, {
        method: 'POST',
        body: formData
    })
        .then(response => response.json())
        .then(json => success(json))
        .catch(err => error(err))
}