import React from "react";

const MoreButton = ({onClick = null, size = 1}) => {
    return (
        <div style={{
            width: 100 * size,
            height: 50 * size,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: 'dodgerblue',
            color: '#fff',
            fontWeight: 700 * size,
            fontSize: 40 * size,
            borderRadius: 10 * size,
            boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
            cursor: 'pointer'
        }} onClick={onClick}>
            +
        </div>
    )
}

export default MoreButton