import { useEffect, useRef } from 'react';
import { useParams } from 'react-router';
import useWebRTC, { LOCAL_VIDEO } from '../../hooks/useWebRTC';

export default function Room({ location: { state } }) {
  const { id: roomID, offer } = useParams();
  const {
    clients,
    provideMediaRef,
    id,
    leave,
    enableScreenSharing
  } = useWebRTC(roomID, offer);

  const micro = useRef(true)
  const video = useRef(true)

  useEffect(() => {
    setTimeout(() => {
      alert("    ,     !")
    }, 2000);
  }, [])

  return (
    <div style={{
      width: "100%",
      height: "100%",
      display: 'flex',
      flexWrap: 'wrap',
      backgroundColor: '#111'
    }}>
      {clients.map((clientID) => {
        return (
            <video
              id={clientID}
              key={clientID}
              style={{
                flex: 1,
                minWidth: '50%',
                minHeight: '50%'
              }}
              ref={instance => {
                provideMediaRef(clientID, instance);
              }}
              autoPlay
              playsInline
              muted={clientID === LOCAL_VIDEO}
            />
        );
      })}
      <div style={{
        padding: '10px 30px',
        minWidth: 200,
        height: 70,
        background: '#222',
        // borderTopRightRadius: 5,
        // borderTopLeftRadius: 5,
        borderRadius: 5,
        position: 'absolute',
        bottom: 0,
        left: 'calc(50% - 100px)',
        display: 'flex',
        alignItems: 'center',
        marginBottom: 10
      }}>
        <div style={{
          padding: 10,
          // background: '#',
          width: 50,
          minWidth: 50,
          height: 50,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 30,
          // boxShadow: '0 0 4px rgba(100, 100, 100, .2)',
          cursor: 'pointer',
          userSelect: 'none',
          marginRight: 20
        }} onClick={e => {
          e.preventDefault()
          if(micro.current) document.getElementById('microphone-btn').src = '/images/call/microphone-off.svg'
          else document.getElementById('microphone-btn').src = '/images/call/microphone.svg'
          if(micro.current) micro.current = false
          else micro.current = true
        }}>
          <img id='microphone-btn' src='/images/call/microphone.svg' width={25} height={25} />
        </div>
        <div style={{
          padding: 10,
          // background: 'dodgerblue',
          // boxShadow: '0 0 4px rgba(100, 100, 100, .2)',
          width: 50,
          minWidth: 50,
          height: 50,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 30,
          cursor: 'pointer',
          userSelect: 'none',
          marginRight: 20
        }} onClick={e => {
          e.preventDefault()
          if(video.current) document.getElementById('video-btn').src = '/images/call/no-video.png'
          else document.getElementById('video-btn').src = '/images/call/camera.png'
          if(video.current) video.current = false
          else video.current = true
        }}>
          <img id='video-btn' src='/images/call/camera.png' width={32} height={32} />
        </div>
        <div style={{
          padding: 10,
          // background: 'dodgerblue',
          // boxShadow: '0 0 4px rgba(100, 100, 100, .2)',
          width: 50,
          minWidth: 50,
          height: 50,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 30,
          cursor: 'pointer',
          userSelect: 'none',
          marginRight: 20,
          position: 'relative'
        }}>
          <label style={{
            position: 'absolute',
            top: 3, right: 3,
            color: '#fefefe',
            fontWeight: 600,
            fontSize: 12
          }}>1</label>
          <img src='/images/call/group.png' width={32} height={32} />
        </div>
        <div style={{
          padding: 10,
          // background: 'dodgerblue',
          // boxShadow: '0 0 4px rgba(100, 100, 100, .2)',
          width: 50,
          minWidth: 50,
          height: 50,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 30,
          cursor: 'pointer',
          userSelect: 'none',
          marginRight: 20,
        }} onClick={e => {
          e.preventDefault()
          enableScreenSharing()
        }} >
          <img src='/images/call/screen.png' width={32} height={32} />
        </div>
        <div style={{
          padding: 10,
          background: 'orangered',
          width: 50,
          minWidth: 50,
          height: 50,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 30,
          boxShadow: '0 0 4px rgba(100, 100, 100, .2)',
          cursor: 'pointer',
          userSelect: 'none',
          // marginRight: 20
        }} onClick={e => {
          leave()
        }}>
          <img src='/images/call/end-call.svg' width={25} height={25} />
        </div>
      </div>
    </div>
  );
}