import React from 'react';

import $ from 'jquery';
import Button from '../../../components/Button'

import { useHistory } from 'react-router';

const CallsContainer = ({state, call}) => {
    const History = useHistory()

    return (
        <div style={{
            display: 'none'
        }} className="container-teacher" id="calls-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/teacher/group.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">My Calls</label>
                        <label className="translate">Join and create calls!</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content" style={{
                padding: 0,
                margin: 0,
                width: '100%'
            }}>
                <div className={'calls-history-title'} style={{
                    width: '100%',
                    height: 45,
                    minHeight: 45,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                    backgroundColor: '#f9f9f9',
                    boxShadow: '0 0 10px #ccc',
                    borderBottom: '1px solid #ccc'
                }}>
                    {/* Title */}
                    <div style={{
                        paddingLeft: 20,
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontWeight: 600
                    }} className="translate">
                        Calls history
                    </div>
                    {/* Right Buttons */}
                    <div style={{
                        width: 100,
                        height: '100%',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-evenly'
                    }}>
                        <div className="translate" data-bottom="1" data-title="New call" style={{
                            width: 50,
                            height: 35,
                            borderRadius: 5,
                            boxShadow: '0 0 3px #ccc',
                            backgroundColor: 'dodgerblue',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            cursor: 'pointer'
                        }} onClick={e => {
                            e.preventDefault()
                            $('#call-modal').css({
                                display: 'flex'
                            })
                            $('#call-history').css({
                                display: 'none'
                            })
                        }}>
                            <img width="15" height="15" src="/images/add.svg" />
                        </div>
                    </div>
                </div>
                {/* History */}
                <div id="call-history" style={{
                    width: '100%',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column'
                }}>
                    <div style={{
                        width: '100%',
                        height: 50,
                        minHeight: 50,
                        display: 'flex',
                        alignItems: 'center',
                        paddingLeft: 10,
                        borderBottom: '1px solid #ccc',
                        backgroundColor: '#fff'
                    }}>  23:45</div>
                </div>
                {/* Modal */}
                <div id="call-modal" style={{
                    width: '60%',
                    height: '80%',
                    backgroundColor: '#fff',
                    borderRadius: 10,
                    alignItems: 'center',
                    paddingTop: 10,
                    marginTop: 20,
                    boxShadow: '0 0 10px #ccc',
                    flexDirection: 'column',
                    display: 'none'
                }}>
                    {/* Close Modal */}
                    <div style={{
                        color: '#ddd',
                        fontWeight: 'bold',
                        fontSize: 25,
                        alignSelf: 'flex-end',
                        cursor: 'pointer',
                        marginRight: 20,
                        transitionDuration: '.2s'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#call-modal').css({
                            display: 'none'
                        })
                        $('#call-history').css({
                            display: 'flex'
                        })
                    }} onMouseEnter={(e) => {
                        e.preventDefault()
                        e.target.style.color = '#aaa'
                    }} onMouseLeave={e => {
                        e.preventDefault()
                        e.target.style.color = '#ddd'
                    }}>&times;</div>
                    <label className="translate" style={{
                        marginTop: -20,
                        marginBottom: 10
                    }}>New Call</label>
                    <div style={{
                        width: '90%',
                        height: '100%',
                        overflowY: 'auto',
                        overflowX: 'hidden',
                        display: 'flex',
                        flexDirection: 'column',
                    }}>
                        <label style={{
                            fontSize: 16,
                            fontWeight: 400,
                            marginBottom: 5
                        }} className="translate">Conference Name:</label>
                        <input type="text" maxLength="20" id="call-name" placeholder=" ..." style={{
                            width: 200,
                            height: 50,
                            border: '1px solid #ccc',
                            outline: '0',
                            borderRadius: 5,
                            boxShadow: '0 0 4px #ccc',
                            paddingLeft: 10,
                            fontSize: 14,
                            marginLeft: 10,
                            minHeight: 50
                        }} />
                        {/* Students List */}
                        <label style={{
                            marginTop: 10,
                            fontWeight: 400,
                            fontSize: 16
                        }} className="translate">Students</label>
                        <div id="call-students" style={{
                            paddingLeft: 10,
                            fontSize: 14
                        }}>
                            <div id="allll" style={{
                                display: 'flex',
                                alignItems: 'center',
                                marginTop: 10
                            }}>
                                <input id="all-students" style={{
                                    marginRight: 5
                                }} type="checkbox" onChange={e => {
                                    if ($(e.target).prop('checked'))
                                        $('#call-students div input').each((index, el) => {
                                            $(el).prop('checked', true)
                                            if (el.id !== 'all-students') {
                                                $(el).prop('disabled', true)
                                            }
                                        })
                                    else {
                                        $('#call-students div input').each((index, el) => {
                                            $(el).prop('checked', false)
                                            if (el.id !== 'all-students') {
                                                $(el).prop('disabled', false)
                                            }
                                        })
                                    }
                                }} /><label className="translate">All students</label>
                            </div>
                            {/* <div style={{
                                display: 'flex',
                                alignItems: 'center',
                                marginTop: 10
                            }}>
                                <input style={{
                                    marginRight: 5
                                }} type="checkbox" /><label> </label>
                            </div> */}

                        </div>
                        <div style={{
                            marginTop: 10
                        }}>
                            <Button className="translate" text={"Start Conference"} style={{
                                width: 200,
                                height: 25,
                                minHeight: 40,
                                fontSize: 14,
                                marginBottom: 10
                            }} onClick={e => {
                                e.preventDefault()
                                const conference_name = $('#call-name').val()
                                if (!conference_name) return
                                const users = []
                                $('#call-students div input').each((index, el) => {
                                    if ($(el).prop('checked') && el.id !== 'all-students') users.push($(el).parent().find('label').text())
                                })
                                if (users.length <= 0) return
                                call(users, conference_name)
                                History.push('/room/' + state.class.room_id + '/0', {
                                    name: state.name
                                })
                            }} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CallsContainer;