import { useEffect, useRef, useState } from 'react'
import socket from '../socket'

const ACTIONS = {
    JOIN: 'join-room',
    JOINED: 'joined-room',
    LEAVE: 'leave-room',
    ADD_MEMBER: 'add-member',
    INCOMING_CALL: 'incoming-call',
    CALL: 'call',
    ACCEPT_CALL: 'accept-call'
}

const useCalls = (name, history, showPush, room) => {
    const [clients, updateClients] = useState({})

    const [callUserID, updateCallUserID] = useState('')

    useEffect(() => {
        socket.emit(ACTIONS.JOIN, {
            name: name,
            room: room
        })
    }, [])

    useEffect(() => {
        socket.on(ACTIONS.JOINED, ({ clients }) => {
            console.log('JOINED', clients)
            updateClients(clients)
        })
    }, [])

    useEffect(() => {
        socket.on(ACTIONS.ADD_MEMBER, ({ name, socketID: id }) => {
            console.log('ADD_MEMBER', { name, id })
            clients[name] = {
                socketID: id
            }
        })
    }, [])

    useEffect(() => {
        if (typeof showPush === 'function') socket.on(ACTIONS.INCOMING_CALL, ({ id, name, room: roomID, title }) => {
            console.log('INCOMING CALL', { id, name, room: roomID, title })
            showPush(' "' + title + '" ' + name)
            updateCallUserID(id)
        })
    }, [])

    useEffect(() => {
        socket.on(ACTIONS.ACCEPT_CALL, () => {
            console.log('ACCEPT CALL', room)
        })
    }, [])

    const acceptCall = () => {
        console.log(callUserID)
        socket.emit(ACTIONS.ACCEPT_CALL, {
            id: callUserID,
        })
        history.push('/room/' + room + '/1', {
            name: name
        })
    }

    const call = (users, title) => {
        socket.emit(ACTIONS.CALL, {
            users: users,
            name: name,
            roomID: room,
            title: title
        })
    }

    return {
        acceptCall,
        call
    }
}

export default useCalls