import React, { useState, useRef, useEffect } from 'react'

import $ from 'jquery'

import StudentList from './Lesson/StudentList'
import Information from './Lesson/Information'
import ColorfulButton from './Lesson/Button'
import StudentModal from './Lesson/StudentsModal'
import StartLessonModal from './Lesson/StartLesonModal'
import LessonEndModal from './Lesson/LessonEndModal'

import Ajax from '../../../lib/ajax'

const TableContainer = ({ state }) => {
    /**
     * Lesson
     */
    const [students, setStudents] = useState([]);
    const [onLesson, setOnLesson] = useState([]);

    const [selected, setSelected] = useState(null);
    const studentsConfig = useRef();

    const [coins, updateCoins] = useState(0);
    const [allCoins, updateAllCoins] = useState(0);
    const [lastCoins, updateLastCoins] = useState(0);

    const [modalShown, updateModalShown] = useState(false);
    const [startModal, updateModalStart] = useState(true);
    const [lessonEnded, updateLessonStatus] = useState(false);

    const time = useRef(0);
    const [timeStr, updateTimeStr] = useState('')
    const [interval, updateInterval] = useState(null)

    function fancyTimeFormat(duration)
    {   
        // Hours, minutes and seconds
        var hrs = ~~(duration / 3600);
        var mins = ~~((duration % 3600) / 60);
        var secs = ~~duration % 60;

        // Output like "1:01" or "4:03:59" or "123:03:59"
        var ret = "";

        if (hrs > 0) {
            ret += "" + hrs + ":" + (mins < 10 ? "0" : "");
        }

        ret += "" + mins + ":" + (secs < 10 ? "0" : "");
        ret += "" + secs;
        return ret;
    }

    const drawTime = () => {
        updateTimeStr(fancyTimeFormat(time.current));
    }

    const start = () => {
        studentsConfig.current = {}
        for(var i = 0; i < students.length; i++)
        {
            studentsConfig.current[students[i].id] = {
                coins: 0
            }
        }
        updateInterval(setInterval(() => {
            time.current++;
            drawTime();
        }, 1000))
    }

    const pause = () => {
        if(interval) clearInterval(interval);
    }

    /**
     * Function which gets all teacher's students.
     */
    const get_students = () => {
        $.ajax({
            method: 'POST',
            url: 'https://php.mindclickon.com/libs/teacher/profile.php',
            data: { id: state.id },
            dataType: 'json',
            success: (resp) => {
                if(!resp.students) {
                    alert('   !')
                } else {
                    setStudents(resp.students)
                    setOnLesson([])
                }
            }
        })
    }

    useEffect(() => {
        get_students()
    }, [])

    /**
     * -- Lesson -- 
     */

    return (
        <div style={{
            display: 'none',
        }} className="container-teacher" id="lesson-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/people.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">Lesson</label>
                        <label className="translate">Spend your lesson in online or offline!</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content" style={{
                margin: 0,
                marginTop: 0,
                width: '100%',
                flex: 1,
                height: 'calc(100% - 100px)',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-evenly',
                backgroundColor: '#fff',
                padding: 20
            }}>
                {/* Modals */}
                <LessonEndModal
                    modalShown={lessonEnded}
                    disableModal={() => {
                        updateLessonStatus(false);
                    }}
                    onClose={() => {
                        updateModalStart(true)
                        setOnLesson([])
                    }}
                    studentCount={onLesson.length}
                    time={timeStr}
                    allCoins={allCoins}
                />

                <StartLessonModal
                    modalShown={startModal}
                    disableModal={() => {
                        updateModalStart(false);
                    }}
                    startEvent={start}
                />

                <StudentModal
                    students={students}
                    modalShown={modalShown}
                    disableModal={() => {
                        updateModalShown(false);
                    }}
                    addStudents={(addingStudents=[]) => {
                        setOnLesson(addingStudents)
                    }}
                    onLesson={onLesson}
                />
                {/* Horizontal Container */}
                <div style={{
                    width: '100%',
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'space-evenly',
                }}>
                    <StudentList
                        students={onLesson}
                        selectStudent={(id) => {
                            for(var i = 0; i < onLesson.length; i++)
                            {
                                if(onLesson[i].id == id)
                                {
                                    console.log(onLesson[i].name)
                                    setSelected(onLesson[i])
                                    updateCoins(studentsConfig.current[id].coins)
                                    return;
                                }
                            }
                        }}
                        timeStr={timeStr}
                    />
                    <Information
                        coins={coins}
                        lastCoins={lastCoins}
                        addCoins={(addingCoins=0) => {
                            studentsConfig.current[selected.id].coins += addingCoins

                            updateCoins(coins + addingCoins)
                            if(addingCoins > 0) updateLastCoins(addingCoins)
                            updateAllCoins(allCoins + addingCoins)
                        }}
                        student={selected}
                        onLesson={onLesson}
                        setOnLesson={setOnLesson}
                        setSelected={setSelected}
                    />
                </div>
                {/* Buttons */}
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    width: '90%',
                    marginTop: 20
                }}>
                    <ColorfulButton
                        text={" "}
                        type={'error'}
                        onClick={() => {
                            pause()

                            for(var i = 0; i < Object.keys(studentsConfig.current).length; i++)
                            {
                                var id = Object.keys(studentsConfig.current)[i];
                                Ajax({
                                    "id": id,
                                    "coins": studentsConfig.current[id].coins
                                }, '/add-coins.php',
                                (resp) => {
                                    console.log(resp);
                                }, (err) => {
                                    console.log(err)
                                }, false)
                            }
                            updateLessonStatus(true)
                        }}
                    />
                    <ColorfulButton
                        text={""}
                        type={'warning'}
                        onClick={(e) => {
                            if(e.target.innerHTML === ''){
                                pause()
                                e.target.innerHTML = ""
                                e.target.style.background = "green"
                            }
                            else {
                                start()
                                e.target.innerHTML = ""
                                e.target.style.background = "orange"
                            }
                        }}
                    />
                    <ColorfulButton
                        text={" "}
                        type={'neutral'}
                        onClick={() => {
                            updateModalShown(true)
                        }}
                    />
                    <ColorfulButton
                        text={"   "}
                        type={'neutral'}
                        onClick={() => {
                            $('#lesson-modal').css({
                                display: 'flex'
                            })
                        }}
                    />
                </div>
                {/* Coins */}
                <div style={{
                    width: '90%',
                    flexDirection: 'row',
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    marginTop: 20,
                    alignItems: 'center'
                }}>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/1.svg'} width={50} draggable={'true'} id="5" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>5 </h4>
                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/2.svg'} width={50} draggable={'true'} id="10" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>10 </h4>
                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/3.svg'} width={50} draggable={'true'} id="30" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>30 </h4>
                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/4.svg'} width={50} draggable={'true'} id="50" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>50 </h4>
                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/5.svg'} width={50} draggable={'true'} id="70" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>70 </h4>
                    </div>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}>
                        <img src={'/images/teacher/coins/6.svg'} width={50} draggable={'true'} id="100" style={{
                            cursor: 'pointer'
                        }} onDragStart={e => {
                            e.dataTransfer.setData('text/plain', e.target.id)
                        }} />
                        <h4>100 </h4>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TableContainer;