import React from "react";

const Button = ({ onClick = () => { }, style = {}, background='linear-gradient(to right, dodgerblue, #6ea5ff)', text, size = 1, className = '', id }) => {
    return (
        <div id={id} style={{
            width: 250 * size,
            height: 50 * size,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRadius: 5 * size,
            boxShadow: '0 0 5px #ccc',
            background: background,
            color: '#fff',
            fontWeight: 500 * size,
            fontSize: 17 * size,
            userSelect: 'none',
            cursor: 'pointer',
            minHeight: 50 * size,
            ...style
        }} onClick={onClick} className={className}>
            {!text ? '' : text}
        </div>
    )
}

export default Button