import React from "react";

const WelcomeModal = () => {
    return (
        <div style={{
            position: 'absolute',
            zIndex: 10,
            display: 'none',
            alignItems: 'center',
            justifyContent: 'center',
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(20, 20, 20, .3)'
        }} id="welcome-modal">
            <div style={{
                width: '70%',
                height: '90%',
                borderRadius: 10,
                backgroundColor: '#fff',
                boxShadow: '0 0 10px rgba(20, 20, 20, .3)',
                padding: '20px 0',
                display: 'flex',
                alignItems: 'center',
                flexDirection: 'column',
                position: 'relative',
                overflow: 'hidden'
            }}>
                {/* Close Modal */}
                <div style={{
                    position: 'absolute',
                    right: 20,
                    bottom: 20,
                    width: 150,
                    height: 40,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    border: '1px solid dodgerblue',
                    borderRadius: 5,
                    fontWeight: 500,
                    color: 'dodgerblue',
                    fontSize: 14,
                    cursor: 'pointer'
                }} id="welcome-button">
                    
                </div>
                {/* Right Page Button */}
                <div style={{
                    position: 'absolute',
                    right: 20,
                    top: 'calc(50% - 15px)',
                    cursor: 'pointer'
                }} id="welcome-right">
                    <img style={{
                        filter: 'opacity(.2)'
                    }} src="/images/call/right-arrow.svg" width="30" height="30" />
                </div>
                {/* Left Page Button */}
                <div style={{
                    position: 'absolute',
                    left: 20,
                    top: 'calc(50% - 15px)',
                    cursor: 'pointer',
                }} id="welcome-left">
                    <img style={{
                        filter: 'opacity(.2)',
                        transform: 'rotate(180deg)'
                    }} src="/images/call/right-arrow.svg" width="30" height="30" />
                </div>
                <div style={{
                    width: '100%',
                    display: 'flex',
                    flexDirection: 'row',
                    overflowX: 'hidden'
                }} id="welcome-list">
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column',
                        width: '100%',
                        minWidth: '100%',
                        maxWidth: '100%'
                    }} id="welcome-page-1">
                        <h2>
                               !
                        </h2>
                        <h4 style={{
                            textAlign: 'center',
                            marginTop: 20
                        }}>
                             ,  ,          !
                        </h4>
                        <img src="/images/edit/update.jpg" width="400" />
                    </div>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column',
                        width: '100%',
                        minWidth: '100%',
                        maxWidth: '100%',
                    }} id="welcome-page-2">
                        <h2>
                              "CRM-"!
                        </h2>
                        <h4 style={{
                            textAlign: 'center',
                            marginTop: 20
                        }}>
                                     !
                        </h4>
                        <img src="/images/edit/crm.jpg" width="400" />
                    </div>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column',
                        width: '100%',
                        minWidth: '100%',
                        maxWidth: '100%'
                    }} id="welcome-page-3">
                        <h2>
                             !
                        </h2>
                        <h4 style={{
                            textAlign: 'center',
                            marginTop: 20
                        }}>
                                     !
                        </h4>
                        <img src="/images/edit/editing.jpg" width="400" />
                    </div>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        flexDirection: 'column',
                        width: '100%',
                        minWidth: '100%',
                        maxWidth: '100%',
                    }} id="welcome-page-4">
                        <h2>
                               !
                        </h2>
                        <h4 style={{
                            textAlign: 'center',
                            marginTop: 20
                        }}>
                               !  ,          !
                        </h4>
                        <img src="/images/edit/complete.jpg" width="400" />
                    </div>
                </div>
                {/* Pages */}
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    position: 'absolute',
                    width: 45,
                    left: 'calc(50% - 22.5px)',
                    bottom: 20
                }}>
                    <span style={{
                        width: 10,
                        height: 10,
                        borderRadius: 5,
                        backgroundColor: '#bbb',
                        marginRight: 5,
                    }} id="welcome-num-1" />
                    <span style={{
                        width: 10,
                        height: 10,
                        borderRadius: 5,
                        backgroundColor: '#ddd',
                        marginRight: 5,
                    }} id="welcome-num-2" />
                    <span style={{
                        width: 10,
                        height: 10,
                        borderRadius: 5,
                        backgroundColor: '#ddd',
                        marginRight: 5,
                    }} id="welcome-num-3" />
                    <span style={{
                        width: 10,
                        height: 10,
                        borderRadius: 5,
                        backgroundColor: '#ddd',
                        marginRight: 5,
                    }} id="welcome-num-4" />
                </div>
            </div>
        </div>
    )
}

export default WelcomeModal;