import React from 'react';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import { styled } from '@mui/material/styles';
import { useHistory } from 'react-router';

const HtmlTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: 'purple',
      color: '#fff',
      maxWidth: 300,
      fontSize: theme.typography.pxToRem(15),
    },
  }));

const Compare = () => {
    const History = useHistory();

    return (
        <div style={{
            width: '100%',
            minHeight: '100%',
            background: "#fefefe",
            backgroundImage: 'url("/images/background.png")',
            backgroundSize: 'cover'
        }}>
            <div style={{
                width: '100%',
                height: 80,
            }}>
                <HtmlTooltip placement="right" title={"     ,        ,    ,     ."}>
                    <img src={"/images/mindclickon.svg"} style={{
                        height: '100%',
                        marginLeft: 20
                    }} onClick={e => {
                        e.preventDefault()
                        History.push("/");
                    }} />
                </HtmlTooltip>
            </div>

            <div style={{
                flex: 1,
                marginTop: 20,
                alignItems: 'center',
                justifyContent: 'space-evenly',
                display: 'flex',
                flexDirection: 'column'
            }}>
                
                <div style={{
                    width: '70%',
                    height: 70,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: 'dodgerblue',
                    color: '#fff',
                    fontWeight: 500,
                    fontSize: 19,
                    borderRadius: 30,
                    marginBottom: 30,
                    boxShadow: '0 0 5px rgba(40, 40, 40, .4)'
                }}>
                     
                </div>
                
                <div style={{
                    width: '70%',
                    height: 70,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: 'dodgerblue',
                    color: '#fff',
                    fontWeight: 500,
                    fontSize: 19,
                    borderRadius: 30,
                    marginBottom: 30,
                    boxShadow: '0 0 5px rgba(40, 40, 40, .4)'
                }}>
                      
                </div>
                
                <div style={{
                    width: '70%',
                    height: 70,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: 'dodgerblue',
                    color: '#fff',
                    fontWeight: 500,
                    fontSize: 19,
                    borderRadius: 30,
                    marginBottom: 30,
                    boxShadow: '0 0 5px rgba(40, 40, 40, .4)'
                }}>
                       
                </div>
            </div>
        </div>
    );
}

export default Compare;