import React, { useEffect, useRef, useState } from 'react'

import '../../../assets/styles/divide.css'

import $ from 'jquery'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faAngleRight, faAngleLeft, faCog, faVolumeMute, faVolumeUp, faUser } from '@fortawesome/free-solid-svg-icons'
import { useHistory } from 'react-router'

const Multiply = ({ location: { state } }) => {
    const History = useHistory()

    const [Signals, updateSignals] = useState(true)
    const [LeftSide, updateLeftSide] = useState(false)
    // const [leftSide, updateLeftSide] = useState(false)

    const expressionsLeft = useRef(state.task ? state.task?.count : 0)

    useEffect(() => {
        var expressions = []
        var isOpen = false;
        var signals = true;
        var isAbacus = false;
        var template = '';
        var isChoosed = false;
        var isSpeed = false;
        var isLeftBarOpened = false;
        var speed = 1.5;
        var students = 1;
        var first = []
        var second = []

        /*** ->ALL LOGIC<- */

        var EXPRESSION = ''
        var RIGHT = 0

        const CORRECT_SOUND = new Audio('/audio/line_open.mp3')
        const INCORRECT_SOUND = new Audio('/audio/incorrect.mp3')

        const Data = [
            {
                value: [1, 1], label: 'A * B',
                first: [
                    '2',
                    '3',
                    '4',
                    '5',
                    '6',
                    '7',
                    '8',
                    '9',
                    '*',
                ],
                second: [
                    '2',
                    '3',
                    '4',
                    '5',
                    '6',
                    '7',
                    '8',
                    '9',
                    '*',
                ]
            },
            {
                value: [2, 1], label: 'AB * C',
                first: [
                    'AA',
                    'A0',
                    '10',
                    '*'
                ],
                second: [
                    '2',
                    '3',
                    '4',
                    '5',
                    '6',
                    '7',
                    '8',
                    '9',
                    '*',
                ]
            },
            {
                value: [3, 1], label: 'ABC * D',
                first: [
                    'AAA',
                    'A0B',
                    'AB0',
                    '100',
                    '*'
                ],
                second: [
                    '2',
                    '3',
                    '4',
                    '5',
                    '6',
                    '7',
                    '8',
                    '9',
                    '*',
                ]
            },
            {
                value: [4, 1], label: 'ABCD * E',
                first: [
                    'A00A',
                    'ABC0',
                    'AB0C',
                    'AAAA',
                    'A0BC',
                    'AB00',
                    'A0B0',
                    '*'
                ],
                second: [
                    '2',
                    '3',
                    '4',
                    '5',
                    '6',
                    '7',
                    '8',
                    '9',
                    '*',
                ]
            },
            {
                value: [2, 2], label: 'AB * CD',
                first: [
                    'AA',
                    'A0',
                    '10',
                    '*'
                ],
                second: [
                    'AA',
                    'A0',
                    '10',
                    '*'
                ]
            },
            {
                value: [3, 2], label: 'ABC * DE',
                first: [
                    'AAA',
                    'A0B',
                    'AB0',
                    '100',
                    '*'
                ],
                second: [
                    'AA',
                    'A0',
                    '10',
                    '*'
                ]
            },
            {
                value: [4, 2], label: 'ABCD * EF',
                first: [
                    'A00A',
                    'ABC0',
                    'AB0C',
                    'AAAA',
                    'A0BC',
                    'AB00',
                    'A0B0',
                    '*'
                ],
                second: [
                    'AA',
                    'A0',
                    '10',
                    '*'
                ]
            },
        ]

        const play_sound = (sound) => {
            if (signals) sound.play()
        }

        const start_expression = () => {
            $('#expression').text(EXPRESSION)
        }

        const get_expression = () => {
            var TEMPLATE = template
            if (!TEMPLATE) return;
            if (template === '*') {
                var index = Math.floor(Math.random() * Data.length)
                console.log(Data[index], index)
                TEMPLATE = Data[index].label
                first = [Data[index].first[Math.floor(Math.random() * Data[index].first.length)]]
                second = [Data[index].second[Math.floor(Math.random() * Data[index].second.length)]]
            }
            $.ajax({
                method: 'POST',
                url: 'https://php.mindclickon.com/app/multiply/expression.php',
                data: {
                    'fSize': String(TEMPLATE.split(' * ')[0].length),
                    'sSize': String(TEMPLATE.split(' * ')[1].length),
                    'first': JSON.stringify(first),
                    'second': JSON.stringify(second),
                },
                dataType: 'json',
                success: (resp) => {
                    if (!resp.status) {
                        alert(' !  !')
                    } else {
                        EXPRESSION = `${resp.first} * ${resp.second}`
                        RIGHT = Number(resp.first) * Number(resp.second)
                        hide($('#loading'))
                        show($('#answer'))
                        $('#answer form input').focus()
                        start_expression()
                    }
                }
            })
        }

        const update_statistic = () => {
            $('#left-bar .content').empty()
            $('#completed').text(String(expressions.length))
            expressions.forEach(expression => {
                $('#left-bar .content').append(`
            <div><span>${expression.expression} = ${expression.your}</span><span>${expression.isRight ? `<label style="color: green;"></label>` :
                        `<label style="color: tomato;"></label>`}<label>${expression.right}</label></span></div>
        `)
            })
        }

        const add_coins = (count, cb) => {
            $.ajax({
                method: 'post',
                url: 'https://php.mindclickon.com/app/add-coins.php',
                data: {
                    id: state.id,
                    coins: count
                }
            })
            .done((resp) => {
                cb(JSON.parse(resp))
            })
        }

        const add_points = (count, cb) => {
            $.ajax({
                method: 'post',
                url: 'https://php.mindclickon.com/app/add-points.php',
                data: {
                    id: state.id,
                    points: count
                }
            })
            .done((resp) => {
                cb(JSON.parse(resp))
            })
        }

        $('#ready').on('click', e => {
            e.preventDefault()
            var isRight = false
            if ($('#answer form input').val().trim().length <= 0) return;
            $('#answer-modal').css({
                top: 100,
            })
            setTimeout(() => {
                $('#answer-modal').css({
                    top: -200,
                })
            }, 1000)
            if (Number($('#answer form input').val()) === RIGHT) {
                $('#answer-modal h2').text('')
                $('#answer-modal h2').css({
                    color: 'green'
                })
                play_sound(CORRECT_SOUND)
                isRight = true
            } else {
                $('#answer-modal h2').text('')
                $('#answer-modal h2').css({
                    color: 'tomato'
                })
                play_sound(INCORRECT_SOUND)
            }
            if(state.task) expressionsLeft.current = expressionsLeft.current - 1
            $('#task-status').text(expressionsLeft.current)
            if(expressionsLeft.current <= 0 && state.task)
            {
                fetch('https://app.mindclickon.com/task', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                            taskID: state.task.id,
                            id: state.id
                        })
                    })
                    .then(res => res.json())
                    .then(json => {
                        console.log(json)
                        if(json.status === true)
                        {
                            add_coins(state.task?.reward, (res_json) => {
                                console.log(res_json)
                                if(res_json.status === true)
                                {
                                    add_points(5, (response) => {
                                        console.log(response)
                                        if(response.status === true)
                                        {
                                            $('#point-add-modal').css({
                                                display: 'flex'
                                            })
                                        }
                                        else {
                                            alert(`    (5)!`)
                                        }
                                    })
                                }
                                else {
                                    alert(`    (${state.task?.reward})!`)
                                }
                            })
                        }
                    })
                    .catch(err => {
                        alert('   !    !')
                        console.log(err)
                    })
            }
            init()
            expressions.push({
                expression: EXPRESSION,
                isRight: isRight,
                your: $('#answer form input').val().trim(),
                right: RIGHT
            })
            $('#answer form input').val('')
            update_statistic()
        })

        const hide = (obj) => {
            obj.css({
                display: 'none'
            })
        }

        const show = (obj) => {
            obj.css({
                display: 'block'
            })
        }

        const init = () => {
            init_graphic()

            setTimeout(get_expression, 1000);
        }

        const checkForTask = () => {
            if(state.task)
            {
                $('#container-sidebar').empty().text('   :)')
                template = state.task.template
                first = state.task.first
                second = state.task.second
                init()
            }
        }

        const init_graphic = () => {
            show($('#loading'))
            hide($('#answer'))
        }

        $(window).ready(() => {
            init_graphic()
            if(state.role === 'student')checkForTask()
        });

        /*** ->ALL LOGIC<- */

        $('#left-button').on('click', e => {
            e.preventDefault()
            if (!isLeftBarOpened) {
                $('#left-bar').css({
                    left: 0,
                })
                setTimeout(() => {
                    $('#left-bar #left-button').empty().append('<i class="fas fa-angle-left"></i>')
                }, 100)
                isLeftBarOpened = true
            } else {
                $('#left-bar').css({
                    left: -300,
                })
                setTimeout(() => {
                    $('#left-bar #left-button').empty().append('<i class="fas fa-angle-right"></i>')
                }, 100)
                isLeftBarOpened = false
            }
        })

        $('#speed-switch').on('change', e => {
            e.preventDefault()
            if (isSpeed) {
                isSpeed = false
                $('#speed-pm').css({
                    opacity: .5
                })
                $('#m-speed').css({
                    cursor: 'default'
                })
                $('#p-speed').css({
                    cursor: 'default'
                })
            } else {
                isSpeed = true
                $('#speed-pm').css({
                    opacity: 1
                })
                $('#m-speed').css({
                    cursor: 'pointer'
                })
                $('#p-speed').css({
                    cursor: 'pointer'
                })
            }
        })

        const init_clicks = () => {
            $('.f-item').on('click', e => {
                e.preventDefault()
                console.log($(e.target).css('background-color'))
                var content = $(e.target).text().trim()
                if ($(e.target).css('background-color') === 'rgb(255, 99, 71)') {
                    first = first.filter(val => val !== content)
                    $(e.target).css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                } else {
                    first.push(content)
                    $(e.target).css({
                        background: 'tomato'
                    })
                }
                if (content === '*') {
                    first = ['*']
                    $('.f-item').each((index, el) => {
                        if ($(el).text().trim() !== '*')
                            $(el).css({
                                background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                            })
                    })
                } else {
                    $('#mix-f').css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                }
            })
            $('.s-item').on('click', e => {
                e.preventDefault()
                var content = $(e.target).text().trim()
                if ($(e.target).css('background-color') === 'rgb(255, 99, 71)') {
                    second = second.filter(val => val !== content)
                    $(e.target).css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                } else {
                    second.push(content)
                    $(e.target).css({
                        background: 'tomato'
                    })
                }
                if (content === '*') {
                    second = ['*']
                    $('.s-item').each((index, el) => {
                        if ($(el).text().trim() !== '*')
                            $(el).css({
                                background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                            })
                    })
                } else {
                    $('#mix-s').css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                }
            })
        }

        const switch_template = () => {
            $('#hidden .first').empty()
            $('#hidden .second').empty()
            if (isChoosed && template !== '*') {
                $('#hidden').css({
                    display: 'block'
                })
                Data.forEach(val => {
                    if (val.label === template) {
                        val.first.forEach(first => {
                            if (first === '*') $('#hidden .first').append(`<div id="mix-f" class="f-item">${first}</div>`)
                            else $('#hidden .first').append(`<div class="f-item">${first}</div>`)
                        })
                        val.second.forEach(second => {
                            if (second === '*') $('#hidden .second').append(`<div id="mix-s" class="s-item">${second}</div>`)
                            else $('#hidden .second').append(`<div class="s-item">${second}</div>`)
                        })
                    }
                })
                init_clicks()
            } else {
                $('#hidden').css({
                    display: 'none'
                })
            }
        }

        $('.item').on('click', e => {
            $('.item').each((index, val) => {
                $(val).css({
                    background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                })
            })
            $(e.target).css({
                background: 'tomato'
            })
            template = $(e.target).text().trim().replace('MIX', '*')
            if (!isChoosed) isChoosed = true
            switch_template()
        })

        $('#close-btn').on('click', e => {
            e.preventDefault()
            $('#side-bar').css({
                right: -500,
            })
            isOpen = false
        })

        $('#save-btn').on('click', e => {
            e.preventDefault()
            init()
        })

        $('#settings-btn').on('click', (e) => {
            e.preventDefault();
            $('#side-bar').css({
                right: 0,
            })
            isOpen = true
        })

        $('#click-c').on('click', (e) => {
            e.preventDefault();
            if (isOpen)
                $('#side-bar').css({
                    right: -500,
                });
            isOpen = false;
        })

        /* Speed */
        $('#m-speed').on('click', e => {
            e.preventDefault()
            if (!isSpeed) return;
            if (speed > .1) {
                speed = (speed * 10 - 1) / 10;
                $('#speed').text(String(speed))
            }
        })

        $('#p-speed').on('click', e => {
            e.preventDefault()
            if (!isSpeed) return;
            if (speed < 3.0) {
                speed = (speed * 10 + 1) / 10;
                $('#speed').text(String(speed))
            }
        })
        /* Speed */

        /* Students */
        $('#m-students').on('click', e => {
            if (students > 1) {
                students--;
                $('#students').text(String(students))
            }
        })

        $('#p-students').on('click', e => {
            if (students < 10) {
                students++;
                $('#students').text(String(students))
            }
        })
        /* Students */

        $('#signal-e').on('click', e => {
            e.preventDefault()
            if (signals) {
                // document.querySelector('#signal-e i').class = 'fas fa-volume-mute'; 
                updateSignals(false)
                signals = false;
            } else {
                // document.querySelector('#signal-e i').class = 'fas fa-volume-up';
                updateSignals(true)
                signals = true;
            }
        })

        $('#abacus-e').on('click', e => {
            e.preventDefault()
            if (isAbacus) {
                document.querySelector('#abacus-e img').src = "/images/abacus-off.png"
                $('#abacus-graphic').css({
                    opacity: 0
                })
                isAbacus = false;
            } else {
                document.querySelector('#abacus-e img').src = "/images/abacus.svg"
                $('#abacus-graphic').css({
                    opacity: 1
                })
                isAbacus = true;
            }
        })
    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%'
        }}>
            {/* 100 RIGHT MODAL */}
            <div id="point-add-modal" style={{
                width: '100%',
                height: '100%',
                display: 'none',
                alignItems: 'center',
                justifyContent: 'center',
                position: 'absolute',
                top: 0,
                left: 0,
                background: 'rgba(40, 40, 40, .2)',
                zIndex: 200
            }}>
                <div id="point-add-modal-content" style={{
                    width: '350px',
                    height: '325px',
                    background: '#fff',
                    borderRadius: 10,
                    boxShadow: '0 0 10px rgba(40, 40, 40, .4)',
                    display: 'flex',
                    flexDirection: 'column',
                    padding: 10,
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    textAlign: 'center'
                }}>
                    <h3> <label style={{
                        fontWeight: 600
                    }}>{state.task?.count ? state.task?.count : -1}</label>  <label style={{
                        color: 'green'
                    }}></label>!</h3>
                    <h4 style={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>: +{state.task?.reward ? state.task?.reward : -1}   5 .<img style={{
                        marginLeft: 5
                    }} src={'/images/award.png'} width={20} height={20} /></h4>
                    <img width={128} height={128} src={'/images/anniversary.png'} style={{
                        filter: 'drop-shadow(0 0 5px rgba(40, 40, 40, .3))'
                    }} />
                    <div style={{
                        width: 150,
                        height: 40,
                        background: 'linear-gradient(to right, dodgerblue, blue)',
                        borderRadius: 10,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        color: '#fefefe',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        cursor: 'pointer',
                        userSelect: 'none'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#point-add-modal').css({
                            display: 'none'
                        })
                        History.replace('/student', state)
                    }}>
                        
                    </div>
                </div>
            </div>

            <div className="divide-header">
                <div onClick={e => {
                    e.preventDefault()
                    History.goBack()
                }} className="back-item">
                    <img src="/images/left-arrow.svg" width="30" height="30" />
                </div>
                <div className="status-text">
                    <label id="completed">0</label>
                    <label>completed</label>
                </div>
                <div className="action-icons" style={{
                    width: state.task ? 400 : 200
                }}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        flexDirection: 'column',
                        color: '#fefefe'
                    }}>
                        <label id='task-status'>{state.task?.count}</label>
                        <label style={{
                            fontSize: 12
                        }}> </label>
                    </div>
                    <div id="abacus-e" className="abacus-item">
                        <img src="/images/abacus-off.png" width="30" height="30" />
                    </div>
                    <div id="signal-e" className="signal-item">
                        <FontAwesomeIcon icon={Signals ? faVolumeUp : faVolumeMute} />
                    </div>
                    <div id="settings-btn" className="settings-item">
                        <FontAwesomeIcon icon={faUser} />
                    </div>
                </div>
            </div>

            <div id="click-c" className="container-mental">
                <img id="abacus-graphic" className="abacus-graphic" src="/images/abacus-graphic.png" width="300" style={{
                    opacity: 0,
                    position: 'absolute',
                    left: 100,
                    bottom: 100,
                    pointerEvents: 'none'
                }} />
                <div id="loading" className="loading">
                    <div className="loader"> ...</div>
                </div>
                <div style={{
                    backgroundColor: '#fff'
                }} id="answer" className="answer">
                    <form>
                        <h3 id="expression">45 * 3</h3>
                        <input type="number" placeholder="..." pattern="\d+" />
                        <button id="ready">
                            
                        </button>
                    </form>
                </div>
                <div id="answer-modal" style={{
                    width: 300,
                    height: 75,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    borderRadius: 10,
                    backgroundColor: '#fff',
                    boxShadow: '0 0 5px #ccc',
                    position: 'absolute',
                    top: -200,
                    left: 'calc(50% - 150)',
                    zIndex: 5,
                    transitionDuration: '.3s'
                }}>

                    <h2></h2>

                </div>
            </div>

            <div id="left-bar" className="left-side-divide">
                <div className="content">
                    <h3 style={{
                        color: '#ccc',
                        textAlign: 'center'
                    }}>
                           ,   !
                    </h3>
                </div>
                <div id="left-button" className="button">
                    <FontAwesomeIcon icon={LeftSide ? faAngleLeft : faAngleRight} />
                </div>
            </div>

            <div id="side-bar" className="side-bar-divide">
                <div className="side-bar-header">
                    <label></label>
                    <div id="close-btn" className="close-btn">
                        &times;
                    </div>
                </div>
                <div className="container-side-bar" id='container-sidebar'>
                    <div className="more-item">
                        <h4> :</h4>
                        <div className="items">
                            <div className="item">
                                A * B
                            </div>
                            <div className="item">
                                AB * C
                            </div>
                            <div className="item">
                                ABC * D
                            </div>
                            <div className="item">
                                ABCD * E
                            </div>
                            <div className="item">
                                AB * CD
                            </div>
                            <div className="item">
                                ABC * DE
                            </div>
                            <div className="item">
                                ABCD * EF
                            </div>
                            <div id="mix" className="item">
                                MIX
                            </div>
                        </div>
                    </div>
                    <div id="hidden" className="hidden">
                        <h3> :</h3>
                        <div className="first">

                        </div>
                        <h3> :</h3>
                        <div className="second">

                        </div>
                    </div>
                    <div id="save-btn" className="save-btn">
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Multiply