import React from 'react';

const ColorfulButton = ({ text, type = 'success' | 'warning' | 'error' | 'neutral', onClick = () => {}, clearMargin = false }) => {
    return (
        <div style={{
            width: 150,
            height: 30,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: 'white',
            backgroundColor: type == "success" ? "green" : type == "warning" ? "orange" : type == "error" ? "crimson" : type == "neutral" ? "dodgerblue" : "#ccc",
            fontSize: 12,
            borderRadius: 5,
            cursor: 'pointer',
            marginRight: clearMargin ? 0 : 10
        }} onClick={onClick}>
            {text}
        </div>
    )
}

export default ColorfulButton;