import React, { useEffect, useRef, useState } from "react";

import '../../../assets/styles/mental.css'

import $ from 'jquery'

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faVolumeUp, faVolumeMute, faAngleRight, faAngleLeft, faUser } from "@fortawesome/free-solid-svg-icons";
import { useHistory } from "react-router";

function MentalCount({ location: { state } }) {
    const History = useHistory()
    const [Signals, updateSignals] = useState(true)
    const [LeftBar, updateLeftBar] = useState(false)

    const expressionsLeft = useRef(state.task ? state.task?.count : 0)

    useEffect(() => {
        var expressions = []
        var isOpen = false;
        var signals = true;
        var isAbacus = false;
        var students = 1;
        var isLeftBarOpened = false

        /*** ->ALL LOGIC<- */
        var TERMS = []
        var RIGHT = []
        var isRights = []
        var IDS = []

        var isModal = false
        var isStart = false
        var isCounting = false

        const close_modal = () => {
            const STUDENTS = Math.min(Number($('#students').text().trim()), 6)
            isModal = false
            for (var i = 0; i < STUDENTS; i++) {
                $('#modal-' + i).css({
                    display: 'none'
                })
                RIGHT = []
                $(`#answer-${i} form input`).val('')
            }
            for (var i = 0; i < STUDENTS; i++) {
                hide($('#answer-' + i))
                hide($('#start-' + i))
                hide($('#back-count-' + i))
                hide($('#count-' + i))
                show($('#loading-' + i))
            }
            get_terms()
        }

        $(window).on('keyup', e => {
            e.preventDefault()
            if (e.keyCode === 13 && isModal) {
                hide($('#answer-modal'))
                close_modal()
            }
            if (e.keyCode === 13 && isStart) {
                start_expression()
            }
        })

        const BACK_COUNT_SOUND = new Audio('/audio/before.mp3')
        const COUNT_SOUND = new Audio('/audio/signal.mpeg')
        const INCORRECT_SOUND = new Audio('/audio/incorrect.mp3')
        const CORRECT_SOUND = new Audio('/audio/line_open.mp3')

        const BACK_COUNT_SOURCES = [
            '',
            ''
        ]

        const play_sound = (sound) => {
            if (signals) sound.play()
        }

        const answer = (index) => {
            const STUDENTS = Math.min(Number($('#students').text().trim()), 6)
            if(!STUDENTS) STUDENTS = 1
            if (TERMS.filter(val => val !== undefined).length < STUDENTS) return
            if (IDS.includes(index)) return;
            IDS.push(index)
            var ANSWER = $(`#answer-${index} form input`).val().trim()
            if (isNaN(Number(ANSWER)) || ANSWER < 0 || !ANSWER) return;
            $('#answer-content').css({
                display: 'flex',
                flexDirection: 'column',
                width: '100%',
            })
            if (isModal) return;
            if (isRights.filter(val => val !== undefined).length + 1 >= STUDENTS) {
                hide($('#answer-' + index))
                $(`#answer-${index} form input`).blur()
                const isRight = Number(ANSWER) === Number(RIGHT[index])
                isRights[index] = {
                    is: isRight,
                    your: ANSWER,
                    right: RIGHT[index]
                }
                expressions.push({
                    expression: TERMS[index],
                    isRight: isRight,
                    your: ANSWER,
                    right: RIGHT[index]
                })
                if(state.task) expressionsLeft.current = expressionsLeft.current - 1
                $('#task-status').text(expressionsLeft.current)
                if(expressionsLeft.current <= 0 && state.task) {
                    fetch('https://app.mindclickon.com/task', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                            taskID: state.task?.id,
                            id: state.id
                        })
                    })
                    .then(res => res.json())
                    .then(json => {
                        console.log(json)
                        if(json.status === true)
                        {
                            add_coins(state.task?.reward, (res_json) => {
                                console.log(res_json)
                                if(res_json.status === true)
                                {
                                    add_points(5, (response) => {
                                        console.log(response)
                                        if(response.status === true)
                                        {
                                            $('#point-add-modal').css({
                                                display: 'flex'
                                            })
                                        }
                                        else {
                                            alert(`    (5)!`)
                                        }
                                    })
                                }
                                else {
                                    alert(`    (${state.task?.reward})!`)
                                }
                            })
                        }
                    })
                    .catch(err => {
                        alert('   !    !')
                        console.log(err)
                    })
                }
                isStart = false
                setTimeout(() => { isModal = true }, 1000)
                show($('#answer-modal'))
                $('#answer-content').empty()
                for (var i = 0; i < STUDENTS; i++) {
                    $('#answer-content').append(`<h1 style="width: 100%; text-align: left; color: ${isRights[i].is === true ? 'green' : 'red'}">${isRights[i].is === true ? '' : ''}</h1>`)
                    const list = $('<ul></ul>').css({
                        display: 'flex',
                        flexDirection: 'column',
                        width: '100%',
                        backgroundColor: '#f1f1f1'
                    })
                    for (var TERM of TERMS[i]) {
                        list.append(`<li>${TERM}</li>`)
                    }
                    $('#answer-content').append(`<h2 style="width: 100%; text-align: left;"> :</h2>`)
                    $('#answer-content').append(`<h3 style="width: 100%; text-align: left;">${isRights[i].your}</h3>`)
                    $('#answer-content').append(`<h2 style="width: 100%; text-align: left;"> :</h2>`)
                    $('#answer-content').append(`<h3 style="width: 100%; text-align: left;">${isRights[i].right}</h3>`)
                    $('#answer-content').append(list)
                    $('#answer-content').append('<hr>')
                }
                IDS = []
                isRights = []
                update_statistic()
                return
            }
            console.log('Continued!')
            hide($('#answer-' + index))
            $(`#answer-${index} form input`).blur()
            const isRight = Number(ANSWER) === Number(RIGHT[index])
            isRights[index] = {
                is: isRight,
                your: ANSWER,
                right: RIGHT[index]
            }
            expressions.push({
                expression: TERMS[index],
                isRight: isRight,
                your: ANSWER,
                right: RIGHT[index]
            })
            show($('#modal-' + index))
            play_sound(isRight ? CORRECT_SOUND : INCORRECT_SOUND)
            update_statistic()
        }

        const get_terms = (indexes = null) => {
            const STUDENTS = Math.min(Number($('#students').text().trim()), 6)
            isStart = false
            if (!indexes) {
                TERMS = []
                for (var i = 0; i < STUDENTS; i++) {
                    var min = Number(EXPRESSION_DATA[i].diapason.split('-')[0])
                    var max = Number(EXPRESSION_DATA[i].diapason.split('-')[1])
                    var formula = EXPRESSION_DATA[i].formula.replace(/\:.+/, '')
                    var term_count = EXPRESSION_DATA[i].terms
                    console.log(min, max, formula, term_count)
                    $.ajax({
                        method: 'POST',
                        url: 'https://app.mindclickon.com/expression',
                        dataType: 'json',
                        data: {
                            'formula': formula,
                            'min': Number(min),
                            'max': Number(max),
                            'term_count': term_count,
                            'isBigger': false,
                            'i': i
                        },
                    })
                        .done((resp) => {
                            if (resp.status === false) {
                                alert('   0x0004')
                            } else {
                                TERMS[resp.i] = resp.expression
                                console.log(TERMS)
                                hide($('#loading-' + String(resp.i)))
                                show($('#start-' + String(resp.i)))
                                if (STUDENTS === TERMS.length && !TERMS.includes(null) && !TERMS.includes(undefined)) {
                                    isStart = true
                                }
                            }
                        })
                }
            } else {
                var COMPLETED = 0
                for (var i of indexes) {
                    var min = Number(EXPRESSION_DATA[i].diapason.split('-')[0])
                    var max = Number(EXPRESSION_DATA[i].diapason.split('-')[1])
                    var formula = EXPRESSION_DATA[i].formula.replace(/\:.+/, '')
                    var term_count = EXPRESSION_DATA[i].terms
                    console.log(min, max, formula, term_count)
                    TERMS[i] = null
                    $.ajax({
                        method: 'POST',
                        url: 'https://app.mindclickon.com/expression',
                        dataType: 'json',
                        data: {
                            'formula': formula,
                            'min': Number(min),
                            'max': Number(max),
                            'term_count': term_count,
                            'isBigger': false,
                            'i': i
                        },
                    })
                        .done((resp) => {
                            if (resp.status === false) {
                                alert('   0x0004')
                            } else {
                                TERMS[resp.i] = resp.expression
                                COMPLETED++
                                console.log(TERMS)
                                hide($('#loading-' + String(resp.i)))
                                show($('#start-' + String(resp.i)))
                                var isQuit = true
                                if (COMPLETED + 1 > indexes.length) {
                                    for (var i2 of indexes) {
                                        if (TERMS[i2] == null || TERMS[i2] == undefined) isQuit = false
                                    }
                                    if (isQuit) {
                                        isStart = true
                                    }
                                }
                            }
                        })
                }
            }
        }

        const EXPRESSION_DATA = []
        var ALL_STUDENTS = []

        const update_students_list = () => {
            const busy_students = {}
            for (var { account } of EXPRESSION_DATA) {
                if (account) busy_students[account.id] = true
            }
            var students = ALL_STUDENTS
            students = students.filter(student => busy_students[student.id] !== true)
            console.log(students)
            const list = $('#students-all-list')
            $(list).empty()
            for (var student of students) {
                const student_item = $(`<div id="data-${student.id}"><img src="/images/teacher/user-circle.svg" style="margin-right: 5px;" width="20" height="20" />${student.name}</div>`).css({
                    display: 'flex',
                    alignItems: 'center',
                    paddingLeft: 5,
                    borderRadius: 5,
                    border: '1px solid #f1f1f1',
                    boxShadow: '0 0 5px #ccc',
                    width: 200,
                    height: 40,
                    // cursor: 'pointer',
                    marginBottom: 10
                }).attr("draggable", 'true').on('dragstart', e => {
                    $('#side-bar').css({
                        right: -400
                    })
                    e.originalEvent.dataTransfer.setData('text', JSON.stringify({
                        name: $(e.target).text(),
                        id: $(student_item).attr('id').replace('data-', '')
                    }))
                })
                $(list).append(student_item)
            }
        }

        const init = () => {
            var STUDENTS = Math.min(Number($('#students').text().trim()), 6)

            if(!STUDENTS) STUDENTS = 1

            $('#click-c').empty()
            for (var i = 0; i < STUDENTS; i++) {
                console.log(`${i}/${STUDENTS}`)
                const toolbar = $('<div></div>').css({
                    display: 'flex',
                    width: '80%',
                    height: 40,
                    borderRadius: '0 0 10px 10px',
                    backgroundColor: '#fff',
                    boxShadow: '0 0 5px #ccc',
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    position: 'absolute',
                    top: 0
                })
                const student_holder = $(`<div id="holder-${i}"></div>`).css({
                    width: 200,
                    height: 35,
                    borderRadius: 5,
                    // backgroundColor: '#f1f1f1',
                    border: '1px dashed #f1f1f1',
                    boxShadow: '0 0 2px #ccc',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    color: '#ccc',
                    fontSize: 12,
                    fontWeight: 500
                }).append(' ').on('dragover', e => {
                    e.preventDefault()
                }).on('drop', e => {
                    e.preventDefault()
                    $('#side-bar').css({
                        right: 0
                    })
                    const data = JSON.parse(e.originalEvent.dataTransfer.getData('text'))
                    $(e.target).css({
                        border: '0',
                        color: '#000',
                    }).empty().append('<img src="/images/teacher/user-circle.svg" style="margin-right: 5px;" width="20" height="20" />' + data.name)
                    EXPRESSION_DATA[e.target.id.replace('holder-', '')].account = data
                    update_students_list()
                })
                const settings_btn = $('<img src="/images/settings-grey.svg" width="20" />').css({
                    cursor: 'pointer'
                }).on('click', e => {
                    e.preventDefault()
                    if (setup.css('display') === 'none')
                        setup.css({
                            display: 'flex',
                        })
                    else {
                        setup.css({
                            display: 'none',
                        })
                    }
                })
                if(state.role === 'teacher')
                    toolbar.append(student_holder)
                else toolbar.append(`<label>${state.name}</label>`).css({
                    width: '400px'
                })
                if(!state.task) toolbar.append(settings_btn)

                const PM_TERMS = $(`<div class="pm-item">
                        <label class="title">
                            - :
                        </label>
                        
                    </div>`)
                const PM_TERMS_CONTAINER = $(`<div class="pm"></div>`)
                const MINUS_TERM = $(`<div id="m-terms-${i}" class="minus">
                <img src="/images/minus.svg" width="20" height="20" />
            </div>`).on('click', e => {
                    e.preventDefault()
                    var index = Number(e.target.id.replace('m-terms-', ''))
                    var newTerm = Math.max(EXPRESSION_DATA[index].terms - 1, 2)
                    EXPRESSION_DATA[index].terms = newTerm
                    PM_TERMS_CONTAINER.find('.content').text(String(newTerm))
                })
                PM_TERMS_CONTAINER.append(MINUS_TERM)
                PM_TERMS_CONTAINER.append(`<div class="content">10</div>`)
                const PLUS_TERM = $(`<div id="p-terms-${i}" class="plus">
                <img src="/images/add.svg" width="20" height="20" />
            </div>`).on('click', e => {
                    e.preventDefault()
                    var index = Number(e.target.id.replace('p-terms-', ''))
                    var newTerm = Math.min(EXPRESSION_DATA[index].terms + 1, 50)
                    EXPRESSION_DATA[index].terms = newTerm
                    PM_TERMS_CONTAINER.find('.content').text(String(newTerm))
                })
                PM_TERMS_CONTAINER.append(PLUS_TERM)
                PM_TERMS.append(PM_TERMS_CONTAINER)

                const PM_SPEED = $(`<div class="pm-item">
                        <label class="title">
                            :
                        </label>
                        
                    </div>`)
                const PM_SPEED_CONTAINER = $(`<div class="pm"></div>`)
                const MINUS_SPEED = $(`<div id="m-speed-${i}" class="minus">
                <img src="/images/minus.svg" width="20" height="20" />
            </div>`).on('click', e => {
                    e.preventDefault()
                    var index = Number(e.target.id.replace('m-speed-', ''))
                    var newSpeed = Math.max(EXPRESSION_DATA[index].speed * 10 - 1, 1)
                    EXPRESSION_DATA[index].speed = newSpeed / 10
                    PM_SPEED_CONTAINER.find('.content').text(String(newSpeed / 10))
                })
                PM_SPEED_CONTAINER.append(MINUS_SPEED)
                PM_SPEED_CONTAINER.append(`<div class="content">1.5</div>`)
                const PLUS_SPEED = $(`<div id="p-speed-${i}" class="plus">
                <img src="/images/add.svg" width="20" height="20" />
            </div>`).on('click', e => {
                    e.preventDefault()
                    var index = Number(e.target.id.replace('p-speed-', ''))
                    var newSpeed = Math.min(EXPRESSION_DATA[index].speed * 10 + 1, 30)
                    EXPRESSION_DATA[index].speed = newSpeed / 10
                    PM_SPEED_CONTAINER.find('.content').text(String(newSpeed / 10))
                })
                PM_SPEED_CONTAINER.append(PLUS_SPEED)
                PM_SPEED.append(PM_SPEED_CONTAINER)

                const FORMULA_SELECT = $(`<select id="list-formula-${i}">
                    <option value="NF:1-4">   4 ()</option>
                    <option value="NF:1-5">   5 ()</option>
                    <option value="NF"> </option>
                    <option value="+1=+5-4">+1=+5-4</option>
                    <option value="-1=+4-5">-1=+4-5</option>
                    <option value="+2=+5-3">+2=+5-3</option>
                    <option value="-2=+3-5">-2=+3-5</option>
                    <option value="+3=+5-2">+3=+5-2</option>
                    <option value="-3=+2-5">-3=+2-5</option>
                    <option value="+4=+5-1">+4=+5-1</option>
                    <option value="-4=+1-5">-4=+1-5</option>
                    <option value="LF"> </option>
                    <option value="+1=-9+10">+1=-9+10</option>
                    <option value="+2=-8+10">+2=-8+10</option>
                    <option value="+3=-7+10">+3=-7+10</option>
                    <option value="+4=-6+10">+4=-6+10</option>
                    <option value="+5=-5+10">+5=-5+10</option>
                    <option value="-1=-10+9">-1=-10+9</option>
                    <option value="-2=-10+8">-2=-10+8</option>
                    <option value="-3=-10+7">-3=-10+7</option>
                    <option value="-4=-10+6">-4=-10+6</option>
                    <option value="-5=-10+5">-5=-10+5</option>
                    <option value="+9=-1+10">+9=-1+10</option>
                    <option value="-9=-10+1">-9=-10+1</option>
                    <option value="+8=-2+10">+8=-2+10</option>
                    <option value="-8=-10+2">-8=-10+2</option>
                    <option value="+7=-3+10">+7=-3+10</option>
                    <option value="-7=-10+3">-7=-10+3</option>
                    <option value="+6=-4+10">+6=-4+10</option>
                    <option value="-6=-10+4">-6=-10+4</option>
                    <option value="BF"> </option>
                    <option value="+9=+4-5+10">+9=+4-5+10</option>
                    <option value="-9=-10+5-4">-9=-10+5-4</option>
                    <option value="+8=+3-5+10">+8=+3-5+10</option>
                    <option value="-8=-10+5-3">-8=-10+3-5</option>
                    <option value="+7=+2-5+10">+7=+2-5+10</option>
                    <option value="-7=-10+5-2">-7=-10+5-2</option>
                    <option value="+6=+1-5+10">+6=+1-5+10</option>
                    <option value="-6=-10+5-1">-6=-10+5-1</option>
                    <option value="FF"></option>
                    <option value="MIX"> </option>
                </select>`).on('change', e => {
                    EXPRESSION_DATA[Number(e.target.id.replace('list-formula-', ''))].formula = $(e.target).val()
                    if ($(e.target).val().includes(':')) {
                        DIAPASON_SELECT.prop('disabled', true)
                        EXPRESSION_DATA[Number(e.target.id.replace('list-formula-', ''))].diapason = $(e.target).val().replace('NF:', '')
                    }
                    else {
                        DIAPASON_SELECT.prop('disabled', false)
                        if (EXPRESSION_DATA[Number(e.target.id.replace('list-formula-', ''))].diapason === '1-4') EXPRESSION_DATA[Number(e.target.id.replace('list-formula-', ''))].diapason = '1-9'
                    }
                })
                const D_LIST_FORMULA = $(`<div class="d-list-item">
                <label class="title">
                    :
                </label>
                </div>`).append(FORMULA_SELECT)

                const DIAPASON_SELECT = $(`<select id="list-diapason-${i}" disabled>
                    <option value="1-9"> 1  9</option>
                    <option value="1-99"> 1  99</option>
                    <option value="10-99"> 10  99</option>
                    <option value="100-999"> 100  999</option>
                    <option value="1-999"> 1  999</option>
                    <option value="10-999"> 10  999</option>
                </select>`).on('change', e => {
                    EXPRESSION_DATA[Number(e.target.id.replace('list-diapason-', ''))].diapason = $(e.target).val()
                    console.log(EXPRESSION_DATA[Number(e.target.id.replace('list-diapason-', ''))])
                })
                const D_LIST_DIAPASON = $(`<div class="d-list-item">
                <label class="title">
                    :
                </label>
                </div>`).append(DIAPASON_SELECT)

                const READY_BUTTON = $(`<div id="ready-${i}" class="save-btn">
                
            </div>`).on('click', e => {
                    e.preventDefault()
                    hide($('#answer-' + e.target.id.replace('ready-', '')))
                    hide($('#start-' + e.target.id.replace('ready-', '')))
                    hide($('#back-count-' + e.target.id.replace('ready-', '')))
                    hide($('#count-' + e.target.id.replace('ready-', '')))
                    show($('#loading-' + e.target.id.replace('ready-', '')))
                    hide(setup)
                    get_terms(e.target.id.replace('ready-', ''))
                })

                const setup = $('<div></div>').css({
                    width: 350,
                    backgroundColor: '#fff',
                    borderRadius: 10,
                    boxShadow: '0 0 5px #ccc',
                    padding: 10,
                    display: 'none',
                    flexDirection: 'column',
                    position: 'absolute',
                    top: 45,
                    left: 'calc(50% - 175px)',
                    minHeight: '80%',
                    maxHeight: '80%',
                    height: '80%',
                    overflowY: 'auto',
                    alignItems: 'center'
                }).append(PM_TERMS).append(PM_SPEED).append(D_LIST_FORMULA).append(D_LIST_DIAPASON).append(READY_BUTTON)
                const container = $(`
                <div>
                    <img id="abacus-graphic-${i}" class="abacus-graphic" src="/images/abacus-graphic.png" width="300" style="position: absolute; left: 100px;bottom: 50px;opacity: 0; pointer-events: none;" />
                    <div id="loading-${i}" class="loading">
                        <div class="loader"> ...</div>
                    </div>
                    <div id="start-${i}">
                        ${STUDENTS === 1 ? `<button id="start-btn-${i}" class="start-btn">
                            <label>CLICK</label>
                        </button>
                        <br />` : ''}
                        <small style="color: #ccc; font-size: 20px;"> Enter  !</small>
                    </div>
                    <div class="back-count" id="back-count-${i}">
                        <label style="font-size: ${Math.max(50 / STUDENTS, 35)}px">
                             
                        </label>
                    </div>
                    <div id="count-${i}" class="count">
                        <label>
                            78
                        </label>
                    </div>
                    <div id="answer-${i}" class="answer">
                        <form>
                            <input
                                oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
                                type="number" maxlength="6" />
                            <button class="ready" id="ready-${i}">
                                
                            </button>
                            <button class="again" id="again-${i}">
                                 
                            </button>
                        </form>
                    </div>
                    <div id="modal-${i}" style="width: 300px; height: 100px; background: #fff; border-radius: 10px; box-shadow: 0 0 5px #ccc; -webkit-box-shadow: 0 0 5px #ccc; display: none;">
                        <img src="/images/like.svg" width="50" height="50" style="margin: 25px 125px;"/>
                    </div>
                </div>
                `).css({
                    width: STUDENTS > 1 ? String(100 / (STUDENTS > 3 ? 3 : STUDENTS)) + '%' : '100%',
                    height: STUDENTS > 3 ? '50%' : '100%',
                    minWidth: STUDENTS > 1 ? String(100 / (STUDENTS > 3 ? 3 : STUDENTS)) + '%' : '100%',
                    minHeight: STUDENTS > 3 ? '50%' : '100%',
                    overflow: 'hidden',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    borderRight: '1px solid #ccc',
                    borderBottom: '1px solid #ccc',
                    position: 'relative'
                })
                $(container).append(toolbar).append(setup)
                $('#click-c').append(container).css({
                    flexWrap: 'wrap'
                })
                if (STUDENTS === 1) $('#start-btn-' + i).on('click', e => {
                    e.preventDefault()
                    start_expression()
                })
                if(!state.task)
                    EXPRESSION_DATA[i] = ({
                        account: null,
                        terms: 10,
                        speed: 1.5,
                        formula: 'NF:1-4',
                        diapason: '1-4'
                    })
                else
                    EXPRESSION_DATA[i] = ({
                        account: null,
                        ...state.task
                    })
            }

            console.log(EXPRESSION_DATA)

            if (state && state.role === 'teacher') {
                $('.container-side-bar').empty().append('<div style="width: 80%;"><h3> :</h3></div>').css({
                    justifyContent: 'flex-start',
                })
                $.ajax({
                    method: 'POST',
                    url: 'https://php.mindclickon.com/libs/teacher/profile.php',
                    data: { id: state.id },
                    dataType: 'json',
                    success: (resp) => {
                        console.log(resp)
                        if (resp.students.length <= 0 || !resp.students) {
                            $('.container-side-bar').append('<h4 style="color: #ccc;">    !</h4>')
                        } else {
                            ALL_STUDENTS = []
                            const list = $('<div id="students-all-list"></div>').css({
                                width: '80%',
                                marginTop: 20
                            })
                            for (var i = 0; i < resp.students.length; i++) {
                                var student = resp.students[i]
                                ALL_STUDENTS.push({
                                    id: student.data.id,
                                    name: student.name
                                })
                                const student_item = $(`<div id="data-${student.data.id}"><img src="/images/teacher/user-circle.svg" style="margin-right: 5px;" width="20" height="20" />${student.name}</div>`).css({
                                    display: 'flex',
                                    alignItems: 'center',
                                    paddingLeft: 5,
                                    borderRadius: 5,
                                    border: '1px solid #f1f1f1',
                                    boxShadow: '0 0 5px #ccc',
                                    width: 200,
                                    height: 40,
                                    // cursor: 'pointer',
                                    marginBottom: 10
                                }).attr("draggable", 'true').on('dragstart', e => {
                                    $('#side-bar').css({
                                        right: -400
                                    })
                                    e.originalEvent.dataTransfer.setData('text', JSON.stringify({
                                        name: $(e.target).text(),
                                        id: $(student_item).attr('id').replace('data-', '')
                                    }))
                                })
                                $(list).append(student_item)
                            }
                            $('.container-side-bar').append(list)
                            const PM_STUDENTS = $(`<div class="pm-item">
                        <label class="title">
                            - :
                        </label>
                        
                    </div>`)
                            const PM_STUDENTS_CONTAINER = $(`<div class="pm"></div>`)
                            const MINUS_SPEED = $(`<div id="m-students-${i}" class="minus">
                <img src="/images/minus.svg" width="20" height="20" />
            </div>`).on('click', e => {
                                e.preventDefault()
                                var newStudents = Math.max(Number(PM_STUDENTS_CONTAINER.find('.content').text()) - 1, 1)
                                PM_STUDENTS_CONTAINER.find('.content').text(String(newStudents))
                            })
                            PM_STUDENTS_CONTAINER.append(MINUS_SPEED)
                            PM_STUDENTS_CONTAINER.append(`<div id="students" class="content">${STUDENTS}</div>`)
                            const PLUS_SPEED = $(`<div id="p-students-${i}" class="plus">
                <img src="/images/add.svg" width="20" height="20" />
            </div>`).on('click', e => {
                                e.preventDefault()
                                var newStudents = Math.min(Number(PM_STUDENTS_CONTAINER.find('.content').text()) + 1, 30)
                                PM_STUDENTS_CONTAINER.find('.content').text(String(newStudents))
                            })
                            PM_STUDENTS_CONTAINER.append(PLUS_SPEED)
                            PM_STUDENTS.append(PM_STUDENTS_CONTAINER)

                            const READY_BUTTON = $(`<div id="ready-${i}" class="save-btn">
                                    
                                </div>`).on('click', e => {
                                e.preventDefault()
                                init()
                            })

                            $('.container-side-bar').append(PM_STUDENTS).append(READY_BUTTON)
                        }
                    },
                    error: (err) => {
                        console.log(err)
                    }
                })
            }

            for (var i = 0; i < STUDENTS; i++) {
                hide($('#answer-' + i))
                hide($('#start-' + i))
                hide($('#back-count-' + i))
                hide($('#count-' + i))
                show($('#loading-' + i))
            }
            isModal = false;
            setTimeout(get_terms, 1000);
        }

        $(window).ready(init)

        const hide = (obj) => {
            obj.css({
                display: 'none',
            })
        }

        const show = (obj) => {
            obj.css({
                display: 'block',
            })
        }

        $('#left-button').on('click', e => {
            e.preventDefault()
            if (!isLeftBarOpened) {
                $('#left-bar').css({
                    left: 0,
                })
                setTimeout(() => {
                    updateLeftBar(true)
                }, 100)
                isLeftBarOpened = true
            } else {
                $('#left-bar').css({
                    left: -300,
                })
                setTimeout(() => {
                    updateLeftBar(false)
                }, 100)
                isLeftBarOpened = false
            }
        })

        $('#close-modal-btn').on('click', e => {
            e.preventDefault()
            hide($('#answer-modal'))
            close_modal()
        })

        const add_coins = (count, cb) => {
            $.ajax({
                method: 'post',
                url: 'https://php.mindclickon.com/app/add-coins.php',
                data: {
                    id: state.id,
                    coins: count
                }
            })
            .done((resp) => {
                cb(JSON.parse(resp))
            })
        }

        const add_points = (count, cb) => {
            $.ajax({
                method: 'post',
                url: 'https://php.mindclickon.com/app/add-points.php',
                data: {
                    id: state.id,
                    points: count
                }
            })
            .done((resp) => {
                cb(JSON.parse(resp))
            })
        }

        const update_statistic = () => {
            $('#left-bar .content').empty()
            $('#completed').text(String(expressions.length))
            expressions.forEach(expression => {
                $('#left-bar .content').append(`
                    <div><span>${expression.expression} = ${expression.your}</span><span>${expression.isRight ? `<label style="color: green;"></label>` :
                                `<label style="color: tomato;"></label>`}<label>${expression.right}</label></span></div>
                `)
            })
        }

        const Start_Interval = (i) => {
            var y = 0
            var t = TERMS
            console.log(TERMS)
            console.log(RIGHT)
            hide($('#back-count-' + i))
            show($('#count-' + i))
            $(`#count-${i} label`).text(t[i][y])
            play_sound(COUNT_SOUND)
            var speed = EXPRESSION_DATA[i].speed
            var count = setInterval(() => {
                y++;
                play_sound(COUNT_SOUND)
                console.log(y, t[i])
                if (y + 1 > t[i].length) {
                    if (y + 1 - t[i].length === 1) {
                        hide($('#count-' + i))
                        show($('#answer-' + i))
                        $(`#answer-${i} input`).focus()
                        clearInterval(count)
                        console.log('Cleared!')
                        document.getElementById('ready-' + i).onclick = function(e) {
                            e.preventDefault()
                            const id = Number(e.target.id.replace('ready-', ''))
                            console.log(e.target.id)
                            if (!$(`#answer-${id} input`).val()) return;
                            console.log('From there 1!')
                            return answer(id)
                        }
                        hide($('#count-' + i))
                        show($('#answer-' + i))
                        $('#abacus-graphic-' + i).css({
                            opacity: 0
                        })
                        $(`#answer-${i} input`).focus()
                        $('#settings-btn').css({ opacity: 1, color: "#fff" })
                        if (isOpen) $('#side-bar').css({
                            right: 0,
                        })
                        isCounting = false
                    }
                }
                $(`#count-${i} label`).text(t[i][y])
                if (t[i][y] === t[i][y - 1]) {
                    $(`#count-${i} label`).css({
                        marginLeft: -30
                    })
                }
                else
                    $(`#count-${i} label`).css({
                        marginLeft: 0
                    })
            }, speed * 1000)
        }

        /**
         * Calls when back count time is ended!
         */
        function start_count(indexes) {
            const STUDENTS = Math.min(Number($('#students').text().trim()), 6);
            if(!STUDENTS) STUDENTS = 1
            console.log(STUDENTS)
            var t = TERMS;
            RIGHT = [];
            if (!indexes) {
                for (var i = 0; i < STUDENTS; i++) {
                    RIGHT[i] = 0;
                    t[i].forEach((number) => {
                        RIGHT[i] += Number(number);
                    });
                }
                console.log('STUDENTS, ', STUDENTS);
                for (var i = 0; i < STUDENTS; i++) {
                    Start_Interval(i);
                }
            } else {
                var y = 0;
                var COMPLETED = 0;
                for (var i of indexes) {
                    RIGHT[i] = 0;
                    t[i].forEach((number) => {
                        RIGHT[i] += Number(number);
                    });
                    hide($('#back-count-' + i));
                    show($('#count-' + i));
                    $(`#count-${i} label`).text(t[i][y]);
                    var speed = EXPRESSION_DATA[i].speed;
                    var count = setInterval(() => {
                        play_sound(COUNT_SOUND);
                        y++;
                        if (y + 1 > t[i].length) {
                            if (y + 1 - t[i].length === 1) {
                                $('#ready-' + i).click(e => {
                                    e.preventDefault();
                                    const id = Number(e.target.id.replace('ready-', ''));
                                    console.log($(`#answer-${id} input`).val());
                                    if (!$(`#answer-${id} input`).val())
                                        return;
                                    console.log('From there 2!');
                                    answer(id);
                                });
                                hide($('#count-' + i));
                                show($('#answer-' + i));
                                $(`#answer-${i} input`).focus();
                                COMPLETED++;
                            }
                            if (COMPLETED >= STUDENTS) {
                                console.log('ENND!');
                                clearInterval(count);
                                $('#ready-' + i).click(e => {
                                    e.preventDefault();
                                    const id = Number(e.target.id.replace('ready-', ''));
                                    answer(id);
                                });
                                hide($('#count-' + i));
                                show($('#answer-' + i));
                                $('#abacus-graphic-' + i).css({
                                    opacity: 0
                                });
                                $(`#answer-${i} input`).focus();
                                $('#settings-btn').css({ opacity: 1, color: "#fff" });
                                if (isOpen)
                                    $('#side-bar').css({
                                        right: 0,
                                    });
                                isCounting = false;
                            }
                        }
                        $(`#count-${i} label`).text(t[i][y]);
                        if (t[i][y] === t[i][y - 1]) {
                            $(`#count-${i} label`).css({
                                marginLeft: -30
                            });
                        }

                        else
                            $(`#count-${i} label`).css({
                                marginLeft: 0
                            });
                        console.log(y, t[i].length);
                    }, speed * 1000);
                }
                play_sound(COUNT_SOUND);
            }
        }

        $('#start-btn').on('click ', e => {
            e.preventDefault();
            start_expression()
        })

        const start_expression = (indexes) => {
            const STUDENTS = Math.min(Number($('#students').text().trim()), 6)
            isCounting = true
            $('#side-bar').css({
                right: -500,
            })
            $('#settings-btn').css({ opacity: .5, color: '#ccc' })
            isStart = false
            if (!indexes) {
                for (var i = 0; i < STUDENTS; i++) {
                    hide($('#start-' + i))
                    show($('#back-count-' + i))
                    $('#back-count-' + i + ' label').text(' ')
                }
                play_sound(BACK_COUNT_SOUND)
                var y = 0
                var back_time = setInterval(() => {
                    for (var i = 0; i < STUDENTS; i++) {
                        $('#back-count-' + i + ' label').text(BACK_COUNT_SOURCES[y])
                    }
                    if (y > 2) {
                        clearInterval(back_time)
                        start_count();
                    }
                    y++
                }, 1000)
            } else {
                for (var i of indexes) {
                    hide($('#start-' + i))
                    show($('#back-count-' + i))
                    $('#back-count-' + i + ' label').text(' ')
                }
                play_sound(BACK_COUNT_SOUND)
                var y = 0
                var back_time = setInterval(() => {
                    for (var i of indexes) {
                        $('#back-count-' + i + ' label').text(BACK_COUNT_SOURCES[y])
                    }
                    if (y > 2) {
                        clearInterval(back_time)
                        start_count(indexes);
                    }
                    y++
                }, 1000)
            }
        }

        /*** ->ALL LOGIC<- */

        $('#close-btn').on('click', e => {
            e.preventDefault()
            $('#side-bar').css({
                right: -500,
            })
            isOpen = false
        })

        $('#save-btn').on('click', e => {
            e.preventDefault()
            init()
        })

        $('#settings-btn').on('click', (e) => {
            e.preventDefault();
            if (isCounting === true || state.role !== 'teacher') return
            $('#side-bar').css({
                right: 0,
            })
            isOpen = true
        })

        $('#click-c').on('click', (e) => {
            e.preventDefault();
            if (isOpen)
                $('#side-bar').css({
                    right: -500,
                });
            isOpen = false;
        })

        /* Students */
        $('#m-students').on('click', e => {
            if (students > 1) {
                students--;
                $('#students').text(String(students))
            }
        })

        $('#p-students').on('click', e => {
            if (students < 10) {
                students++;
                $('#students').text(String(students))
            }
        })
        /* Students */

        $('#signal-e').on('click', e => {
            e.preventDefault()
            if (signals) {
                signals = false;
                updateSignals(false)
            } else {
                updateSignals(true)
                signals = true;
            }
        })

        $('#abacus-e').on('click', e => {
            e.preventDefault()
            if (isAbacus) {
                document.querySelector('#abacus-e img').src = "/images/abacus-off.png"
                $('#abacus-graphic').css({
                    opacity: 0
                })
                isAbacus = false;
            } else {
                document.querySelector('#abacus-e img').src = "/images/abacus.svg"
                $('#abacus-graphic').css({
                    opacity: 1
                })
                isAbacus = true;
            }
        })
    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%'
        }}>
            {/* 100 RIGHT MODAL */}
            <div id="point-add-modal" style={{
                width: '100%',
                height: '100%',
                display: 'none',
                alignItems: 'center',
                justifyContent: 'center',
                position: 'absolute',
                top: 0,
                left: 0,
                background: 'rgba(40, 40, 40, .2)',
                zIndex: 200
            }}>
                <div id="point-add-modal-content" style={{
                    width: '350px',
                    height: '325px',
                    background: '#fff',
                    borderRadius: 10,
                    boxShadow: '0 0 10px rgba(40, 40, 40, .4)',
                    display: 'flex',
                    flexDirection: 'column',
                    padding: 10,
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    textAlign: 'center'
                }}>
                    <h3> <label style={{
                        fontWeight: 600
                    }}>{state.task?.count ? state.task?.count : -1}</label>  <label style={{
                        color: 'green'
                    }}></label>!</h3>
                    <h4 style={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>: +{state.task?.reward ? state.task?.reward : -1}   5 .<img style={{
                        marginLeft: 5
                    }} src={'/images/award.png'} width={20} height={20} /></h4>
                    <img width={128} height={128} src={'/images/anniversary.png'} style={{
                        filter: 'drop-shadow(0 0 5px rgba(40, 40, 40, .3))'
                    }} />
                    <div style={{
                        width: 150,
                        height: 40,
                        background: 'linear-gradient(to right, dodgerblue, blue)',
                        borderRadius: 10,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        color: '#fefefe',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        cursor: 'pointer',
                        userSelect: 'none'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#point-add-modal').css({
                            display: 'none'
                        })
                        History.replace('/student', state)
                    }}>
                        
                    </div>
                </div>
            </div>

            <header className="header-mental">
                <div className="back-item" onClick={e => {
                    e.preventDefault()
                    History.goBack()
                }}>
                    <img src="/images/left-arrow.svg" width="30" height="30" />
                </div>
                <div className="status-text">
                    <label id="completed">0</label>
                    <label>completed</label>
                </div>
                <div className="action-icons" style={{
                    width: state.task ? 400 : 200,
                }}>
                    <div style={{
                        height: '100%',
                        display: state.task ? 'flex' : 'none',
                        justifyContent: 'center',
                        alignItems: 'center',
                        flexDirection: 'column'
                    }}>
                        <label id="task-status" style={{
                            fontSize: 14,
                            color: '#fff'
                        }}>{expressionsLeft.current}</label>
                        <label style={{
                            fontSize: 11,
                            color: '#fff',
                            whiteSpace: 'nowrap'
                        }}> </label>
                    </div>
                    <div id="abacus-e" className="abacus-item">
                        <img src="/images/abacus-off.png" width="30" height="30" />
                    </div>
                    <div id="signal-e" className="signal-item">
                        <FontAwesomeIcon icon={Signals ? faVolumeUp : faVolumeMute} />
                    </div>
                    <div id="settings-btn" className="settings-item">
                        <FontAwesomeIcon icon={faUser} />
                    </div>
                </div>
            </header>
            <div id="click-c" className="container-mental">
                <img id="abacus-graphic" className="abacus-graphic" src="/images/abacus-graphic.png" width="300" style={{
                    position: 'absolute',
                    left: 100,
                    bottom: 50,
                    opacity: 0
                }} />
                <div id="loading" className="loading">
                    <div className="loader"> ...</div>
                </div>
                <div id="start">
                    <button id="start-btn" className="start-btn">
                        <label>CLICK</label>
                    </button>
                    <br />
                    <small style={{
                        color: '#ccc',
                        fontSize: 20
                    }}> Enter  !</small>
                </div>
                <div className="back-count" id="back-count">
                    <label>
                         
                    </label>
                </div>
                <div id="count" className="count">
                    <label>
                        78
                    </label>
                </div>
                <div id="answer" className="answer">
                    <form>
                        <input
                            oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);"
                            type="number" maxlength="6" />
                        <button id="ready">
                            
                        </button>
                        <button id="again">
                             
                        </button>
                    </form>
                </div>
            </div>

            <div id="answer-modal" className="modal">

                {/* <!-- Modal content --> */}
                <div className="modal-content">
                    <span id="close-modal-btn" className="close">&times;</span>
                    <div className="content" id="answer-content">
                        <div>
                            <h1></h1>
                            <h2> :</h2>
                            <h2 style={{
                                fontWeight: 400
                            }} id="your-answer"></h2>
                            <h2> :</h2>
                            <h2 style={{
                                fontWeight: 400
                            }} id="right-answer"></h2>
                            <hr />
                        </div>
                        <div id="terms-list">
                            <ul>

                            </ul>
                        </div>
                    </div>
                </div>

            </div>

            <div id="left-bar" className="left-side-mental">
                <div className="content">
                    <h3 style={{
                        color: '#ccc',
                        textAlign: 'center'
                    }}>
                           ,   !
                    </h3>
                </div>
                <div id="left-button" className="button">
                    <FontAwesomeIcon icon={LeftBar ? faAngleLeft : faAngleRight} />
                </div>
            </div>

            <div id="side-bar" className="side-bar-mental" >
                <div className="side-bar-header">
                    <label></label>
                    <div id="close-btn" className="close-btn">
                        &times;
                    </div>
                </div>
                <div className="container-side-bar">
                    <div className="pm-item">
                        <label className="title">
                            - :
                        </label>
                        <div className="pm">
                            <div id="m-terms" className="minus">
                                <img src="/images/minus.svg" width="20" height="20" />
                            </div>
                            <div className="content">
                                <input type="number" maxlength="2" max="100" min="1" id="terms" value="10" />
                            </div>
                            <div id="p-terms" className="plus">
                                <img src="/images/add.svg" width="20" height="20" />
                            </div>
                        </div>
                    </div>
                    <div className="pm-item">
                        <label className="title">
                            :
                        </label>
                        <div className="pm">
                            <div id="m-speed" className="minus">
                                <img src="/images/minus.svg" width="20" height="20" />
                            </div>
                            <div className="content">
                                <label id="speed">
                                    1.5
                                </label>
                            </div>
                            <div id="p-speed" className="plus">
                                <img src="/images/add.svg" width="20" height="20" />
                            </div>
                        </div>
                    </div>
                    <div className="d-list-item">
                        <label className="title">
                            :
                        </label>
                        <select id="list-formula">
                            <option value="NF:1-4">   4 ()</option>
                            <option value="NF:1-5">   5 ()</option>
                            <option value="NF"> </option>
                            <option value="+1=+5-4">+1=+5-4</option>
                            <option value="-1=+4-5">-1=+4-5</option>
                            <option value="+2=+5-3">+2=+5-3</option>
                            <option value="-2=+3-5">-2=+3-5</option>
                            <option value="+3=+5-2">+3=+5-2</option>
                            <option value="-3=+2-5">-3=+2-5</option>
                            <option value="+4=+5-1">+4=+5-1</option>
                            <option value="-4=+1-5">-4=+1-5</option>
                            <option value="LF"> </option>
                            <option value="+1=-9+10">+1=-9+10 ()</option>
                            <option value="-1=-10+9">-1=-10+9 ()</option>
                            <option value="BF"> </option>
                            <option value="FF"></option>
                        </select>
                    </div>
                    <div className="d-list-item">
                        <label className="title">
                             :
                        </label>
                        <select id="list-diapason" disabled>
                            <option value="1-9"> 1  9</option>
                            <option value="1-99"> 1  99</option>
                            <option value="10-99"> 10  99</option>
                            <option value="100-999"> 100  999</option>
                            <option value="1-999"> 1  999</option>
                            <option value="10-999"> 10  999</option>
                        </select>
                    </div>
                    <div className="pm-item">
                        <label className="title">
                            - :
                        </label>
                        <div className="pm">
                            <div id="m-students" className="minus">
                                <img src="/images/minus.svg" width="20" height="20" />
                            </div>
                            <div className="content">
                                <label id="students">
                                    1
                                </label>
                            </div>
                            <div id="p-students" className="plus">
                                <img src="/images/add.svg" width="20" height="20" />
                            </div>
                        </div>
                    </div>
                    <div id="save-btn" className="save-btn">
                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MentalCount;