import React from 'react';
import ColorfulButton from './Button';
import { InfoRounded } from '@mui/icons-material';
import { TextField } from '@mui/material';

const StartLessonModal = ({ modalShown = true, disableModal = () => {}, startEvent = () => {} }) => {
    return (
        <div style={{
            zIndex: 100,
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(40, 40, 40, .1)',
            display: modalShown ? 'flex' : 'none',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
            <div style={{
                width: 400,
                height: 350,
                borderRadius: 10,
                backgroundColor: 'white',
                boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                padding: 20
            }}>
                <h4> :</h4>
                <h5 style={{
                    whiteSpace: 'break-spaces',
                    marginTop: 10,
                    marginBottom: 20
                }}>      "" -  ,           ,          . 
                    </h5>
                <TextField
                    placeholder={" ..."}
                    size={'small'}
                    helperText={""}
                />
                <h6 style={{
                    color: '#ccc',
                    marginTop: 20,
                    marginBottom: 20,
                    whiteSpace: 'break-spaces'
                }}>
                <InfoRounded fontSize={'12'} color={"info"}/>        ,    ,      .     ?
                </h6>
                <ColorfulButton
                    text={' '}
                    type={'success'}
                    onClick={() => {
                        disableModal()
                        startEvent()
                    }}
                    clearMargin={true}
                />
            </div>
        </div>
    )
};

export default StartLessonModal;