    useEffect(() => {
        if (!state) History.goBack()
        console.log(state)
    }, [])

    useEffect(() => {
        var currentWelcomePage = 1
        var globalOffset = $('#welcome-list').offset().left
        const welcome_right = () => {
            if (!$('#welcome-page-' + (currentWelcomePage + 1)).offset()) return
            currentWelcomePage++
            if (currentWelcomePage === 3) $('#welcome-button').text('')
            var offset = $('#welcome-page-' + currentWelcomePage).offset().left
            if (!offset) return
            // if (currentWelcomePage === 4) offset += offset
            $('#welcome-list').stop().animate({
                scrollLeft: offset
            }, 500, 'swing')
            $('#welcome-num-' + currentWelcomePage).css({
                backgroundColor: '#bbb'
            })
            $('#welcome-num-' + (currentWelcomePage - 1)).css({
                backgroundColor: '#ccc'
            })
        }

        const welcome_left = () => {
            if (!$('#welcome-page-' + (currentWelcomePage - 1)).offset()) return
            currentWelcomePage--
            if (currentWelcomePage !== 3) $('#welcome-button').text('')
            var offset = Math.abs($('#welcome-page-' + currentWelcomePage).offset().left - globalOffset)
            // if (currentWelcomePage === 1) offset = 0
            console.log(offset, currentWelcomePage)
            $('#welcome-list').stop().animate({
                scrollLeft: offset
            }, 500, 'swing')
            $('#welcome-num-' + currentWelcomePage).css({
                backgroundColor: '#bbb'
            })
            $('#welcome-num-' + (currentWelcomePage + 1)).css({
                backgroundColor: '#ccc'
            })
        }

        $('#welcome-right').on('click', welcome_right);
        $('#welcome-left').on('click', welcome_left);
        $('#welcome-button').on('click', e => {
            e.preventDefault()
            if ($(e.target).text().trim() === '') welcome_right()
            else $('#welcome-modal').css({
                display: 'none'
            })
        })

        if (!cookies.update_id || cookies.update_id !== String(UPDATE_ID)) {
            console.log(cookies.update_id)
            var d = new Date()
            d.setTime(d.getTime() + (12 * 30 * 24 * 60 * 60 * 1000));
            setCookie("update_id", UPDATE_ID, { expires: d, path: '/' })
            $('#welcome-modal').css({
                display: 'flex'
            })
        } else {
            $('#welcome-modal').css({
                display: 'none'
            })
        }

        if (!state) History.goBack()

    }, [])

