import { Checkbox } from '@mui/material';
import React, { useState } from 'react';
import ColorfulButton from './Button';

const StudentModal = ({ students = [], onLesson = [], modalShown, disableModal = () => {}, addStudents = (addingStudents=[]) => {} }) => {
    const update = () => {
        var temp = []
        for(var i = 0; i < students.length; i++)
        {
            var el = document.getElementById("checkbox-" + i)
            if(el.checked)
            {
                temp.push(students[i]);
            }
        }
        return temp;
    }

    const checkOnLesson = (student) => {
        for(var i = 0; i < onLesson.length; i++) {
            var val = onLesson[i];
            if(student.id === val.id) return true;
        }
        return false;
    }

    return (
        <div style={{
            zIndex: 100,
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(40, 40, 40, .1)',
            display: modalShown ? 'flex' : 'none',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
            <div style={{
                width: 400,
                height: 350,
                borderRadius: 10,
                backgroundColor: 'white',
                boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                padding: 20
            }}>
                <h4> :</h4>
                <ul>
                    {
                        students.map((val, i) => {
                            if(checkOnLesson(val))
                                return (
                                    <li key={i} style={{
                                        display: 'flex',
                                        flexDirection: 'row'
                                    }}>
                                        <Checkbox id={'checkbox-' + i} disabled />
                                        <label>{val.name}</label>
                                    </li>
                                )
                            else
                                return (
                                    <li key={i} style={{
                                        display: 'flex',
                                        flexDirection: 'row'
                                    }}>
                                        <Checkbox id={'checkbox-' + i} />
                                        <label>{val.name}</label>
                                    </li>
                                )
                        })
                    }
                </ul>
                <ColorfulButton
                    text={' ()'}
                    type={'neutral'}
                    onClick={() => {
                        addStudents(update())
                        disableModal()
                    }}
                    clearMargin={true}
                />
            </div>
        </div>
    )
};

export default StudentModal