import React from 'react';
import Switch from '@mui/material/Switch'
import ColorfulButton from './Button';
import { IconButton, Tooltip } from '@mui/material'
import { InfoRounded } from '@mui/icons-material'

const Information = ({ setSelected = () => {}, coins = 0, addCoins = (addingCoins=0) => {}, lastCoins = 0, student={name: ''}, onLesson, setOnLesson }) => {
    return (
        <div style={{
            width: 730,
            height: 440,
            padding: 20,
            display: 'flex',
            flexDirection: 'column',
            overflowY: 'auto',
            borderRadius: 20,
            boxShadow: '0 0 5px rgba(40, 40, 40, .4)'
        }}>
            {
                student === null ? 
                <div style={{
                    flex: 1,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}>
                    <h3 style={{
                        color: '#ccc'
                    }}> ,     .</h3>
                </div>
                :
                <div style={{
                    width: '100%',
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    flex: 1
                }}>
                    <h3>  {student.name}:</h3>
                    {/* Container */}
                    <div style={{
                        width: '100%',
                        display: 'flex',
                        flex: 1,
                        flexDirection: 'row',
                        marginTop: 30,
                        justifyContent: 'space-between'
                    }}>
                        {/* Left side */}
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'space-between',
                            flex: 1,
                        }}>
                            {/* Checkboxes and more... */}
                            <div>
                                <label style={{
                                    fontSize: 15
                                }}>:</label><Switch defaultChecked />
                            </div>
                            {/* Wallet */}
                            <div>
                                <label> : {coins} </label>
                                <Tooltip title="    .      ,    !" >
                                    <IconButton color={'info'}>
                                        <InfoRounded />
                                    </IconButton>
                                </Tooltip>
                                <br/>
                                <img src={'/images/teacher/wallet-texted.svg'}
                                    onDragOver={e => {
                                        e.preventDefault()
                                        e.dataTransfer.dropEffect = 'copy'
                                    }}
                                    onDrop={e => {
                                        e.preventDefault()
                                        const data = e.dataTransfer.getData('text/plain')
                                        console.log(data)
                                        addCoins(Number(data))
                                    }}
                                onClick={e => {
                                    e.preventDefault()
                                    var count = prompt('  : ', '500')
                                    while(!count) count = prompt('  : ', '500')
                                    if(!Number(count)) return;
                                    else if(Number(count) > coins) return alert('      ( < ' + coins + ' )!')
                                    else if(Number(count) < 0) return alert('     !')
                                    addCoins(-(Number(count)))
                                }} />
                            </div>
                        </div>
                        {/* Right side */}
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            justifyContent: 'space-between',
                            flex: 1,
                        }}>
                            {/* Journal */}
                            <div>
                                <h5 style={{
                                    color: '#ccc',
                                    marginBottom: 10
                                }}>:</h5>
                                {/* TODO: List from magazine. */}
                                <div style={{
                                    marginLeft: 10,
                                }}>
                                    <ul style={{
                                        width: '100%',
                                        display: 'flex',
                                        flexDirection: 'column',
                                    }}>
                                        <li style={{
                                            display: 'flex',
                                            justifyContent: 'space-between',
                                            marginBottom: 10
                                        }}>
                                            <h6 style={{
                                                color: '#ccc',
                                            }}>
                                                
                                            </h6>
                                            <span style={{
                                                display: 'flex',
                                                flexDirection: 'row',
                                                width: '60%',
                                                justifyContent: 'space-evenly'
                                            }}>
                                                <h6 style={{
                                                    color: '#ccc',
                                                }}>
                                                    
                                                </h6>
                                                <h6 style={{
                                                    color: '#ccc',
                                                }}>
                                                    
                                                </h6>
                                            </span>
                                        </li>
                                        <li style={{
                                            display: 'flex',
                                            justifyContent: 'space-between',
                                            marginBottom: 10
                                        }}>
                                            <label style={{
                                                fontSize: 12,
                                                whiteSpace: 'nowrap'
                                            }}> 1 :</label>
                                            <span style={{
                                                display: 'flex',
                                                flexDirection: 'row',
                                                width: '70%',
                                                justifyContent: 'space-evenly'
                                            }}>
                                                <input style={{
                                                    width: 50,
                                                    height: 20,
                                                    border: '1px solid #ccc',
                                                    borderRadius: 5,
                                                    outline: '0',
                                                    fontSize: 11,
                                                    paddingLeft: 5,
                                                    boxShadow: '0 0 2px rgba(40, 40, 40, .4)'
                                                }} />
                                                <h6 style={{
                                                    color: '#000',
                                                }}>
                                                    (57) 
                                                </h6>
                                            </span>
                                        </li>
                                        <li style={{
                                            display: 'flex',
                                            justifyContent: 'space-between',
                                            marginBottom: 10
                                        }}>
                                            <label style={{
                                                fontSize: 12,
                                                whiteSpace: 'nowrap'
                                            }}>  5 :</label>
                                            <span style={{
                                                display: 'flex',
                                                flexDirection: 'row',
                                                width: '70%',
                                                justifyContent: 'space-evenly'
                                            }}>
                                                <input style={{
                                                    width: 50,
                                                    height: 20,
                                                    border: '1px solid #ccc',
                                                    borderRadius: 5,
                                                    outline: '0',
                                                    fontSize: 11,
                                                    paddingLeft: 5,
                                                    boxShadow: '0 0 2px rgba(40, 40, 40, .4)'
                                                }} />
                                                <h6 style={{
                                                    color: '#000',
                                                }}>
                                                    (120) 
                                                </h6>
                                            </span>
                                        </li>
                                        <li style={{
                                            display: 'flex',
                                            justifyContent: 'space-between'
                                        }}>
                                            <label style={{
                                                fontSize: 12,
                                                whiteSpace: 'nowrap'
                                            }}> 100 :</label>
                                            <span style={{
                                                display: 'flex',
                                                flexDirection: 'row',
                                                width: '70%',
                                                justifyContent: 'space-evenly'
                                            }}>
                                                <input style={{
                                                    width: 50,
                                                    height: 20,
                                                    border: '1px solid #ccc',
                                                    borderRadius: 5,
                                                    outline: '0',
                                                    fontSize: 11,
                                                    paddingLeft: 5,
                                                    boxShadow: '0 0 2px rgba(40, 40, 40, .4)'
                                                }} />
                                                <h6 style={{
                                                    color: '#000',
                                                }}>
                                                    (45) 
                                                </h6>
                                            </span>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            {/* Actions */}
                            <div>
                                <h5 style={{
                                    color: '#ccc',
                                    marginBottom: 10
                                }}>:</h5>
                                {/* List of buttons. */}
                                <div style={{
                                    marginLeft: 10,
                                    width: '100%',
                                    display: 'flex',
                                    flexDirection: 'row',
                                    justifyContent: 'space-between'
                                }}>
                                    <ColorfulButton
                                        text={'  '}
                                        type={'error'}
                                        onClick={() => {
                                            var temp = onLesson;
                                            setOnLesson(temp.filter(val => {
                                                return val.id !== student.id;
                                            }))
                                            setSelected(null)
                                        }}
                                    />
                                    <ColorfulButton
                                        text={'  ' + lastCoins}
                                        type={'warning'}
                                        onClick={() => {
                                            addCoins(lastCoins)
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
        </div>
    )
};

export default Information;