import React, { useEffect, useRef, useState } from "react";
import { useHistory } from "react-router";

import '../../assets/styles/student.css'

import $ from "jquery";
import moment from 'moment'
// import 'moment/locale/ru'

import useCalls from "../../hooks/useCalls";

const Student = ({ location: { state } }) => {
    const [messages, updateMessages] = useState([])
    const [profileURI, updateURI] = useState('/images/default-profile.jpg')
    const [points, updatePoints] = useState('0')
    const [coins, updateCoins] = useState('0')
    const [balance, updateBalance] = useState(0)

    const taskSettings = useRef(null)

    const History = useHistory()

    const pushRef = useRef()
    const topRef = useRef(-400)

    const showPush = (content) => {
        topRef.current = 20
        $('#push-notification label').text(content)
        setTimeout(() => {
            topRef.current = -400; $('#push-notification').css({
                top: -400
            })
        }, 1000 * 60)
    }

    var { acceptCall } = useCalls(state.name, History, showPush, state.class.room_id)


    useEffect(async () => {
        if (!state) History.goBack()
        else {
            var appData = state.appData
            if (appData) {
                appData = JSON.parse(appData)
                if (appData.uri) updateURI(appData.uri)
            }
            console.log(state)
            updatePoints(state.points)
            updateCoins(state.coins)
            fetch('https://app.mindclickon.com/task', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    id: state.id
                })
            })
            .then(res => res.json())
            .then(json => {
                if(json.status === true)
                {
                    console.log(json)
                    if(json.tasks?.length > 0)
                    {
                        taskSettings.current = json.tasks[json.tasks.length - 1];
                        $('#task-type').text(`: ${json.tasks[json.tasks.length - 1].type === 'mental' ? ' ' : ''}`)
                        $('#new-task-modal').css({
                            display: 'flex'
                        })
                    }
                }
            })
            .catch(err => console.log(err))
        }
    }, [])

    useEffect(() => {
        $('#clip-hover').hover((e) => {
            e.preventDefault()
            $('#clip-items').css('opacity', 1)
        }, (e) => {
            e.preventDefault()
            $('#clip-items').css('opacity', 0)
        })

        $('#clip-items').hover((e) => {
            e.preventDefault()
            $('#clip-items').css('opacity', 1)
        }, (e) => {
            e.preventDefault()
            $('#clip-items').css('opacity', 0)
        })

        const hide = (el) => {
            $(el).hide()
        }

        const show = (el) => {
            $(el).show()
            var children = $(el).children()
            while (children.length > 0) {
                $(children).each((i, ele) => $(ele).show())
                children = $(children).children()
            }
        }

        const switch_page = (identifier) => {
            $('#container div').each((index, el) => {
                hide(el)
            })
            show(('#' + identifier))
            $('#sidebar #buttons .item').each((index, el) => {
                $(el).css({
                    backgroundColor: '#4D4662',
                })
                $('#' + el.id + ' img').css({
                    filter: 'invert(47%) sepia(13%) saturate(1001%) hue-rotate(213deg) brightness(93%) contrast(86%)'
                })
            })
            $('#' + identifier + '-btn').css({
                backgroundColor: '#645A81'
            })
            $('#' + identifier + '-btn img').css({
                filter: 'brightness(0) invert(1)'
            })
            $('#parts>div').each((index, el) => {
                hide(el)
            })
            show($('#' + identifier + '-part'))
        }

        $(window).ready(() => {
            hide('#notification-list')
            switch_page('quests')
        })

        $('#trainers-btn').on('click', e => {
            e.preventDefault()
            switch_page('trainers')
        })

        $('#quests-btn').on('click', e => {
            e.preventDefault()
            switch_page('quests')
        })

        $('#crm-btn').on('click', e => {
            e.preventDefault()
            switch_page('crm')
        })

        $('#tasks-btn').on('click', e => {
            e.preventDefault()
            switch_page('tasks')
        })

        $('#rating-btn').on('click', e => {
            e.preventDefault()
            switch_page('rating')
        })

        $('#calls-btn').on('click', e => {
            e.preventDefault()
            switch_page('calls')
        })

        $('#chat-btn').on('click', e => {
            e.preventDefault()
            switch_page('chat')
        })

        $('#notification-list').hover(e => {
            e.preventDefault()
            show($('#notification-list'))
        }, (e) => {
            e.preventDefault()
            hide($('#notification-list'))
        })

        $('#not-btn').hover(e => {
            e.preventDefault()
            show($('#notification-list'))
        }, (e) => {
            e.preventDefault()
            hide($('#notification-list'))
        })

    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%'
        }}>
            <div id="new-task-modal" style={{
                width: '100%',
                height: '100%',
                position: 'absolute',
                top: 0,
                left: 0,
                background: 'rgba(100, 100, 100, .3)',
                display: 'none',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 100
            }}>
                <div style={{
                    width: '40%',
                    height: '60%',
                    borderRadius: 10,
                    background: '#fefefe',
                    boxShadow: '0 0 5px rgba(100, 100, 100, .4)',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'space-evenly'
                }}>
                    <h3>   !</h3>
                    <h4 id="task-type">:  </h4>
                    <img src="/images/mosquito.png" width={250} />
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                    }}>
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            borderRadius: 5,
                            background: 'linear-gradient(to top, dodgerblue, lightblue)',
                            width: 150,
                            height: 40,
                            marginRight: 10,
                            cursor: 'pointer',
                            userSelect: 'none',
                            color: '#fefefe',
                            boxShadow: '0 0 5px rgba(100, 100, 100, .3)'
                        }} onClick={e => {
                            e.preventDefault()
                            if(!taskSettings.current) return;
                            if(!taskSettings.current.type) return;
                            if(taskSettings.current.type === 'mental')
                                History.push('/games/mental-count', {
                                    ...state,
                                    task: taskSettings.current
                                })
                            else if(taskSettings.current.type === 'multiply')
                                History.push('/games/multiply', {
                                    ...state,
                                    task: taskSettings.current
                                })
                        }} ></div>
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            borderRadius: 5,
                            background: 'linear-gradient(to top, red, crimson)',
                            width: 150,
                            height: 40,
                            color: '#fefefe',
                            cursor: 'pointer',
                            userSelect: 'none',
                            boxShadow: '0 0 5px rgba(100, 100, 100, .3)'
                        }} onClick={e => {
                            e.preventDefault()
                            $('#new-task-modal').css({
                                display: 'none'
                            })
                        }}></div>
                    </div>
                </div>
            </div>

            <div ref={pushRef} id="push-notification" style={{
                width: 400,
                height: 75,
                boxShadow: '0 0 5px #ccc',
                zIndex: 10,
                position: 'absolute',
                top: topRef.current,
                left: 'calc(50% - 200px)',
                backgroundColor: '#fff',
                borderRadius: 5,
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                transitionDuration: '.3s',
            }}>
                <img src="/images/student/bell.svg" width="25" height="25" style={{
                    width: '20%',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                }} />
                <label style={{
                    fontSize: 16,
                    fontWeight: 500,
                    fontFamily: 'Montserrat, sans-serif',
                    width: '50%',
                    textAlign: 'left',
                    textOverflow: 'ellipsis',
                    overflow: 'hidden',
                }}></label>
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    width: '30%'
                }}>
                    <div style={{
                        width: 35,
                        height: 35,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        borderRadius: 17.5,
                        backgroundColor: 'green',
                        boxShadow: '0 0 5px #ccc',
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        acceptCall()
                    }}>
                        <img src="/images/call/end-call.svg" width="20" height="25" />
                    </div>
                    <div style={{
                        width: 35,
                        height: 35,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        borderRadius: 17.5,
                        backgroundColor: 'red',
                        boxShadow: '0 0 5px #ccc',
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#push-notification').css({
                            top: -400
                        })
                        topRef.current = -400
                    }}>
                        <img src="/images/call/end-call.svg" width="20" height="25" />
                    </div>
                </div>
            </div>

            <div id="sidebar" className="sidebar">
                <div id="buttons" className="buttons">
                    <div className="logo">
                        <img alt="image" src="/images/student/logo.svg" width="45" height="45"
                            onClick={() => {
                                History.push('/')
                            }} />
                    </div>
                    <div id="trainers-btn" className="item">
                        <img alt="image" src="/images/student/online-course.svg" width="30" height="30" />
                    </div>
                    <div id="rating-btn" className="item">
                        <img alt="image" src="/images/student/rating.svg" width="30" height="30" />
                    </div>
                    <div id="calls-btn" className="item">
                        <img alt="image" src="/images/call/end-call.svg" width="30" height="30" />
                    </div>
                </div>
            </div>

            <header>
                <div className="right">
                    <div className="coins">
                        <img alt="image" src="/images/student/dollar.svg" width="20" height="20" /><label>{coins}</label>
                    </div>
                    <div className="level">
                        <img alt="image" src="/images/student/trophy-cup.svg" width="20" height="20" /><label>{points}</label>
                    </div>
                    <div className="profile">
                        <img src={profileURI} alt="image" height="40" />
                    </div>
                </div>
                <ul id="notification-list" className="notification-list">
                    {messages.map((val, index) => {
                        // console.log(val, chat, chat.id === val.id)
                        return (
                            <li key={String(index)}>
                                <img src={'/images/' + (val.sender.type === 'human' ? 'email' : 'medal') + '.svg'} width="25" height="25" />
                                <label>{val.content}</label>
                                <small>{moment(val.date).format('DD:MM hh:mm A')}</small>
                            </li>
                        )
                    })}
                </ul>
            </header>

            <div id="container" className="container-student">
                <div id="trainers" className="trainers">
                    <div className="content">
                        <div className="item">
                            <div className="image">
                                <img alt="image" src="/images/student/speedometer.svg" width="100" height="100" />
                            </div>
                            <div className="info">
                                <h3> </h3>
                                <button onClick={() => {
                                    History.push('/games/mental-count', {
                                        ...state,
                                        role: 'student'
                                    })
                                }}></button>
                                <small> :   14:34</small>
                            </div>
                        </div>
                        <div className="item">
                            <div className="image">
                                <img alt="image" src="/images/student/crowd.svg" width="100" height="100" />
                            </div>
                            <div className="info">
                                <h3></h3>
                                <button onClick={() => {
                                    History.push('/games/multiply')
                                    // alert(' ')
                                }}></button>
                                <small> :   14:34</small>
                            </div>
                        </div>
                        <div className="item">
                            <div className="image">
                                <img alt="image" src="/images/student/divide.svg" width="100" height="100" />
                            </div>
                            <div className="info">
                                <h3></h3>
                                <button onClick={() => {
                                    History.push('/games/divide')
                                }}></button>
                                <small> :   14:34</small>
                            </div>
                        </div>
                        {/* <div className="item">
                            <div className="image">
                                <img alt="image" src="/images/student/coding.svg" width="100" height="100" />
                            </div>
                            <div className="info">
                                <h3></h3>
                                <button onClick={() => {
                                    // History.push('/games/programming')
                                    alert(' ')
                                }}></button>
                                <small> :   14:34</small>
                            </div>
                        </div> */}
                        <div className="item">
                            <div className="image">
                                <img alt="image" src="/images/student/frequency.svg" width="100" height="100" />
                            </div>
                            <div className="info">
                                <h3> </h3>
                                <button onClick={() => {
                                    History.push('/games/shulte')
                                }}></button>
                                <small> :   14:34</small>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="quests" className="quests">
                    <div className="head">
                        <label id="last-update-quests">
                              : 5 
                        </label>
                        <div className="tables-name">
                            <label></label>
                            <label></label>
                        </div>
                    </div>
                    <ul>
                        <li>
                            <div className="content">
                                <img alt="image" src="/images/student/wizard.svg" width="35" height="35" />
                                <label>     10 </label>
                            </div>
                            <div className="information">
                                <div className="coins">
                                    <img alt="image" src="/images/student/dollar.svg" width="20" height="20" /><label>+100</label>
                                </div>
                                <div className="status">
                                    <img alt="image" src="/images/student/crown.svg" width="25" height="25" />
                                </div>
                            </div>
                        </li>
                        <li>
                            <div className="content">
                                <img alt="image" src="/images/student/assasin.svg" width="35" height="35" />
                                <label>     10 </label>
                            </div>
                            <div className="information">
                                <div className="coins">
                                    <img alt="image" src="/images/student/dollar.svg" width="20" height="20" /><label>+100</label>
                                </div>
                                <div className="status">
                                    <img alt="image" src="/images/student/crown.svg" width="25" height="25" />
                                </div>
                            </div>
                        </li>
                        <li>
                            <div className="content">
                                <img alt="image" src="/images/student/wizard.svg" width="35" height="35" />
                                <label>     10 </label>
                            </div>
                            <div className="information">
                                <div className="coins">
                                    <img alt="image" src="/images/student/dollar.svg" width="20" height="20" /><label>+100</label>
                                </div>
                                <div className="status">
                                    <img alt="image" src="/images/student/crown.svg" width="25" height="25" />
                                </div>
                            </div>
                        </li>
                        <li>
                            <div className="content">
                                <img alt="image" src="/images/student/assasin.svg" width="35" height="35" />
                                <label>     10 </label>
                            </div>
                            <div className="information">
                                <div className="coins">
                                    <img alt="image" src="/images/student/dollar.svg" width="20" height="20" /><label>+100</label>
                                </div>
                                <div className="status">
                                    <img alt="image" src="/images/student/crown.svg" width="25" height="25" />
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                {/* <div id="crm" className="crm">
                    <div className="head">
                        <div className="title">
                            <h3> ,   .</h3>
                            <small>            </small>
                        </div>
                        <div className="panel">
                            <div style={{
                                backgroundColor: '#796F9C'
                            }} className="btn" id="plus-button">
                                +
                            </div>
                            <div className="btn" id="sort-button">
                                <img alt="image" src="/images/student/bars.svg" width="20" />
                            </div>
                        </div>
                    </div>
                    <div id="crm-calendar" className="calendar">
                    </div>
                </div>
                <div id="tasks" className="tasks">
                    <h1>Tasks</h1>
                        </div> */ }
                <div id="rating" className="rating">
                    <h1>Rating</h1>
                </div>
                <div id="calls" className="calls">
                    <h1>Calls</h1>
                </div>
                {/* <div id="chat" className="chat">
                    <div className="chat-box">
                        <div className="chats">
                            <div className="search">
                                <img alt="image" src="/images/student/loupe.svg" width="20" height="20" />
                                <input type="text" maxLength="25" placeholder=" ..." />
                            </div>
                            <ul>
                                {Object.values(chats.current).length <= 0 ? '' : Object.values(chats.current).map((val, index) => {
                                    // console.log(clients, clients[val.name], val)
                                    if (val.messages) return (
                                        <li style={{
                                            backgroundColor: chat.id === val.id ? '#f1f1f1' : '#fff'
                                        }} key={val.id ? val.id : index} className="chat-item">
                                            <div className="img">
                                                <img alt="image" src="https://klike.net/uploads/posts/2019-03/1551512888_2.jpg" width="60" height="60" />
                                            </div>
                                            <div className="name-last-message">
                                                <label>{String(val.name)}</label>
                                                <small className="last-message">{String(val.messages.current[val.messages.current.length - 1].content)}</small>
                                            </div>
                                            <div className="time-new-message">
                                                {/* moment(val.messages.current[val.messages.current.length - 1].date).format('hh:mm A') */}
                {/* <label>{clients.current[val.name].status}</label> */}
                {/* <div className="new-message">
                                                    5
                                                </div>
                                            </div>
                                        </li>
                                    )
                                })}
                            </ul>
                        </div >
                        <div className="messaging">
                            <div className="head">
                                <div className="add-btn"><img alt="image" src="/images/student/add-user.svg" width="20" height="20" /> 
                                </div>
                                <label onClick={e => {
                                    e.preventDefault()
                                    call()
                                }}></label>
                            </div>
                            <ul className="messages">
                                {
                                    chat && chat.messages && !Array.isArray(chat.messages) ? chat.messages.current.map((val, index) => {
                                        return (
                                            <li style={{
                                                alignSelf: val.isMy === true ? 'flex-end' : 'flex-start',
                                                alignItems: val.isMy === true ? "flex-end" : "flex-start"
                                            }} key={String(index)}>
                                                <label>{val.content}</label>
                                                <small>{moment(val.date).format('hh:mm A')}</small>
                                            </li>
                                        )
                                    }) : chat && chat.messages ? chat.messages.map((val, index) => {
                                        return (
                                            <li style={{
                                                alignSelf: val.isMy === true ? 'flex-end' : 'flex-start',
                                                alignItems: val.isMy === true ? "flex-end" : "flex-start"
                                            }} key={String(index)}>
                                                <label>{val.content}</label>
                                                <small>{moment(val.date).format('hh:mm A')}</small>
                                            </li>
                                        )
                                    }) : ''}
                            </ul>
                            <div className="input-message">
                                <ul id="clip-items" className="clip-items">
                                    <li><img alt="image" src="/images/student/image.svg" /></li>
                                    <li><img alt="image" src="/images/student/video.svg" /></li>
                                    <li><img alt="image" src="/images/student/file.svg" /></li>
                                </ul>
                                <div className="input">
                                    <img alt="image" id="clip-hover" src="/images/student/paperclip.svg" width="20" height="20" />
                                    <input placeholder=" ..." type="text" id="message" onChange={(e) => {
                                        e.preventDefault()
                                        updateMessage(e.target.value)
                                    }} />
                                </div>
                                <div onClick={e => {
                                    e.preventDefault()
                                    if (!message) return
                                    sendMessage({
                                        content: message,
                                        date: Date.now()
                                    })
                                }} className="send-btn">
                                    <img alt="image" src="/images/student/paper-plane.svg" width="20" height="20" />
                                </div>
                            </div>
                        </div>
                    </div >
                </div > */}
            </div >
        </div >
    )
}

export default Student;