import React, { useEffect, useState } from 'react';

import MoreButton from '../../../components/MoreButton';
import $ from 'jquery'

import Upload from '../../../lib/upload';
import moment from 'moment';
import Loader from 'react-loader-spinner';

const ProfileContainer = ({ state }) => {

    const [currentFile, setFile] = useState(null);
    const [files, setFiles] = useState(null);

    const loadFiles = () => {
        const formData = new FormData()

        formData.append("id", state.id)

        $.ajax({
            method: 'POST',
            url: 'https://portfolio.mindclickon.com/load',
            data: JSON.stringify({
                id: state.id
            }),
            contentType: 'application/json',
            processData: false,
            success: (data) => {
                console.log(data)
                if(data.status === true)
                {
                    setFiles(data.files)
                }
            },
            error: (err) => {
                console.log(err)
                alert(' !')
            },
            cache: false,
            timeout: 60000,
            async: true,
        })
    }

    useEffect(() => {
        loadFiles()
    }, [])

    return (
        <div style={{ display: 'none' }} className="container-teacher" id="profile-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/teacher/user.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">My Profile</label>
                        <label className="translate">Profile with personal data and your portfolio</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content">
                <input id={"file-get"} type={'file'} style={{
                    display: 'none'
                }} onChange={e => {
                    if(!e.target.files || e.target.files.length <= 0) return
                    setFile(URL.createObjectURL(e.target.files[0]))
                    var file = e.target.files[0];
                    var upload = new Upload(file, state.id);

                    upload.doUpload(loadFiles)
                    $('#portfolio-modal').css({
                        display: 'flex'
                    })
                }} name="file" />
                <div className="profile-photo">
                    <img className="data-uri"
                        src="https://prikolist.club/wp-content/uploads/2019/07/Kartinki_kot_v_ochkah_1_29100843.jpg" width="200"
                        height="200" />
                </div>
                <label className="profile-name data-name">
                    ...
                </label>
                <div className="portfolio-list" style={{
                    height: 300,
                    minHeight: 300
                }}>
                    <div className="head translate" style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                    }}>
                        <label>Portfolio (Images, Videos)</label>
                        <div style={{
                            display: 'flex',
                            flexDirection: 'row',
                            alignItems: 'center',
                        }}>
                            <small style={{
                                color: '#ccc',
                                fontSize: 12,
                                marginRight: 10
                            }}>    ,  .</small>
                            <MoreButton size={.6} onClick={() => {
                                $('#file-get').trigger('click')
                            }} />
                        </div>
                    </div>
                    <div className="page-content" style={{
                        overflowX: 'auto',
                        flex: 1
                    }}>
                        {files === null ? 
                            <div style={{
                                flex: 1,
                                justifyContent: 'center',
                                alignItems: 'center',
                                display: 'flex'
                            }}>
                                ...
                            </div>
                        : files.length == 0 || !files.length ?
                            <div style={{
                                flex: 1,
                                alignItems: 'center',
                                justifyContent: 'center',
                                display: 'flex'
                            }}>
                                    !
                            </div>
                        :
                            <div style={{
                                flex: 1,
                                display: 'flex',
                                flexDirection: 'row',
                                alignItems: 'center',
                                paddingLeft: 10,
                                paddingRight: 10
                            }}>
                                {files.map((val, index) => {
                                    return (
                                        <div key={index} style={{
                                            height: 200,
                                            maxWidth: 170,
                                            width: 170,
                                            display: 'flex',
                                            flexDirection: 'column',
                                            alignItems: 'center',
                                            justifyContent: 'space-evenly',
                                            marginRight: 12,
                                        }}>
                                            <img style={{
                                                width: 70
                                            }} src={['png','jpeg','jpg'].includes(val.filename.split('.').pop()) ? 'https://pdf.mindclickon.com/storage/' + val.originalFilename : 
                                            val.filename.split('.').pop() === 'pdf' ? '/images/edit/pdf.png' :
                                            val.filename.split('.').pop() === 'svg' ? '/images/edit/svg.png' :
                                            val.filename.split('.').pop() === 'doc' || val.filename.split('.').pop() === 'docx' ? '/images/edit/doc.png' :
                                            val.filename.split('.').pop() === 'xls' || val.filename.split('.').pop() === 'xlsx' ? '/images/edit/sheets.png' :
                                            val.filename.split('.').pop() === 'txt' ? '/images/edit/txt.png' :
                                            val.filename.split('.').pop() === 'csv' ? '/images/edit/csv.png' :
                                            '/images/edit/unknown-file.png'
                                            } />
                                            <label style={{
                                                color: '#222',
                                                fontSize: 15,
                                                textAlign: 'center'
                                            }}>{val.filename.length > 10 ? val.filename.slice(0, 10) + '...' : val.filename}</label>
                                            <span style={{
                                                display: 'flex',
                                                justifyContent: 'center',
                                                flexDirection: 'column',
                                                alignItems: 'center'
                                            }}>
                                                <label style={{
                                                    color: '#ccc',
                                                    fontSize: 11,
                                                    textAlign: 'center'
                                                }}>: {moment(val.date).fromNow(false)}</label>
                                                <label style={{
                                                    color: '#ccc',
                                                    fontSize: 11,
                                                    textAlign: 'center'
                                                }}>{'"' + val.filename.split('.').pop().toUpperCase() + '" '}</label>
                                            </span>
                                        </div>
                                    )
                                })}
                            </div>}
                    </div>
                </div>
                <div className="personal-data">
                    <div className="head translate">
                        Personal Data
                    </div>
                    <div className="page-content">
                        <label>ZUL code: <label id="data-code" className="black">...</label></label>
                        <label className="translate">Email: <label id="data-email" className="black">...</label></label>
                        <label className="translate">Password: <label className="black">***</label></label>
                    </div>
                </div>
            </div>
        </div>
    ) 
}

export default ProfileContainer;