import React, { useEffect } from "react";

import { useHistory } from "react-router";

import $ from 'jquery'

const MentalGenerator = () => {
    useEffect(() => {
        const update_viewer = (file) => {
            const objectURL = 'https://pdf.mindclickon.com/mental/' + file
            $('#pdf-viewer').attr('src', objectURL)
            $('#pdf-viewer-obj').attr('data', objectURL)
        }

        var TERMS = 10
        var TABLES_H = 5
        var TABLES_H_MAX = 8
        var ROWS = 3
        var ROWS_MAX = 8
        var isAutoHeight = false
        var isGrowByRows = false

        $('#p-term').on('click', e => {
            e.preventDefault()
            const oldValue = Number($('#terms').text())
            if (oldValue < 50) {
                TERMS = oldValue + 1
                $('#terms').text(String(TERMS))
            }
        })

        $('#m-term').on('click', e => {
            e.preventDefault()
            const oldValue = Number($('#terms').text())
            if (oldValue > 2) {
                TERMS = oldValue - 1
                $('#terms').text(String(TERMS))
            }
        })

        $('#p-tables-h').on('click', e => {
            e.preventDefault()
            if ($('#tables-h-item').prop('disabled')) return;
            const oldValue = Number($('#tables-h').text())
            if (oldValue < TABLES_H_MAX) {
                TABLES_H = oldValue + 1
                $('#tables-h').text(String(TABLES_H))
            }
        })

        $('#m-tables-h').on('click', e => {
            e.preventDefault()
            if ($('#tables-h-item').prop('disabled')) return;
            const oldValue = Number($('#tables-h').text())
            if (oldValue > 3) {
                TABLES_H = oldValue - 1
                $('#tables-h').text(String(TABLES_H))
            }
        })

        $('#p-rows').on('click', e => {
            e.preventDefault()
            if (isAutoHeight) return
            const oldValue = Number($('#rows').text())
            if (oldValue < ROWS_MAX) {
                ROWS = oldValue + 1
                $('#rows').text(String(ROWS))
            }
        })

        $('#design-check').on('change', e => {
            e.preventDefault()
            if ($('#design-check').prop('checked') === true) {
                $('#check-rows').prop('checked', true)
                $('#check-rows').prop('disabled', true)
                $('#tables-h-item').prop('disabled', true)
                $('#tables-h-item').css({
                    opacity: .5,
                    cursor: 'not-allowed'
                })
                $('#p-tables-h').css('cursor', 'not-allowed')
                $('#m-tables-h').css('cursor', 'not-allowed')
            } else {
                $('#check-rows').prop('disabled', false)
                $('#tables-h-item').prop('disabled', false)
                $('#tables-h-item').css({
                    opacity: 1,
                    cursor: 'pointer'
                })
                $('#p-tables-h').css('cursor', 'pointer')
                $('#m-tables-h').css('cursor', 'pointer')
            }
        })

        $('#m-rows').on('click', e => {
            e.preventDefault()
            if (isAutoHeight) return
            const oldValue = Number($('#rows').text())
            if (oldValue > 3) {
                ROWS = oldValue - 1
                $('#rows').text(String(ROWS))
            }
        })

        $('#check-rows').on('change', e => {
            e.preventDefault()
            if (isAutoHeight) {
                $('.rows-opacity').css({
                    opacity: "1",
                    cursor: 'default'
                })
                $('.rows-cursor').css({
                    cursor: 'pointer'
                })
                isAutoHeight = false
            }
            else {
                $('.rows-opacity').css({
                    opacity: ".5",
                    cursor: 'not-allowed'
                })
                $('.rows-cursor').css({
                    cursor: 'not-allowed'
                })
                isAutoHeight = true
            }
        })

        $(function () {
            $('input[type=radio]').change(function (e) {
                if (this.id == 'portrait') {
                    TABLES_H_MAX = 8
                    if (TABLES_H > 8) {
                        TABLES_H = 8
                        $('#tables-h').text(String(TABLES_H))
                    }
                    $('#max_tables').text(`: ${TABLES_H_MAX}\n     !`)
                } else {
                    TABLES_H_MAX = 10
                    $('#max_tables').text(`: ${TABLES_H_MAX}`)
                }
            });
        });

            $('#ready').on('click', e => {
                e.preventDefault()
                console.log('Clicked!')
                var min = $('#diapason').val().split('-')[0]
                var max = $('#diapason').val().split('-')[1]
                if ($('#formula').val().includes(':')) {
                    const diapason = $('#formula').val().split(':')[1]
                    min = diapason.split('-')[0]
                    max = diapason.split('-')[1]
                }
                if(!$('#page_count').val() )
                {
                    alert('  !')
                    return
                }
                const DATA = {
                    'formula': $('#formula').val().replace(/\:.*$/gi, ''),
                    'min': Number(min),
                    'max': Number(max),
                    'term_count': Number($('#terms').text()),
                    'page_count': Number($('#page_count').val()),
                    'table_size': {
                        'h_count': $('#design-check').prop('checked') ? 10 : Number($('#tables-h').text()),
                        'v_count': Number($('#rows').text()),
                        'isAutoHeight': isAutoHeight
                    },
                    'grow': {
                        'byPage': $('#byPage').prop('checked'),
                        'byRows': Number($('#grow-rows').text()),
                        'isGrowByRows': isGrowByRows,
                        'isGrow': $('#byPage').prop('checked') === true || isGrowByRows === true
                    },
                    'orientation': ($('#portrait').prop('checked') === true ? 'p' : 'l'),
                    'withRight': $('#withRight').prop('checked'),
                    'design': $('#design-check').prop('checked'),
                    'fileFormat': ($('#pdf').prop('checked') === true ? 'pdf' : 'excel')
                }
                console.log(DATA)
                $.ajax({
                    method: 'POST',
                    url: 'https://app.mindclickon.com/mental-generator',
                    dataType: 'json',
                    data: DATA,
                    success: (response) => {
                        console.log(response)
                        if (!response.status) {
                            console.log(response)
                            return
                        }
                        if(DATA.fileFormat === 'pdf') update_viewer(response.file)
                        else {
                            console.log('https://pdf.mindclickon.com/storage/' + response.file)
                            $('#excel-frame').attr('src', 'https://pdf.mindclickon.com/mental/' + response.file)
                        }
                    },
                    error: (jqXHR, textStatus) => {
                        alert(' !')
                        console.log(jqXHR, textStatus)
                    }
                })
            })
    }, [])

    const History = useHistory();

    return (
        <div style={{
            width: '100%',
            height: '100%',
            backgroundColor: '#fff',
            display: 'flex',
            flexDirection: 'row'
        }}>
            <iframe id="excel-frame" style={{
                display: 'none'
            }}></iframe>
            {/* Settings */}
            <div style={{
                width: '30%',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                overflowY: 'scroll'
            }}>
                {/* Header */}
                <div style={{
                    width: '100%',
                    height: 56,
                    minHeight: 56,
                    color: '#fff', 
                    fontWeight: 500,
                    fontSize: 21,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    background: 'linear-gradient(to right, dodgerblue, #6ea5ff)',
                    boxShadow: '0 4px 5px #ccc'
                }}>
                    <div className="back-item" onClick={e => {
                        e.preventDefault()
                        History.replace('/teacher')
                    }}>
                        <img src="/images/left-arrow.svg" width="30" height="30" />
                    </div>
                    <div style={{
                        flex: 1,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}></div>
                </div>
                {/* Formula */}
                <div style={{
                    width: '80%',
                    display: 'flex',
                    flexDirection: 'column',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        margin: 0,
                        marginBottom: 10
                    }}>
                        :
                    </label>
                    <select style={{
                        outline: '0',
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 10px',
                        border: '1px solid #ccc',
                        boxShadow: '0 0 5px #ccc',
                        fontSize: 17,
                        backgroundColor: '#fff',
                        borderRadius: 5,
                        margin: 0
                    }} id="formula">
                        <option value="NF:1-4">   4 ()</option>
                        <option value="NF:1-5">   5 ()</option>
                        <option value="NF"> </option>
                        <option value="+1=+5-4">+1=+5-4</option>
                        <option value="-1=+4-5">-1=+4-5</option>
                        <option value="+2=+5-3">+2=+5-3</option>
                        <option value="-2=+3-5">-2=+3-5</option>
                        <option value="+3=+5-2">+3=+5-2</option>
                        <option value="-3=+2-5">-3=+2-5</option>
                        <option value="+4=+5-1">+4=+5-1</option>
                        <option value="-4=+1-5">-4=+1-5</option>
                        <option value="LF"> </option>
                        <option value="+1=-9+10">+1=-9+10</option>
                        <option value="+2=-8+10">+2=-8+10</option>
                        <option value="+3=-7+10">+3=-7+10</option>
                        <option value="+4=-6+10">+4=-6+10</option>
                        <option value="+5=-5+10">+5=-5+10</option>
                        <option value="-1=-10+9">-1=-10+9</option>
                        <option value="-2=-10+8">-2=-10+8</option>
                        <option value="-3=-10+7">-3=-10+7</option>
                        <option value="-4=-10+6">-4=-10+6</option>
                        <option value="-5=-10+5">-5=-10+5</option>
                        <option value="+9=-1+10">+9=-1+10</option>
                        <option value="-9=-10+1">-9=-10+1</option>
                        <option value="+8=-2+10">+8=-2+10</option>
                        <option value="-8=-10+2">-8=-10+2</option>
                        <option value="+7=-3+10">+7=-3+10</option>
                        <option value="-7=-10+3">-7=-10+3</option>
                        <option value="+6=-4+10">+6=-4+10</option>
                        <option value="-6=-10+4">-6=-10+4</option>
                        <option value="BF"> </option>
                        <option value="+9=+4-5+10">+9=+4-5+10</option>
                        <option value="-9=-10+5-4">-9=-10+5-4</option>
                        <option value="+8=+3-5+10">+8=+3-5+10</option>
                        <option value="-8=-10+5-3">-8=-10+3-5</option>
                        <option value="+7=+2-5+10">+7=+2-5+10</option>
                        <option value="-7=-10+5-2">-7=-10+5-2</option>
                        <option value="+6=+1-5+10">+6=+1-5+10</option>
                        <option value="-6=-10+5-1">-6=-10+5-1</option>
                        <option value="FF"></option>
                        <option value="MIX"> </option>
                    </select>
                </div>
                {/* Term Size */}
                <div style={{
                    width: '80%',
                    display: 'flex',
                    flexDirection: 'column',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        margin: 0,
                        marginBottom: 10
                    }}>
                         :
                    </label>
                    <select style={{
                        outline: '0',
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 10px',
                        border: '1px solid #ccc',
                        boxShadow: '0 0 5px #ccc',
                        fontSize: 17,
                        backgroundColor: '#fff',
                        borderRadius: 5,
                        margin: 0
                    }} id="diapason">
                        <option value="1-9"> 1  9</option>
                        <option value="1-99"> 1  99</option>
                        <option value="10-99"> 10  99</option>
                        <option value="100-999"> 100  999</option>
                        <option value="1-999"> 1  999</option>
                        <option value="10-999"> 10  999</option>
                    </select>
                </div>
                {/* Term Count */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        margin: 0,
                        marginBottom: 10
                    }}>
                        - :
                    </label>
                    <div style={{
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        flexDirection: 'row',
                        border: '1px solid #ccc',
                        borderRadius: 5,
                        boxShadow: '0 0 5px #ccc',
                        overflow: 'hidden',
                        marginTop: 10
                    }}>
                        {/* Minus */}
                        <div id="m-term" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'tomato',
                            boxShadow: '3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/minus.svg' width="20" height="20" />
                        </div>
                        {/* Content */}
                        <div style={{
                            flex: 1,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            fontSize: 19,
                            fontWeight: 500
                        }} id='terms'>
                            10
                        </div>
                        {/* Plus */}
                        <div id="p-term" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'green',
                            boxShadow: '-3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/add.svg' width="20" height="20" />
                        </div>
                    </div>
                </div>
                {/* With Right Checkbox */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <input id="withRight" type="checkbox" />
                    <label style={{
                        marginLeft: 10,
                        fontSize: 17,
                        fontWeight: 500
                    }}>   </label>
                </div>
                {/* Page Count */}
                <div style={{
                    width: '80%',
                    display: 'flex',
                    flexDirection: 'column',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        fontWeight: 500,
                        marginBottom: 10
                    }}>
                        - :
                    </label>
                    <input style={{
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 10px',
                        border: '1px solid #ccc',
                        outline: '0',
                        borderRadius: 5,
                        fontSize: 17,
                        boxShadow: '0 0 5px #ccc'
                    }} type="number" id="page_count" pattern="\d+" min="1" max="100" />
                    <small>: 100</small>
                </div>
                {/* Columns Count */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        margin: 0,
                        marginBottom: 10
                    }}>
                        - :
                    </label>
                    <div style={{
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        flexDirection: 'row',
                        border: '1px solid #ccc',
                        borderRadius: 5,
                        boxShadow: '0 0 5px #ccc',
                        overflow: 'hidden',
                        marginTop: 10
                    }}>
                        {/* Minus */}
                        <div id="m-tables-h" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'tomato',
                            boxShadow: '3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/minus.svg' width="20" height="20" />
                        </div>
                        {/* Content */}
                        <div style={{
                            flex: 1,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            fontSize: 19,
                            fontWeight: 500
                        }} id="tables-h">
                            10
                        </div>
                        {/* Plus */}
                        <div id="p-tables-h" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'green',
                            boxShadow: '-3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/add.svg' width="20" height="20" />
                        </div>
                    </div>
                    <small id="max_tables" style={{
                        color: '#ccc'
                    }} id="max_tables">: 10<br />    
                        !</small>
                </div>
                {/* Rows Count */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <label style={{
                        fontSize: 17,
                        margin: 0,
                        marginBottom: 10
                    }}>
                        - :
                    </label>
                    <div className="rows-opacity" style={{
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        flexDirection: 'row',
                        border: '1px solid #ccc',
                        borderRadius: 5,
                        boxShadow: '0 0 5px #ccc',
                        overflow: 'hidden',
                        marginTop: 10
                    }}>
                        {/* Minus */}
                        <div className="rows-cursor" id="m-rows" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'tomato',
                            boxShadow: '3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/minus.svg' width="20" height="20" />
                        </div>
                        {/* Content */}
                        <div style={{
                            flex: 1,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            fontSize: 19,
                            fontWeight: 500
                        }} id="rows">
                            10
                        </div>
                        {/* Plus */}
                        <div className="rows-cursor" id="p-rows" style={{
                            width: 50,
                            height: 50,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            backgroundColor: 'green',
                            boxShadow: '-3px 0 4px #ccc',
                            cursor: 'pointer'
                        }}>
                            <img src='/images/add.svg' width="20" height="20" />
                        </div>
                    </div>
                    <div style={{
                        width: '100%',
                    }}>
                        <input id="check-rows" type="checkbox" />
                        <label style={{
                            marginLeft: 10,
                            fontSize: 17,
                            fontWeight: 500
                        }}> </label>
                    </div>
                </div>
                {/* By Page */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <input id="byPage" type="checkbox" />
                    <label style={{
                        marginLeft: 10,
                        fontSize: 17,
                        fontWeight: 500
                    }}>    </label>
                </div>
                {/* Design */}
                <div style={{
                    width: '80%',
                    marginTop: 20
                }}>
                    <input type="checkbox" id="design-check" />
                    <label style={{
                        marginLeft: 10,
                        fontSize: 17,
                        fontWeight: 500
                    }}>  </label>
                </div>
                {/* Orientation */}
                <div style={{
                    marginTop: 20,
                    width: '80%',
                    marginBottom: 20,
                }}>
                    <h4>:</h4>
                    <div style={{
                        marginLeft: 10,
                        marginTop: 10
                    }}>
                        <input type="radio" name="orientation" id="portrait" value="option1" checked />
                        <label style={{
                            fontSize: 17,
                            fontWeight: 500,
                            marginLeft: 10
                        }}>
                            
                        </label>
                    </div>
                    <div style={{
                        marginTop: 10,
                        marginLeft: 10
                    }}>
                        <input type="radio" name="orientation" id="landscape" value="option2" />
                        <label style={{
                            fontSize: 17,
                            fontWeight: 500,
                            marginLeft: 10
                        }}>
                            
                        </label>
                    </div>
                </div>
                {/* Orientation */}
                <div style={{
                    marginTop: 20,
                    width: '80%',
                    marginBottom: 20,
                }}>
                    <h4> :</h4>
                    <div style={{
                        marginLeft: 10,
                        marginTop: 10
                    }}>
                        <input type="radio" name="fileformat" id="pdf" value="option1" checked />
                        <label style={{
                            fontSize: 17,
                            fontWeight: 500,
                            marginLeft: 10
                        }}>
                            PDF 
                        </label>
                    </div>
                    <div style={{
                        marginTop: 10,
                        marginLeft: 10
                    }}>
                        <input type="radio" name="fileformat" id="excel" value="option2" />
                        <label style={{
                            fontSize: 17,
                            fontWeight: 500,
                            marginLeft: 10
                        }}>
                            Excel  (.xlsx)
                        </label>
                    </div>
                </div>
                <div style={{
                    minWidth: 200,
                    fontSize: 19,
                    minHeight: 50,
                    borderRadius: 5,
                    background: 'dodgerblue',
                    color: '#fff',
                    fontWeight: 500,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    userSelect: 'none',
                    cursor: 'pointer'
                }} id="ready"></div>
            </div>
            {/* PDF Viewer */}
            <div style={{
                width: '70%',
                height: '100%'
            }}>
                <object id="pdf-viewer-obj" data=''
                    type="application/pdf" width="100%" height="100%">

                        {/* https://www.w3.org/WAI/ER/tests/xhtml/testfiles/resources/pdf/dummy.pdf */}
                    <iframe id="pdf-viewer" src='' width="100%"
                        height="100%">
                        <p>    PDF!</p>
                    </iframe>

                </object>
            </div>
        </div>
    )
}

export default MentalGenerator