import React, { useEffect, useRef } from "react";

import $ from 'jquery'
import { useHistory } from "react-router";

const TaskEditor = ({ location: { state } }) => {
    const History = useHistory()
    const canvasRef = useRef(null)
    const tool = useRef(null)
    const history = useRef([])

    const handOffset = useRef([0, 0])
    const lastHandOffset = useRef([0, 0])

    const zoomSize = useRef(1)

    const isHolding = useRef(false)
    const mousePosition = useRef([0, 0])

    const currentFramePosition = useRef(25)

    const selectBox = useRef(null)
    const hoverBox = useRef(null)
    const ctrlKey = useRef(false)
    const selectOffset = useRef([0, 0])

    useEffect(() => {
        const appendHistory = (item) => {
            history.current = [...history.current, item]
        }

        const canvas = canvasRef.current
        canvas.style.width = '100%';
        canvas.style.height = '100%';
        canvas.width = canvas.offsetWidth;
        canvas.height = canvas.offsetHeight;

        $([window, document, canvas]).on('resize', e => {
            e.preventDefault()
            console.log('resize')
            canvas.style.width = '100%';
            canvas.style.height = '100%';
            canvas.width = canvas.offsetWidth;
            canvas.height = canvas.offsetHeight;
        })

        window.addEventListener('resize', () => {
            canvas.style.width = '100%';
            canvas.style.height = '100%';
            canvas.width = canvas.offsetWidth;
            canvas.height = canvas.offsetHeight;
        })

        const ctx = canvas.getContext('2d')
        const width = Number(ctx.canvas.width)
        const height = Number(ctx.canvas.height)

        var mouseClickPosition = null
        var mouseEndPosition = null

        function rgbToHex(r, g, b) {
            if (r > 255 || g > 255 || b > 255)
                throw "Invalid color component";
            return ((r << 16) | (g << 8) | b).toString(16);
        }

        canvas.onmousemove = (e) => {
            e.preventDefault()
            mousePosition.current = [e.clientX - 250, e.clientY - 50]
            if (isHolding.current && tool.current === 'hand') {
                handOffset.current = [(lastHandOffset.current[0] + mousePosition.current[0] - mouseClickPosition[0]), (lastHandOffset.current[1] + mousePosition.current[1] - mouseClickPosition[1])]
            } else if (tool.current === 'vector') {
                if (!isHolding.current) {
                    const selectedItems = []
                    var IDENTIFIER = 0
                    for (var item of history.current) {
                        if (clickItem(item, mousePosition.current)) selectedItems.push({
                            ...item,
                            id: IDENTIFIER
                        })
                        IDENTIFIER++
                    }
                    if (selectedItems.length > 0) {
                        if ((!e.ctrlKey && !ctrlKey.current) || selectedItems.length < 2) hoverBox.current = {
                            ...selectedItems[selectedItems.length - 1],
                            strokeColor: 'dodgerblue'
                        }
                        else {
                            hoverBox.current = {
                                ...selectedItems[selectedItems.length - 2],
                                strokeColor: 'dodgerblue',
                            }
                        }
                    }
                    else hoverBox.current = null
                } else if (selectBox.current) {
                    const position = mousePosition.current;
                    [history.current[selectBox.current.id].x, history.current[selectBox.current.id].y] = [position[0] - selectOffset.current[0], position[1] - selectOffset.current[1]]
                    selectBox.current = {
                        ...selectBox.current,
                        x: position[0] - selectOffset.current[0],
                        y: position[1] - selectOffset.current[1]
                    }
                }
            }
        }
        canvas.onmouseup = (e) => {
            e.preventDefault()
            isHolding.current = false
            if (tool.current === 'hand') lastHandOffset.current = [handOffset.current[0], handOffset.current[1]]
            if (['rect', 'line'].includes(tool.current) && mouseClickPosition && mouseEndPosition) {
                appendHistory({
                    type: 'rect',
                    style: 'fill',
                    x: ((mouseClickPosition[0] / zoomSize.current) - handOffset.current[0]),
                    y: ((mouseClickPosition[1] / zoomSize.current) - handOffset.current[1]),
                    width: (mouseEndPosition[0] - mouseClickPosition[0]) / zoomSize.current,
                    height: (mouseEndPosition[1] - mouseClickPosition[1]) / zoomSize.current,
                    fillColor: '#cccccc',
                })
            }
        }
        canvas.onmouseleave = (e) => {
            e.preventDefault()
            isHolding.current = false
            hoverBox.current = null
        }
        canvas.onmousedown = (e) => {
            e.preventDefault()
            isHolding.current = true
            mousePosition.current = [e.clientX - 250, e.clientY - 50]
            mouseClickPosition = [e.clientX - 250, e.clientY - 50]
            if (tool.current !== 'vector') return
            const selectedItems = []
            var IDENTIFIER = 0
            for (var item of history.current) {
                if (clickItem(item, mouseClickPosition)) selectedItems.push({
                    ...item,
                    id: IDENTIFIER
                })
                IDENTIFIER++
            }
            if (selectedItems.length > 0) {
                if (!e.ctrlKey || selectedItems.length < 2) selectBox.current = {
                    ...selectedItems[selectedItems.length - 1],
                    strokeColor: 'dodgerblue'
                }
                else {
                    selectBox.current = {
                        ...selectedItems[selectedItems.length - 2],
                        strokeColor: 'dodgerblue',
                    }
                }
                selectOffset.current = [mousePosition.current[0] - selectBox.current.x, mousePosition.current[1] - selectBox.current.y]
                $('#i-x').val(selectBox.current.x)
                $('#i-y').val(selectBox.current.y)
                $('#i-w').val(selectBox.current.width)
                $('#i-h').val(selectBox.current.height)
                $('#i-color').val(selectBox.current.fillColor ? selectBox.current.fillColor : selectBox.current.color)
                $('#i-color-text').text(selectBox.current.fillColor ? selectBox.current.fillColor : selectBox.current.color)
            }
            else {
                selectBox.current = null
                $('#i-x').val('')
                $('#i-y').val('')
                $('#i-w').val('')
                $('#i-h').val('')
            }
        }

        $(window).keydown(e => {
            ctrlKey.current = e.ctrlKey
            if (e.key === 'Delete' && selectBox.current && tool.current === 'vector') {
                history.current = history.current.filter((val, index) => index !== selectBox.current.id)
                selectBox.current = null
                hoverBox.current = null
                $('#i-x').val('')
                $('#i-y').val('')
                $('#i-w').val('')
                $('#i-h').val('')
                $('#i-color').val('')
                $('#i-color-text').text('')
                console.log('Deleted!')
            } else if (e.ctrlKey && tool.current === 'vector') {
                // Duplicate
                console.log(e.key)
                if (e.key === 'd' && selectBox.current) {
                    e.preventDefault()
                    console.log('Duplicate!', history.current[selectBox.current.id])
                    history.current = [
                        ...history.current,
                        selectBox.current
                    ]
                    console.log(history.current.length)
                } else if (e.key === 'c' && selectBox.current) {
                    e.preventDefault()
                    if (!navigator.clipboard) {
                        console.log('Does not support copy!')
                        return
                    }
                    navigator.clipboard.writeText(JSON.stringify(selectBox.current))
                        .then(() => {
                            console.log('Success!')
                        }, () => {
                            console.log('Error!')
                        })
                } else if (e.key === 'v') {
                    if (!navigator.clipboard) {
                        console.log('Does not support copy!')
                        return
                    }
                    navigator.clipboard.readText()
                        .then((val) => {
                            if (JSON.parse(val) && JSON.parse(val).type) {
                                history.current = [
                                    ...history.current,
                                    JSON.parse(val)
                                ]
                            }
                        }, () => {
                            console.log('Error to get text!')
                        })
                }
            }
        })

        $(window).keyup(e => {
            if (e.key === 'Control') ctrlKey.current = false
        })

        $('#i-color').on('input', e => {
            if (selectBox.current && history.current[selectBox.current.id]) {
                history.current[selectBox.current.id].fillColor = $(e.target).val()
                $('#i-color-text').text($(e.target).val())
            }
        })

        canvas.addEventListener('wheel', checkScrollDirection);

        function checkScrollDirection(event) {
            if (checkScrollDirectionIsUp(event)) {
                zoomSize.current += .05
                zoomSize.current = Math.min(5, zoomSize.current)
            } else {
                zoomSize.current -= .05
                zoomSize.current = Math.max(.1, zoomSize.current)
            }
            $('#zoom-value').text(Math.floor(zoomSize.current * 100) + '%')
        }

        function checkScrollDirectionIsUp(event) {
            if (event.wheelDelta) {
                return event.wheelDelta > 0;
            }
            return event.deltaY < 0;
        }

        const callibratePosition = ([x, y], offSet = true) => {
            return [callibrateByZoom(x + (offSet === true ? handOffset.current[0] : 0)), callibrateByZoom(y + (offSet === true ? handOffset.current[1] : 0))]
        }

        const callibrateByZoom = (value) => {
            return value * zoomSize.current
        }

        const clickItem = (item, position) => {
            /**
             *  | --- |
             *  |     |
             *  | --- |
             */
            var [x1, y1] = callibratePosition([item.x, item.y]);
            var x2 = x1 + callibrateByZoom(item.width < 0 ? 0 : item.width);
            var y2 = y1 + callibrateByZoom(item.height < 0 ? 0 : item.height);
            if (item.width < 0) [x1, y1] = [callibratePosition([item.x + item.width, 0])[0], y1]
            if (item.height < 0) [x1, y1] = [x1, callibratePosition([0, item.y + item.height])[1]]
            if ((position[0] > x1 && position[0] < x2) && (position[1] > y1 && position[1] < y2)) {
                return true
            }
            return false
        }

        const drawItem = (item) => {
            var [x, y] = callibratePosition([item.x, item.y])
            var cWidth = callibrateByZoom(item.width)
            var cHeight = callibrateByZoom(item.height)
            switch (item.type) {
                case "frame":
                    ctx.fillStyle = item.fillColor
                    ctx.fillRect(x, y, cWidth, cHeight)
                    break;
                case "rect":
                    if (item.style === 'fill') {
                        ctx.fillStyle = item.fillColor
                        ctx.fillRect(x, y, cWidth, cHeight)
                    } else if (item.style === 'stroke') {
                        ctx.strokeStyle = item.strokeColor
                        ctx.strokeRect(x, y, cWidth, cHeight)
                    } else if (item.style === 'both') {
                        ctx.fillStyle = item.fillColor;
                        ctx.fillRect(x, y, cWidth, cHeight);
                        ctx.strokeStyle = item.strokeColor;
                        ctx.lineWidth = 1;
                        ctx.strokeRect(x, y, cWidth, cHeight);
                    }
                    break;
                case "pen":
                    ctx.beginPath()
                    if (item.style === 'fill') {
                        ctx.fillStyle = item.color
                        ctx.arc(x, y, item.radius, 0, 2 * Math.PI)
                        ctx.fill()
                    } else if (item.style === 'stroke') {
                        ctx.strokeStyle = item.color
                        ctx.arc(x, y, item.radius, 0, 2 * Math.PI)
                        ctx.stroke()
                    } else if (item.style === 'both') {
                        ctx.strokeStyle = item.strokeColor
                        ctx.fillStyle = item.fillColor
                        ctx.arc(x, y, item.radius, 0, 2 * Math.PI)
                        ctx.stroke()
                        ctx.fill()
                    }
                    break;
            }
        }

        const tik = () => {
            ctx.clearRect(0, 0, width, height)
            var IDENTIFIER = 0
            for (var item of history.current) {
                drawItem(item)
                if (selectBox.current && selectBox.current.id === IDENTIFIER) {
                    ctx.strokeStyle = selectBox.current.strokeColor
                    const [sx, sy] = callibratePosition([selectBox.current.x, selectBox.current.y])
                    ctx.strokeRect(sx, sy, callibrateByZoom(selectBox.current.width), callibrateByZoom(selectBox.current.height))
                } else if (hoverBox.current && hoverBox.current.id === IDENTIFIER) {
                    ctx.strokeStyle = hoverBox.current.strokeColor
                    const [sx, sy] = callibratePosition([hoverBox.current.x, hoverBox.current.y])
                    ctx.strokeRect(sx, sy, callibrateByZoom(hoverBox.current.width), callibrateByZoom(hoverBox.current.height))
                }
                IDENTIFIER++
            }
            if (tool.current === 'rect' && isHolding.current) {
                mouseEndPosition = [mousePosition.current[0], mousePosition.current[1]]
                ctx.fillStyle = '#ccc'
                ctx.fillRect(mouseClickPosition[0], mouseClickPosition[1], mousePosition.current[0] - mouseClickPosition[0], mousePosition.current[1] - mouseClickPosition[1])
            }
            requestAnimationFrame(tik)
        }

        requestAnimationFrame(tik)

        $('#frame').on('click', e => {
            e.preventDefault()
            appendHistory({
                type: 'frame',
                x: currentFramePosition.current,
                y: 25,
                width: 375,
                height: 812,
                fillColor: '#ffffff'
            })
            currentFramePosition.current += 400
        })
        $('#vector').on('click', e => { e.preventDefault(); $('#' + tool.current).css({ backgroundColor: 'rgba(0, 0, 0, 0)' }); tool.current = 'vector'; $(e.target).css({ backgroundColor: 'rgba(255, 255, 255, .1)' }) })
        $('#rect').on('click', e => { e.preventDefault(); $('#' + tool.current).css({ backgroundColor: 'rgba(0, 0, 0, 0)' }); tool.current = 'rect'; $(e.target).css({ backgroundColor: 'rgba(255, 255, 255, .1)' }) })
        $('#pen').on('click', e => { e.preventDefault(); $('#' + tool.current).css({ backgroundColor: 'rgba(0, 0, 0, 0)' }); tool.current = 'pen'; $(e.target).css({ backgroundColor: 'rgba(255, 255, 255, .1)' }) })
        $('#hand').on('click', e => { e.preventDefault(); $('#' + tool.current).css({ backgroundColor: 'rgba(0, 0, 0, 0)' }); tool.current = 'hand'; $(e.target).css({ backgroundColor: 'rgba(255, 255, 255, .1)' }) })
    }, [])

    useEffect(() => {
        var currentWelcomePage = 1
        var globalOffset = $('#welcome-list').offset().left
        const welcome_right = () => {
            if (!$('#welcome-page-' + (currentWelcomePage + 1)).offset()) return
            currentWelcomePage++
            if (currentWelcomePage === 3) $('#welcome-button').text('')
            var offset = $('#welcome-page-' + currentWelcomePage).offset().left - globalOffset
            if (!offset) return
            if (currentWelcomePage === 3) offset += offset
            $('#welcome-list').stop().animate({
                scrollLeft: offset
            }, 500, 'swing')
            $('#welcome-num-' + currentWelcomePage).css({
                backgroundColor: '#bbb'
            })
            $('#welcome-num-' + (currentWelcomePage - 1)).css({
                backgroundColor: '#ccc'
            })
        }

        const welcome_left = () => {
            if (!$('#welcome-page-' + (currentWelcomePage - 1)).offset()) return
            currentWelcomePage--
            if (currentWelcomePage !== 3) $('#welcome-button').text('')
            var offset = Math.abs($('#welcome-page-' + currentWelcomePage).offset().left - globalOffset)
            if (currentWelcomePage === 1) offset = 0
            console.log(offset, currentWelcomePage)
            $('#welcome-list').stop().animate({
                scrollLeft: offset
            }, 500, 'swing')
            $('#welcome-num-' + currentWelcomePage).css({
                backgroundColor: '#bbb'
            })
            $('#welcome-num-' + (currentWelcomePage + 1)).css({
                backgroundColor: '#ccc'
            })
        }

        $('#welcome-right').on('click', welcome_right);
        $('#welcome-left').on('click', welcome_left);
        $('#welcome-button').on('click', e => {
            e.preventDefault()
            if ($(e.target).text().trim() === '') welcome_right()
            else $('#welcome-modal').css({
                display: 'none'
            })
        })

        if (!state) History.goBack()

    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%',
        }}>
            {/* Welcome Modal */}
            <div style={{
                position: 'absolute',
                zIndex: 10,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                width: '100%',
                height: '100%',
                backgroundColor: 'rgba(20, 20, 20, .3)'
            }} id="welcome-modal">
                <div style={{
                    width: '70%',
                    height: '90%',
                    borderRadius: 10,
                    backgroundColor: '#fff',
                    boxShadow: '0 0 10px rgba(20, 20, 20, .3)',
                    padding: '20px 0',
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                    position: 'relative',
                    overflow: 'hidden'
                }}>
                    {/* Close Modal */}
                    <div style={{
                        position: 'absolute',
                        right: 20,
                        bottom: 20,
                        width: 150,
                        height: 40,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        border: '1px solid dodgerblue',
                        borderRadius: 5,
                        fontWeight: 500,
                        color: 'dodgerblue',
                        fontSize: 14,
                        cursor: 'pointer'
                    }} id="welcome-button">
                        
                    </div>
                    {/* Right Page Button */}
                    <div style={{
                        position: 'absolute',
                        right: 20,
                        top: 'calc(50% - 15px)',
                        cursor: 'pointer'
                    }} id="welcome-right">
                        <img style={{
                            filter: 'opacity(.2)'
                        }} src="/images/call/right-arrow.svg" width="30" height="30" />
                    </div>
                    {/* Left Page Button */}
                    <div style={{
                        position: 'absolute',
                        left: 20,
                        top: 'calc(50% - 15px)',
                        cursor: 'pointer',
                    }} id="welcome-left">
                        <img style={{
                            filter: 'opacity(.2)',
                            transform: 'rotate(180deg)'
                        }} src="/images/call/right-arrow.svg" width="30" height="30" />
                    </div>
                    <div style={{
                        width: '100%',
                        display: 'flex',
                        flexDirection: 'row',
                        overflowX: 'hidden'
                    }} id="welcome-list">
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            flexDirection: 'column',
                            width: '100%',
                            minWidth: '100%'
                        }} id="welcome-page-1">
                            <h2>
                                    !
                            </h2>
                            <h4 style={{
                                textAlign: 'center',
                                marginTop: 20
                            }}>
                                      ,          .PDF, .JPG,     .MC
                            </h4>
                            <img src="/images/edit/welcome.jpg" width="550" />
                        </div>
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            flexDirection: 'column',
                            width: '100%',
                            minWidth: '100%'
                        }} id="welcome-page-2">
                            <h2>
                                  !
                            </h2>
                            <h4 style={{
                                textAlign: 'center',
                                marginTop: 20
                            }}>
                                               !
                            </h4>
                            <img src="/images/edit/comfort.jpg" width="400" />
                        </div>
                        <div style={{
                            display: 'flex',
                            alignItems: 'center',
                            flexDirection: 'column',
                            width: '100%',
                            minWidth: '100%'
                        }} id="welcome-page-3">
                            <h2>
                                !
                            </h2>
                            <h4 style={{
                                textAlign: 'center',
                                marginTop: 20
                            }}>
                                               MindStore!
                            </h4>
                            <img src="/images/edit/functionality.jpg" width="550" />
                        </div>
                    </div>
                    {/* Pages */}
                    <div style={{
                        display: 'flex',
                        flexDirection: 'row',
                        alignItems: 'center',
                        position: 'absolute',
                        width: 45,
                        left: 'calc(50% - 22.5px)',
                        bottom: 20
                    }}>
                        <span style={{
                            width: 10,
                            height: 10,
                            borderRadius: 5,
                            backgroundColor: '#bbb',
                            marginRight: 5,
                        }} id="welcome-num-1" />
                        <span style={{
                            width: 10,
                            height: 10,
                            borderRadius: 5,
                            backgroundColor: '#ddd',
                            marginRight: 5,
                        }} id="welcome-num-2" />
                        <span style={{
                            width: 10,
                            height: 10,
                            borderRadius: 5,
                            backgroundColor: '#ddd',
                            marginRight: 5,
                        }} id="welcome-num-3" />
                    </div>
                </div>
            </div>
            {/* Left Side */}
            <div style={{
                width: 250,
                height: '100%',
                position: 'absolute',
                left: 0,
                top: 0,
                backgroundColor: '#fff',
                borderRight: '1px solid #ccc'
            }}>
                {/* Title */}
                <div style={{
                    width: '100%',
                    height: 30,
                    borderBottom: '1px solid #ccc',
                    display: 'flex',
                    alignItems: 'center',
                    padding: '0 5px',
                    fontSize: 14,
                    fontWeight: 600,
                    color: '#222'
                }}>
                    <label></label>
                </div>
            </div>
            {/* ToolBar */}
            <div style={{
                width: 'calc(100% - 500px)',
                height: 50,
                backgroundColor: '#333',
                boxShadow: '0 4px 4px rgba(20, 20, 20, .3)',
                position: 'absolute',
                left: 250,
                top: 0,
                zIndex: 5,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between'
            }}>
                <div style={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center'
                }}>
                    <span id="vector" style={{
                        width: 50,
                        height: 50,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        cursor: 'pointer',
                        transitionDuration: '.3s'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, .1)'
                        })
                    }} onMouseLeave={e => {
                        if (tool.current !== 'vector') $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, 0)'
                        })
                    }}>
                        <img style={{
                            pointerEvents: 'none',
                            userSelect: 'none',
                            marginRight: -10
                        }} src="/images/edit/cursor.svg" width="25" height="25" />
                    </span>
                    <span id="frame" style={{
                        width: 50,
                        height: 50,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        cursor: 'pointer',
                        transitionDuration: '.3s'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, .1)'
                        })
                    }} onMouseLeave={e => {
                        if (tool.current !== 'frame') $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, 0)'
                        })
                    }}>
                        <img style={{
                            pointerEvents: 'none',
                            userSelect: 'none'
                        }} src="/images/call/frame.svg" width="30" height="30" />
                    </span>
                    <span id="rect" style={{
                        width: 50,
                        height: 50,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        cursor: 'pointer',
                        transitionDuration: '.3s'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, .1)'
                        })
                    }} onMouseLeave={e => {
                        if (tool.current !== 'rect') $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, 0)'
                        })
                    }}>
                        <img style={{
                            pointerEvents: 'none',
                            userSelect: 'none'
                        }} src="/images/call/rectanglefff.svg" width="30" height="30" />
                    </span>
                    <span id="pen" style={{
                        width: 50,
                        height: 50,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        cursor: 'pointer',
                        transitionDuration: '.3s'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, .1)'
                        })
                    }} onMouseLeave={e => {
                        if (tool.current !== 'pen') $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, 0)'
                        })
                    }}>
                        <img style={{
                            pointerEvents: 'none',
                            userSelect: 'none'
                        }} src="/images/call/penfff.svg" width="25" height="25" />
                    </span>
                    <span id="hand" style={{
                        width: 50,
                        height: 50,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        cursor: 'pointer',
                        transitionDuration: '.3s'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, .1)'
                        })
                    }} onMouseLeave={e => {
                        if (tool.current !== 'hand') $(e.target).css({
                            backgroundColor: 'rgba(255, 255, 255, 0)'
                        })
                    }}>
                        <img style={{
                            pointerEvents: 'none',
                            userSelect: 'none'
                        }} src="/images/edit/hand.svg" width="30" height="30" />
                    </span>
                </div>
                <div style={{
                    width: 70,
                    height: 50,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center'
                }}>
                    <label style={{
                        color: '#fff',
                        fontWeight: 500,
                        fontSize: 19
                    }} id="zoom-value">100%</label>
                </div>
            </div>
            {/* Right Side */}
            <div style={{
                width: 250,
                height: '100%',
                position: 'absolute',
                right: 0,
                top: 0,
                backgroundColor: '#fff',
                borderLeft: '1px solid #ccc'
            }}>
                {/* Header */}
                <div style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    paddingRight: 50,
                    width: '100%',
                    height: 25,
                    fontSize: 14,
                    fontWeight: 500,
                    borderBottom: '1px solid #ccc'
                }}>
                    <label style={{
                        cursor: 'pointer',
                        textDecoration: 'underline'
                    }}></label>
                    <label style={{
                        cursor: 'pointer'
                    }}></label>
                </div>
                <div style={{
                    flex: 1,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center'
                }} id="information">
                    {/* Position */}
                    <div style={{
                        width: '80%',
                        padding: '20px 0',
                        borderBottom: '1px solid #ccc'
                    }}>
                        <div style={{
                            marginBottom: 20,
                            display: 'flex',
                            width: '100%',
                            justifyContent: 'space-evenly',
                            alignItems: 'center'
                        }}>
                            <h5 style={{
                                // color: '#ccc',
                                fontSize: 18
                            }}>X:</h5>
                            <input id="i-x" style={{
                                borderRadius: 5,
                                backgroundColor: '#fff',
                                border: '1px solid #ccc',
                                fontSize: 15,
                                width: 70,
                                height: 25,
                                outline: '0',
                                paddingLeft: 10
                            }} type="number" onChange={e => {
                                $(e.target).val($(e.target).val().slice(0, 20))
                            }} />

                            <h5 style={{
                                // color: '#ccc',
                                fontSize: 18
                            }}>Y:</h5>
                            <input id="i-y" style={{
                                borderRadius: 5,
                                backgroundColor: '#fff',
                                border: '1px solid #ccc',
                                fontSize: 15,
                                width: 70,
                                height: 25,
                                outline: '0',
                                paddingLeft: 10
                            }} type="number" onInput={e => {
                                $(e.target).val($(e.target).val().slice(0, 20))
                            }} />
                        </div>
                        <div style={{
                            display: 'flex',
                            width: '100%',
                            justifyContent: 'space-evenly',
                            alignItems: 'center'
                        }}>
                            <h5 style={{
                                // color: '#ccc',
                                fontSize: 18
                            }}>W:</h5>
                            <input id="i-w" style={{
                                borderRadius: 5,
                                backgroundColor: '#fff',
                                border: '1px solid #ccc',
                                fontSize: 15,
                                width: 70,
                                height: 25,
                                outline: '0',
                                paddingLeft: 10
                            }} type="number" onChange={e => {
                                $(e.target).val($(e.target).val().slice(0, 20))
                            }} />

                            <h5 style={{
                                // color: '#ccc',
                                fontSize: 18
                            }}>H:</h5>
                            <input id="i-h" style={{
                                borderRadius: 5,
                                backgroundColor: '#fff',
                                border: '1px solid #ccc',
                                fontSize: 15,
                                width: 70,
                                height: 25,
                                outline: '0',
                                paddingLeft: 10
                            }} type="number" onInput={e => {
                                $(e.target).val($(e.target).val().slice(0, 20))
                            }} />
                        </div>
                    </div>
                    {/* Color */}
                    <div style={{
                        width: '80%',
                        padding: '20px 0',
                        borderBottom: '1px solid #ccc'
                    }}>
                        <div style={{
                            display: 'flex',
                            width: '100%',
                            justifyContent: 'space-evenly',
                            alignItems: 'center'
                        }}>
                            <input id="i-color" type="color" />
                            <label id="i-color-text">Black</label>
                        </div>
                    </div>
                </div>
            </div>
            {/* Board */}
            <div style={{
                width: 'calc(100% - 500px)',
                height: 'calc(100% - 50px)',
                position: 'absolute',
                top: 50,
                left: 250
            }}>
                <canvas style={{
                    backgroundColor: '#f1f1f1'
                }} ref={canvasRef} />
            </div>
        </div>
    )
}

export default TaskEditor