import { BrowserRouter, Switch, Route } from 'react-router-dom'
import Divide from './pages/Games/Divide';
import MentalCount from './pages/Games/MentalCount';
import Multiply from './pages/Games/Multiply';
import Shulte from './pages/Games/Shulte';
import Login from './pages/Login';
import MentalGenerator from './pages/Games/MentalGenerator';
import NotFound404 from './pages/NotFound404';
import Room from './pages/Room';
import Student from './pages/Student';
import Teacher from './pages/Teacher';
import TaskEditor from './pages/Edit/TaskEditor';
import Application from './pages/Application';
import WelcomePage from './pages/Welcome';
import Courses from './pages/Courses';
import CoursesForTeacher from './pages/CoursesForTeacher';
import Compare from './pages/Courses';
import ByOwnNames from './pages/ByOwnNames';
import Cells from './pages/Games/Cells';

function App() {
  return (
    <BrowserRouter>
      <Switch>
        {/* <Route exact path='/' component={Main} /> */}
        <Route exact path='/' component={WelcomePage} />
        <Route exact path='/forteachers' component={CoursesForTeacher} />
        <Route exact path='/courses' component={Courses} />
        <Route exact path='/compare' component={Compare} />
        <Route exact path='/by-own-names' component={ByOwnNames} />
        <Route exact path='/login' component={Login} />
        <Route exact path='/student' component={Student} />
        <Route exact path='/teacher' component={Teacher} />
        <Route exact path='/new-application' component={Application} />
        <Route exact path='/games/mental-generator' component={MentalGenerator} />
        <Route exact path='/games/mental-count' component={MentalCount} />
        <Route exact path='/games/cells' component={Cells} />
        <Route exact path='/games/divide' component={Divide} />
        <Route exact path='/games/multiply' component={Multiply} />
        <Route exact path='/games/shulte' component={Shulte} />
        <Route exact path='/edit/task-editor' component={TaskEditor} />
        <Route exact path='/room/:id/:offer' component={Room} />
        <Route component={NotFound404} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
