import React, { useEffect, useState } from 'react'

import '../../../assets/styles/divide.css'

import $ from 'jquery'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faAngleRight, faAngleLeft, faCog, faVolumeMute, faVolumeUp } from '@fortawesome/free-solid-svg-icons'
import { useHistory } from 'react-router'

const Divide = () => {
    const History = useHistory()

    const [Signals, updateSignals] = useState(true)
    const [LeftSide, updateLeftSide] = useState(false)
    // const [leftSide, updateLeftSide] = useState(false)

    useEffect(() => {
        var expressions = []
        var isOpen = false;
        var signals = true;
        var isAbacus = false;
        var template = '';
        var isChoosed = false;
        var isSpeed = false;
        var isLeftBarOpened = false;
        var speed = 1.5;
        var students = 1;
        var first = []
        var second = []

        /*** ->ALL LOGIC<- */

        var EXPRESSION = ''
        var RIGHT = 0

        const CORRECT_SOUND = new Audio('/audio/line_open.mp3')
        const INCORRECT_SOUND = new Audio('/audio/incorrect.mp3')

        const Data = [
            {
                value: [1, 1], label: '',
            },
            {
                value: [2, 1], label: 'AB  C',
            },
            {
                value: [2, 2], label: 'AB  CD',
            },
            {
                value: [3, 1], label: 'ABC  D',
            },
            {
                value: [4, 1], label: 'ABCD  E',
            },
            {
                value: [3, 2], label: 'ABC  DE',
            },
            {
                value: [4, 2], label: 'ABCD  EF',
            },
        ]

        const play_sound = (sound) => {
            if (signals) sound.play()
        }

        const start_expression = () => {
            $('#expression').text(EXPRESSION)
        }

        const get_expression = () => {
            var TEMPLATE = template
            if (template === 'MIX' || template === '*') {
                var index = Math.floor(Math.random() * Data.length)
                TEMPLATE = Data[index].label
            }
            var first = null
            var second = null
            Object.values(Data).forEach(val => {
                if (val.label === TEMPLATE) {
                    first = String(val.value[0])
                    second = String(val.value[1])
                }
            })
            console.log(template, TEMPLATE, String(first), String(second))
            $.ajax({
                method: 'POST',
                url: 'https://app.mindclickon.com/divide',
                data: {
                    'first': first,
                    'second': second,
                    'remainder': 0
                },
                dataType: 'json',
                success: (resp) => {
                    console.log(resp)
                    if (!resp.status) {
                        alert(' !  !')
                    } else {
                        EXPRESSION = resp.expression.replace(/\s*\=\s*\d+/i, '').replace('/', '')
                        RIGHT = Number(resp.expression.split(' = ')[1].trim())
                        hide($('#loading'))
                        show($('#answer'))
                        $('#answer form input').focus()
                        start_expression()
                    }
                }
            })
            // EXPRESSION = `${'250'}  ${'5'}`
            // RIGHT = Number('250') / Number('5')
            // hide($('#loading'))
            // show($('#answer'))
            // $('#answer form input').focus()
            // start_expression()
        }

        const update_statistic = () => {
            $('#left-bar .content').empty()
            $('#completed').text(String(expressions.length))
            expressions.forEach(expression => {
                $('#left-bar .content').append(`
            <div><span>${expression.expression} = ${expression.your}</span><span>${expression.isRight ? `<label style="color: green;"></label>` :
                        `<label style="color: tomato;"></label>`}<label>${expression.right}</label></span></div>
        `)
            })
        }

        $('#ready').on('click', e => {
            e.preventDefault()
            var isRight = false
            if ($('#answer form input').val().trim().length <= 0) return;
            $('#answer-modal').css({
                top: 100,
            })
            setTimeout(() => {
                $('#answer-modal').css({
                    top: -200,
                })
            }, 1000)
            if (Number($('#answer form input').val()) === RIGHT) {
                $('#answer-modal h2').text('')
                $('#answer-modal h2').css({
                    color: 'green'
                })
                play_sound(CORRECT_SOUND)
                isRight = true
            } else {
                $('#answer-modal h2').text('')
                $('#answer-modal h2').css({
                    color: 'tomato'
                })
                play_sound(INCORRECT_SOUND)
            }
            init()
            expressions.push({
                expression: EXPRESSION,
                isRight: isRight,
                your: $('#answer form input').val().trim(),
                right: RIGHT
            })
            $('#answer form input').val('')
            update_statistic()
        })

        const hide = (obj) => {
            obj.css({
                display: 'none'
            })
        }

        const show = (obj) => {
            obj.css({
                display: 'block'
            })
        }

        const init = () => {
            init_graphic()
            setTimeout(get_expression, 1000);
        }

        const init_graphic = () => {
            show($('#loading'))
            hide($('#answer'))
        }

        $(window).ready(init_graphic);

        /*** ->ALL LOGIC<- */

        $('#left-button').on('click', e => {
            e.preventDefault()
            if (!isLeftBarOpened) {
                $('#left-bar').css({
                    left: 0,
                })
                setTimeout(() => {
                    updateLeftSide(true)
                }, 100)
                isLeftBarOpened = true
            } else {
                $('#left-bar').css({
                    left: -300,
                })
                setTimeout(() => {
                    updateLeftSide(false)
                }, 100)
                isLeftBarOpened = false
            }
        })

        $('#speed-switch').on('change', e => {
            e.preventDefault()
            if (isSpeed) {
                isSpeed = false
                $('#speed-pm').css({
                    opacity: .5
                })
                $('#m-speed').css({
                    cursor: 'default'
                })
                $('#p-speed').css({
                    cursor: 'default'
                })
            } else {
                isSpeed = true
                $('#speed-pm').css({
                    opacity: 1
                })
                $('#m-speed').css({
                    cursor: 'pointer'
                })
                $('#p-speed').css({
                    cursor: 'pointer'
                })
            }
        })

        const init_clicks = () => {
            $('.f-item').on('click', e => {
                e.preventDefault()
                console.log($(e.target).css('background-color'))
                var content = $(e.target).text().trim()
                if ($(e.target).css('background-color') === 'rgb(255, 99, 71)') {
                    first = first.filter(val => val !== content)
                    $(e.target).css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                } else {
                    first.push(content)
                    $(e.target).css({
                        background: 'tomato'
                    })
                }
                if (content === '*') {
                    first = ['*']
                    $('.f-item').each((index, el) => {
                        if ($(el).text().trim() !== '*')
                            $(el).css({
                                background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                            })
                    })
                } else {
                    $('#mix-f').css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                }
            })
            $('.s-item').on('click', e => {
                e.preventDefault()
                var content = $(e.target).text().trim()
                if ($(e.target).css('background-color') === 'rgb(255, 99, 71)') {
                    second = second.filter(val => val !== content)
                    $(e.target).css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                } else {
                    second.push(content)
                    $(e.target).css({
                        background: 'tomato'
                    })
                }
                if (content === '*') {
                    second = ['*']
                    $('.s-item').each((index, el) => {
                        if ($(el).text().trim() !== '*')
                            $(el).css({
                                background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                            })
                    })
                } else {
                    $('#mix-s').css({
                        background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                    })
                }
            })
        }

        const switch_template = () => {
            $('#hidden .first').empty()
            $('#hidden .second').empty()
            if (isChoosed && template !== '*') {
                $('#hidden').css({
                    display: 'block'
                })
                init_clicks()
            } else {
                $('#hidden').css({
                    display: 'none'
                })
            }
        }

        $('.item').on('click', e => {
            $('.item').each((index, val) => {
                $(val).css({
                    background: 'linear-gradient(to right, #00BA98, #01d1ab)'
                })
            })
            $(e.target).css({
                background: 'tomato'
            })
            template = $(e.target).text().trim().replace('MIX', '*')
            if (!isChoosed) isChoosed = true
            switch_template()
        })

        $('#close-btn').on('click', e => {
            e.preventDefault()
            $('#side-bar').css({
                right: -500,
            })
            isOpen = false
        })

        $('#save-btn').on('click', e => {
            e.preventDefault()
            init()
        })

        $('#settings-btn').on('click', (e) => {
            e.preventDefault();
            $('#side-bar').css({
                right: 0,
            })
            isOpen = true
        })

        $('#click-c').on('click', (e) => {
            e.preventDefault();
            if (isOpen)
                $('#side-bar').css({
                    right: -500,
                });
            isOpen = false;
        })

        /* Speed */
        $('#m-speed').on('click', e => {
            e.preventDefault()
            if (!isSpeed) return;
            if (speed > .1) {
                speed = (speed * 10 - 1) / 10;
                $('#speed').text(String(speed))
            }
        })

        $('#p-speed').on('click', e => {
            e.preventDefault()
            if (!isSpeed) return;
            if (speed < 3.0) {
                speed = (speed * 10 + 1) / 10;
                $('#speed').text(String(speed))
            }
        })
        /* Speed */

        /* Students */
        $('#m-students').on('click', e => {
            if (students > 1) {
                students--;
                $('#students').text(String(students))
            }
        })

        $('#p-students').on('click', e => {
            if (students < 10) {
                students++;
                $('#students').text(String(students))
            }
        })
        /* Students */

        $('#signal-e').on('click', e => {
            e.preventDefault()
            if (signals) {
                signals = false;
                updateSignals(false)
            } else {
                signals = true;
                updateSignals(true)
            }
        })

        $('#abacus-e').on('click', e => {
            e.preventDefault()
            if (isAbacus) {
                document.querySelector('#abacus-e img').src = "/images/abacus-off.png"
                $('#abacus-graphic').css({
                    opacity: 0
                })
                isAbacus = false;
            } else {
                document.querySelector('#abacus-e img').src = "/images/abacus.svg"
                $('#abacus-graphic').css({
                    opacity: 1
                })
                isAbacus = true;
            }
        })
    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%'
        }}>
            <div className="divide-header">
                <div onClick={e => {
                    e.preventDefault()
                    History.goBack()
                }} className="back-item">
                    <img src="/images/left-arrow.svg" width="30" height="30" />
                </div>
                <div className="status-text">
                    <label id="completed">0</label>
                    <label>completed</label>
                </div>
                <div className="action-icons">
                    <div id="abacus-e" className="abacus-item">
                        <img src="/images/abacus-off.png" width="30" height="30" />
                    </div>
                    <div id="signal-e" className="signal-item">
                        <FontAwesomeIcon icon={Signals ? faVolumeUp : faVolumeMute} />
                    </div>
                    <div id="settings-btn" className="settings-item">
                        <FontAwesomeIcon icon={faCog} />
                    </div>
                </div>
            </div>

            <div id="click-c" className="container-mental">
                <img id="abacus-graphic" className="abacus-graphic" src="/images/abacus-graphic.png" width="300" style={{
                    opacity: 0,
                    position: 'absolute',
                    left: 100,
                    bottom: 100
                }} />
                <div id="loading" className="loading">
                    <div className="loader"> ...</div>
                </div>
                <div style={{
                    backgroundColor: '#fff'
                }} id="answer" className="answer">
                    <form>
                        <h3 id="expression"></h3>
                        <input type="number" placeholder="..." pattern="\d+" />
                        <button id="ready">
                            
                        </button>
                    </form>
                </div>
                <div id="answer-modal" style={{
                    width: 300,
                    height: 75,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    borderRadius: 10,
                    backgroundColor: '#fff',
                    boxShadow: '0 0 5px #ccc',
                    position: 'absolute',
                    top: -200,
                    left: 'calc(50% - 150)',
                    zIndex: 5,
                    transitionDuration: '.3s'
                }}>

                    <h2></h2>

                </div>
            </div>

            <div id="left-bar" className="left-side-divide">
                <div className="content">
                    <h3 style={{
                        color: '#ccc',
                        textAlign: 'center'
                    }}>
                           ,   !
                    </h3>
                </div>
                <div id="left-button" className="button">
                    <FontAwesomeIcon icon={LeftSide ? faAngleLeft : faAngleRight} />
                </div>
            </div>

            <div id="side-bar" className="side-bar-mental">
                <div className="side-bar-header">
                    <label></label>
                    <div id="close-btn" className="close-btn">
                        &times;
                    </div>
                </div>
                <div className="container-side-bar">
                    <div className="more-item">
                        <h4> :</h4>
                        <div className="items">
                            <div className="item">
                                AB  C
                            </div>
                            <div className="item">
                                ABC  D
                            </div>
                            <div className="item">
                                ABCD  E
                            </div>
                            <div className="item">
                                AB  CD
                            </div>
                            <div className="item">
                                ABC  DE
                            </div>
                            <div className="item">
                                ABCD  EF
                            </div>
                            <div className="item">
                                MIX
                            </div>
                        </div>
                    </div>
                    <div className="pm-item">
                        <label className="title">
                            - :
                        </label>
                        <div className="pm">
                            <div id="m-students" className="minus">
                                <img src="/images/minus.svg" width="20" height="20" />
                            </div>
                            <div className="content">
                                <label id="students">
                                    1
                                </label>
                            </div>
                            <div id="p-students" className="plus">
                                <img src="/images/add.svg" width="20" height="20" />
                            </div>
                        </div>
                        <small style={{
                            color: '#ccc'
                        }}>: 6</small>
                    </div>
                    <div id="save-btn" className="save-btn">
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Divide