import React from 'react';
import ColorfulButton from './Button';

const LessonEndModal = ({ modalShown = false, disableModal = () => {}, onClose = () => {},
    time,
    studentCount,
    allCoins
}) => {
    return (
        <div style={{
            zIndex: 100,
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(40, 40, 40, .1)',
            display: modalShown ? 'flex' : 'none',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
            <div style={{
                width: 400,
                height: 350,
                borderRadius: 10,
                backgroundColor: 'white',
                boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                padding: 20
            }}>
                <h4 style={{
                    marginBottom: 20
                }}> :</h4>
                <h5 style={{
                    marginBottom: 10,
                    color: '#ccc'
                }}>  : {time}</h5>
                <h5 style={{
                    marginBottom: 10,
                    color: '#ccc'
                }}> : {allCoins}</h5>
                <h5 style={{
                    marginBottom: 100,
                    color: '#ccc'
                }}>.- : {studentCount}</h5>
                <ColorfulButton
                    text={""}
                    type={'error'}
                    clearMargin={true}
                    onClick={() => {
                        disableModal()
                        onClose()
                    }}
                />
            </div>
        </div>
    )
};

export default LessonEndModal;