import React from "react";

import $ from 'jquery';
import { useHistory } from "react-router";

const SideBar = ({ state }) => {
    const History = useHistory();

    return (
        <div className="side-bar">
            <div className="translate title">
                MENU
            </div>
            <ul className="level1-ul">
                <li className="block-li">
                    <div id="info-header" className="header">
                        <span>
                            <img src="/images/teacher/info.svg" className="opacity" width="20" />
                            <span className="translate">
                                Information
                            </span>
                        </span>
                        <img src="/images/teacher/arrow-down.svg" width="15" className="opacity" />
                    </div>
                    <div id="info-block" className="block">
                        <ul>
                            <li id="analytics-trigger" className="translate block-item selected">Analytics</li>
                            <li id="profile-trigger" className="translate block-item">Profile</li>
                            <li id="groups-trigger" className="translate block-item">My Groups</li>
                        </ul>
                    </div>
                </li>
                <li className="block-li">
                    <div id="games-header" className="header">
                        <span>
                            <img src="/images/teacher/online-course.svg" className="opacity" width="20" />
                            <span className="translate">
                                Games
                            </span>
                        </span>
                        <img src="/images/teacher/arrow-down.svg" width="15" className="opacity rotate180" />
                    </div>
                    <div id="games-block" style={{
                        display: 'none'
                    }} className="block">
                        <ul>
                            <li onClick={() => {
                                History.push('/games/mental-count', {
                                    id: state.id,
                                    role: 'teacher'
                                })
                            }} className="translate block-item">Mental Count</li>
                            <li onClick={() => {
                                History.push('/games/cells', {
                                    id: state.id,
                                    role: 'teacher'
                                })
                                // $('#coming-soon-modal').css({
                                //     display: 'flex'
                                // })
                            }} className="translate block-item">Cells</li>
                            <li onClick={() => {
                                History.push('/games/multiply')
                            }} className="translate block-item">
                                Multiply</li>
                            <li onClick={() => {
                                History.push('/games/divide')
                            }} className="translate block-item">
                                Divide</li>
                            <li onClick={() => {
                                History.push('/games/shulte')
                            }} className="translate block-item">
                                Shulte Tables</li>
                            <li onClick={() => {
                                History.push('/games/mental-generator')
                            }} className="translate block-item">
                                Mental Generator</li>
                        </ul>
                    </div>
                </li>
                <li className="block-li">
                    <div id="calls-header" className="header">
                        <span>
                            <img src="/images/teacher/phone-call.svg" className="opacity" width="20" />
                            <span className="translate">
                                Conferences
                            </span>
                        </span>
                        <img src="/images/teacher/arrow-down.svg" width="15" className="opacity rotate180" />
                    </div>
                    <div id="calls-block" style={{
                        display: 'none'
                    }} className="block">
                        <ul>
                            <li id="calls-trigger" className="translate block-item">Calls</li>
                        </ul>
                    </div>
                </li>
                <li className="block-li">
                    <div id="edit-header" className="header">
                        <span>
                            <img src="/images/edit/apps-outline.svg" className="opacity" width="20" />
                            <span className="translate">
                                Tasks
                            </span>
                        </span>
                        <img src="/images/teacher/arrow-down.svg" width="15" className="opacity rotate180" />
                    </div>
                    <div id="edit-block" style={{
                        display: 'none'
                    }} className="block">
                        <ul>
                            <li id="homework-trigger" className="translate block-item">Homework</li>
                        </ul>
                    </div>
                </li>
                <li className="block-li">
                    <div id="crm-header" className="header">
                        <span>
                            <img src="/images/edit/apps-outline.svg" className="opacity" width="20" />
                            <span className="translate">
                                CRM-SYSTEM
                            </span>
                        </span>
                        <img src="/images/teacher/arrow-down.svg" width="15" className="opacity rotate180" />
                    </div>
                    <div id="crm-block" style={{
                        display: 'none'
                    }} className="block">
                        <ul>
                            <li id="applications-trigger" className="translate block-item">Requests</li>
                            <li id="success-diary-trigger" className="translate block-item">Success Diary</li>
                            <li id="lesson-trigger" className="translate block-item">Lesson</li>
                        </ul>
                    </div>
                </li>
            </ul>
            <div className="translate title">
                CHATS
            </div>
            <ul className="level1-ul">
                <li className="single">
                    <img className="opacity" src="/images/teacher/whatsapp.svg" width="20" alt="whatsapp-icon" />
                    WhatsApp
                </li>
                <li onClick={e => {
                    e.preventDefault()
                    window.location = 'https://t.me/joinchat/RRhL_fkXRvthMTli'
                }} className="single">
                    <img className="opacity" src="/images/teacher/telegram.svg" width="20" alt="telegram-icon" />
                    Telegram
                </li>
            </ul>
        </div>
    );
}

export default SideBar;