import React, { useEffect, useRef, useState } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleLeft, faCog, faUser } from '@fortawesome/free-solid-svg-icons'

import $ from 'jquery'
import { useHistory } from 'react-router'

const GOOD_WORDS = [
    '!',
    '!',
    '!',
    '!',
]

const Shulte = () => {
    const History = useHistory()

    const curNum = useRef("1")
    const completed = useRef("0")

    const seconds = useRef("0")
    const interval = useRef(null)

    const [animations, setAnimations] = useState(true)

    const generateTable = () => {
        if (interval.current !== null) clearInterval(interval.current)
        seconds.current = '0'
        curNum.current = '1'
        $('#curnum').text(curNum.current)
        interval.current = setInterval(() => {
            seconds.current = String(Number(seconds.current) + 1)
            $('#seconds').text(seconds.current)
        }, 1000)
        const size = Number($('#size').val())
        var NUMBERS = []
        const random = []
        for (var i = 0; i < size * size; i++) {
            NUMBERS.push(i + 1)
        }
        $('#table').empty()
        for (var i = 0; i < size * size; i++) {
            const index = Math.floor(Math.random() * NUMBERS.length)
            random.push(NUMBERS[index])
            const item = $(`
            <div style="font-size: 20px; font-weight: 500; width: ${100 / size}%; height: ${100 / size}%; border: 2px solid dodgerblue; display: flex; align-items: center; justify-content: center; cursor: pointer">
                ${Number(NUMBERS[index])}
            </div>
            `)
            if (animations) {
                item.css({
                    transitionDuration: '.2s'
                })
                item.hover(e => {
                    e.preventDefault()
                    item.css({
                        fontSize: 23
                    })
                }, e => {
                    e.preventDefault()
                    item.css({
                        fontSize: 19
                    })
                })
            }
            item.on('click', e => {
                e.preventDefault()
                var color = 'tomato'
                console.log($(e.target).text().trim(), Number(curNum.current.trim()))
                if ($(e.target).text().trim() === curNum.current.trim()) {
                    color = 'lime'
                    curNum.current = String(Number(curNum.current.trim()) + 1)
                    $('#curnum').text(curNum.current)
                }
                if (Number(curNum.current) > size * size) {
                    completed.current = String(Number(completed.current) + 1)
                    $('#completed').text(completed.current)
                    curNum.current = '1'
                    $('#curnum').text(curNum.current)
                    $('#good-word').text(GOOD_WORDS[Math.floor(Math.random() * GOOD_WORDS.length)])
                    $('#top-item').css({
                        top: 50
                    })
                    $('#time').text(': ' + seconds.current + ' ')
                    setTimeout(() => {
                        $('#top-item').css({
                            top: -75
                        })
                    }, 1000)
                    generateTable()
                }
                $(e.target).css({
                    color: color
                })
                setTimeout(() => $(e.target).css({
                    color: '#000'
                }), 500)
            })
            $('#table').append(item)
            NUMBERS = NUMBERS.filter((val, i) => i !== index)
        }
    }

    useEffect(() => {
        $('#click-c').on('click', e => {
            e.preventDefault()
            $('#sidebar').css({
                right: -400
            })
        })
        generateTable()
        $('#settings-btn').on('click', e => {
            e.preventDefault()
            $('#sidebar').css({
                right: 0
            })
        })
        $('#settings-btn').hover((e) => {
            e.preventDefault()
            e.target.style.fontSize = 30 + 'px'
        }, (e) => {
            e.preventDefault()
            e.target.style.fontSize = 25 + 'px'
        })
    }, [])

    return (
        <div style={{
            width: '100%',
            height: '100%',
        }}>
            {/* Header */}
            <div style={{
                width: '100%',
                height: 75,
                position: 'absolute',
                left: 0,
                top: 0,
                background: 'linear-gradient(to left, #CBF25B, #00BA98)',
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
                boxShadow: '0 3px 4px #ccc'
            }}>
                {/* Back */}
                <div style={{
                    padding: '0 15px',
                    fontSize: 25,
                    color: '#fff',
                    cursor: 'pointer'
                }} onClick={e => {
                    e.preventDefault()
                    History.goBack()
                    if (interval.current !== null) clearInterval(interval.current)
                    seconds.current = '0'
                    $('#seconds').text(seconds.current)
                    completed.current = '0'
                    $('#completed').text(completed.current)
                    curNum.current = '1'
                    $('#curnum').text(curNum.current)
                    $('#time').text(': ' + seconds.current + ' ')
                }}>
                    <FontAwesomeIcon icon={faAngleLeft} />
                </div>
                {/* Center Bar */}
                <div style={{
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    {/* Ended Of All */}
                    <div style={{
                        marginRight: 20,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        color: '#fff',
                        fontSize: 16
                    }}>
                        <label id="completed">
                            {completed.current}
                        </label>
                        <small style={{
                            color: '#fff',
                            fontWeight: 'bold'
                        }}>
                            completed
                        </small>
                    </div>
                    {/* Time */}
                    <div style={{
                        marginRight: 20,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        color: '#fff',
                        fontSize: 16
                    }}>
                        <label id="seconds">
                            {seconds.current}
                        </label>
                        <small style={{
                            color: '#fff',
                            fontWeight: 'bold'
                        }}>
                            seconds
                        </small>
                    </div>
                    {/* Current Number */}
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        color: '#fff',
                        fontSize: 16
                    }}>
                        <label id="curnum">
                            {curNum.current}
                        </label>
                        <small style={{
                            color: '#fff',
                            fontWeight: 'bold'
                        }}>
                            number
                        </small>
                    </div>
                </div>
                {/* Actions */}
                <div id="settings-btn" style={{
                    padding: '0 15px',
                    fontSize: 25,
                    color: '#fff',
                    cursor: 'pointer',
                    transitionDuration: '.3s'
                }}>
                    <FontAwesomeIcon icon={faUser} />
                </div>
            </div>
            {/* Container */}
            <div id="click-c" style={{
                width: '100%',
                height: 'calc(100% - 75px)',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                position: 'absolute',
                top: 75,
                left: 0
            }}>
                {/* Center Container */}
                <div style={{
                    width: '60%',
                    height: '90%',
                    backgroundColor: '#fff',
                    boxShadow: '0 0 5px #ccc',
                    borderRadius: 10,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    borderRadius: 10
                }}>
                    {/* Shulte Table */}
                    <div style={{
                        width: 400,
                        height: 400,
                        display: 'flex',
                        flexDirection: 'row',
                        flexWrap: 'wrap',
                    }} id='table'>
                        <div style={{
                            width: '20%',
                            height: '20%',
                            border: '2px solid dodgerblue',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            cursor: 'pointer',
                            fontSize: 19
                        }}>
                            34
                        </div>
                    </div>
                </div>
            </div>
            {/* Side Bar */}
            <div id="sidebar" style={{
                width: 400,
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                position: 'absolute',
                right: 0,
                top: 0,
                backgroundColor: '#fff',
                transitionDuration: '.3s'
            }}>
                {/* Header */}
                <div style={{
                    width: '100%',
                    height: 75,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    color: '#fff',
                    fontSize: 19,
                    fontWeight: 500,
                    background: 'linear-gradient(to right, #CBF25B, #00BA98)'
                }}>
                    
                </div>
                {/* Container */}
                <div style={{
                    width: '100%',
                    height: 'calc(100% - 75px)',
                    minHeight: 'calc(100% - 75px)',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    paddingTop: 20,
                    boxShadow: '-5px 0 5px #ccc'
                }}>
                    {/* Select Size */}
                    <div style={{ width: '80%' }}>
                        <label> :</label>
                        <select id="size" style={{
                            width: '100%',
                            height: 50,
                            borderRadius: 5,
                            border: '1px solid #ccc',
                            backgroundColor: '#fff',
                            padding: '0 10px',
                            outline: '0',
                            boxShadow: '0 0 4px #ccc',
                            marginTop: 10
                        }}>
                            <option value={'3'}>3  3</option>
                            <option value={'4'}>4  4</option>
                            <option value={'5'}>5  5</option>
                            <option value={'6'}>6  6</option>
                        </select>
                    </div>
                    {/* Animations Checkbox */}
                    <div style={{
                        width: '80%',
                        marginTop: 10
                    }}>
                        <input type="checkbox" style={{
                            marginRight: 10
                        }} onChange={e => {
                            setAnimations($(e.target).prop('checked'))
                        }} checked />
                        
                    </div>
                    {/* Ready Button */}
                    <div style={{
                        width: '80%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        color: '#fff',
                        fontSize: 17,
                        fontWeight: 600,
                        backgroundColor: '#00BA98',
                        borderRadius: 5,
                        boxShadow: '0 0 5px #ccc',
                        marginTop: 10,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        generateTable()
                    }}>
                        
                    </div>
                </div>
            </div>
            {/* Top Item */}
            <div id="top-item" style={{
                width: 300,
                height: 75,
                backgroundColor: '#fff',
                borderRadius: 10,
                boxShadow: '0 0 5px #ccc',
                position: 'absolute',
                top: -75,
                left: 'calc(50% - 150px)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexDirection: 'column',
                fontSize: 20,
                color: 'green',
                transitionDuration: '.3s'
            }}>
                <label id="good-word"></label>
                <label id="time" style={{
                    color: '#ccc',
                    fontSize: 14
                }}></label>
            </div>
        </div>
    )
}

export default Shulte