import React from 'react';
import { useHistory } from 'react-router';

const Header = () => {
    const History = useHistory();

    return (
        <header className="header-teacher" style={{
            display: 'flex',
            alignItems: 'center'
        }}>
            <div onClick={e => {
                e.preventDefault()
                History.replace('/login')
            }} data-bottom="1" data-title=" " className="left">
                <img src="/images/mindclickon.svg" width="125"/>
            </div>
            <div className="right" style={{
                display: 'flex',
                alignItems: 'center'
            }}>
                <span>
                    <span id="language-button" className="gray circle">
                        <img src="/images/teacher/uk.svg" width="30" height="30" alt="england flag" />
                    </span>
                    <span data-bottom="1" data-title="Notifications" className="pink circle translate">
                        <img src="/images/teacher/bell.svg" width="17" height="17" alt="bell" />
                        <span className="new-notification"></span>
                    </span>
                </span>
                <span>
                    <img src="https://prikolist.club/wp-content/uploads/2019/07/Kartinki_kot_v_ochkah_1_29100843.jpg" width="50"
                        height="50" alt="profile-photo" className="profile data-uri" />
                    <span className="two-center">
                        <label className="data-name transliterate"></label>
                        <label id="data-role" className="translate"></label>
                    </span>
                </span>
                <span>
                    <img src="/images/teacher/menu.svg" width="30" height="30" alt="bars" />
                </span>
            </div>
        </header>
    )
}

export default Header;