import { useEffect, useRef } from 'react';
import socket from '../CRM/socket';
import ACTIONS from '../CRM/actions';

const useCRM = ({ name, id }, setApps) => {
    const userID = useRef('')

    useEffect(() => {
        socket.emit(ACTIONS.JOIN, {
            name: name,
            id: id,
            date: Date.now()
        })

        socket.on(ACTIONS.JOINED, ({ identifier, applications: apps }) => {
            console.log(apps)
            userID.current = identifier
            setApps(apps)
        })
    }, [])

    useEffect(() => {
        socket.on(ACTIONS.APPLICATION, (apps) => {
            console.log('New application', apps)
            setApps(apps)
        })
    }, [])

    useEffect(() => {
        socket.on(ACTIONS.TAKE_APPLICATION, (apps) => {
            console.log('Take application', apps)
            setApps(apps)
        })
    }, [])

    const takeApplication = (inviteID, appID) => {
        socket.emit(ACTIONS.TAKE_APPLICATION, {
            inviteID,
            id,
            appID
        })
    }

    const untakeApplication = (inviteID) => {
        socket.emit(ACTIONS.UNTAKE_APPLICATION, {
            inviteID,
            id,
        })
    }

    const talkedApplication = (inviteID) => {
        socket.emit(ACTIONS.TALKED_APPLICATION, {
            inviteID,
            id
        })
    }

    const endApplication = (inviteID) => {
        socket.emit(ACTIONS.END_APPLICATION, {
            inviteID,
            id
        })
    }

    return {
        userID: userID,
        takeApplication,
        untakeApplication,
        talkedApplication,
        endApplication
    }
}

export default useCRM