import React, { useEffect, useState } from "react";

import Ajax from "../../../lib/ajax";

const AnalyticsContainer = ({ state }) => {
    const [data, setData] = useState(null)

    useEffect(() => {
        Ajax({
            id: state.id
        }, '/teacher-rate.php',
        (resp) => {
            console.log(resp)
            setData(resp);
        },
        (err) => {
            alert(' !');
        })
    }, [])

    return (
        <div id="analytics-container" className="container-teacher">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/teacher/analytics.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">Analytics Dashboard</label>
                        <label className="translate">Analytics of your rating on the platform</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content">
                <div className="switch-buttons">
                    <div className="checked translate">As teacher</div>
                </div>
                <div className="portfolio-perfomance">
                    <div className="head translate">
                        Portfolio Perfomance
                    </div>
                    <div className="page-content">
                        <div className="stat-element">
                            <span style={{
                                backgroundColor: '#FFED00'
                            }} className="stat-icon">
                                <img src="/images/teacher/responsive.svg" width="40" height="40" />
                            </span>
                            <span className="stat-text">
                                <label className="stat-title translate">Rate Points</label>
                                <label className="stat-value">
                                    {
                                        data ? Number(data.rate) > 1000 ? data.rate.slice(0, data.rate.length - 3) + 'K' : data.rate : '...'
                                    }
                                </label>
                                <label className="stat-status">
                                    <label className="translate" style={{
                                        color: '#ccc'
                                    }}>
                                        :
                                        <label className="yellow">
                                        {
                                            ' ' + (data ? data.rate : '...')
                                        }
                                        </label>
                                    </label>
                                </label>
                            </span>
                        </div>
                        <div className="stat-element">
                            <span style={{
                                backgroundColor: '#E5097F'
                            }} className="stat-icon">
                                <img style={{
                                    marginBottom: -5
                                }} src="/images/teacher/mortarboard.svg" width="40" height="40" />
                            </span>
                            <span className="stat-text">
                                <label className="stat-title translate">Students Rate Points</label>
                                <label className="stat-value">
                                    {
                                        data ? data.studentsRate > 1000 ? String(data.studentsRate).slice(0, String(data.studentsRate).length - 3) + 'K' : data.studentsRate : '...'
                                    }
                                </label>
                                <label className="stat-status">
                                    <label className="gray-text translate">
                                        : 
                                        <label className="red">{' ' + (data ? data.studentsRate : '...')}</label>
                                    </label>
                                </label>
                            </span>
                        </div>
                        <div className="stat-element">
                            <span style={{
                                backgroundColor: '#3AAC53'
                            }} className="stat-icon">
                                <img src="/images/teacher/wallet.svg" width="40" height="40" />
                            </span>
                            <span className="stat-text">
                                <label className="stat-title translate">Capital Gains</label>
                                <label className="stat-value green">
                                    {data ? data.pay : '...'}
                                </label>
                                <label className="stat-status">
                                    <label className="gray-text translate"> :
                                    <label className="aqua">{' ' + (data ? data.nextPay ? data.nextPay : '' : '...')}</label>
                                    </label>
                                </label>
                            </span>
                        </div>
                    </div>
                    <div className="footer">
                        <div className="complete-report-button" style={{
                            width: 150,
                            userSelect: 'none'
                        }}>
                            <img style={{
                                marginLeft: 10,
                                marginRight: 10
                            }} src="/images/teacher/confirmation-white.svg" width="20" height="20" /><label
                               style={{
                                   flex: 1,
                                   cursor: 'pointer'
                               }} className="translate">Update</label>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AnalyticsContainer;