import React from "react";

const GroupsContainer = ( {setStudentMode, openModal} ) => {
    return (
        <div style={{
            display: 'none'
        }} className="container-teacher" id="groups-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/teacher/group.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">My Groups</label>
                        <label className="translate">My groups and my students</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content">
                <div className="all-students">
                    <div style={{
                        display: 'flex',
                        justifyContent: 'space-between'
                    }} className="head">
                        <label className="translate">All Students</label>
                        <div className="translate add-student" data-bottom="1" data-title="New student" style={{
                            width: 50,
                            height: 35,
                            borderRadius: 5,
                            boxShadow: '0 0 3px rgba(40, 40, 40, .4)',
                            backgroundColor: 'dodgerblue',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            cursor: 'pointer'
                        }} onClick={e => {
                            e.preventDefault()
                            setStudentMode('add')
                            openModal()
                        }}>
                            <img width="15" height="15" src="/images/add.svg" />
                        </div>
                    </div>
                    <div id="data-students" className="page-content" style={{
                        justifyContent: 'flex-start',
                        overflow: 'hidden',
                        overflowX: 'auto'
                    }}>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default GroupsContainer;