import React from 'react';

const getRandomColor = () => {
    return [
        '#12971091',
        '#15414978',
        '#53111288',
        '#227261',
        '#199075',
        '#1212108135',
        '#13146193',
        '#732533',
        'dodgerblue',
        '#ffc01e',
        '#9c1eff'
    ][Math.min(Math.floor(Math.random() * 11), 10)]
}

const StudentList = ({ students = [], selectStudent = (id) => {}, timeStr = "" }) => {
    return (
        <div style={{
            width: 350,
            height: 450,
            padding: 20,
            display: 'flex',
            flexDirection: 'column',
            overflowY: 'auto',
            borderRadius: 20,
            boxShadow: '0 0 5px rgba(40, 40, 40, .4)'
        }}>
            <h3 style={{
                marginBottom: 10
            }}>  {students.length} :</h3>
            <h5 style={{
                color: '#ccc'
            }}>{timeStr}</h5>
            <ul>
            {
                students.map((val, index) => {
                    const color = getRandomColor();
                    return (
                        <li style={{
                            display: 'flex',
                            flexDirection: 'row',
                            alignItems: 'center',     
                            justifyContent: 'space-between',
                            padding: 15,
                            width: '100%',
                            height: 40,
                            borderRadius: 10,
                            backgroundColor: color,
                            color: 'white',
                            marginTop: 10,
                            cursor: 'pointer'
                        }} key={index} title={color} onClick={e => {
                            e.preventDefault()
                            selectStudent(val.id)
                        }}>
                            <label>{val.name.length >= 17 ? val.name.slice(0, 16) + '...' : val.name}</label>
                            <img src={'images/teacher/user-white-filled.svg'} width={20} height={20} />
                        </li>
                    )
                })
            }
            </ul>
        </div>
    )
};

export default StudentList;