import React, { useState } from "react";

import $ from 'jquery'

import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import { styled } from '@mui/material/styles';
import { useHistory } from 'react-router';

const HtmlTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: 'purple',
      color: '#fff',
      maxWidth: '70%',
      margin: '0 auto',
      fontSize: theme.typography.pxToRem(15),
    },
  }));

const Login = () => {
    const History = useHistory()

    const [email, updateEmail] = useState('')
    const [password, updatePassword] = useState('')

    return (
        <div style={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            height: '100%',
            backgroundImage: 'url("/images/background.png")',
            backgroundSize: 'cover',
            flexDirection: 'column'
        }}>
            <div style={{
                width: '100%',
                height: 80,
            }}>
                <HtmlTooltip placement="right" title={"     ,        ,    ,     ."}>
                    <img src={"/images/mindclickon.svg"} style={{
                        height: '100%',
                        marginLeft: 20
                    }} onClick={e => {
                        e.preventDefault()
                        History.push("/");
                    }} />
                </HtmlTooltip>
            </div>
            <div style={{
                flex: 1,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
            }}>
                <form style={{
                    width: 400,
                    height: 300,
                    backgroundColor: '#fefefe',
                    borderRadius: 10,
                    boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                    justifyContent: 'space-evenly'
                }}>
                    <h2> </h2>
                    <input onChange={(e) => {
                        updateEmail(e.target.value)
                    }} type="email" placeholder="Email..." autoComplete={'username'} style={{
                        width: 300,
                        height: 50,
                        borderRadius: 5,
                        border: '1px solid #ccc',
                        paddingLeft: 5,
                        display: 'flex',
                        alignItems: 'center'
                    }} />
                    <input onChange={(e) => {
                        updatePassword(e.target.value)
                    }} type="password" placeholder="Password..." autoComplete={'current-password'} style={{
                        width: 300,
                        height: 50,
                        borderRadius: 5,
                        border: '1px solid #ccc',
                        paddingLeft: 5,
                        display: 'flex',
                        alignItems: 'center'
                    }} />
                    <button style={{
                        width: 300,
                        height: 50,
                        borderRadius: 5,
                        backgroundColor: 'dodgerblue',
                        color: '#fff',
                        fontSize: 13,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        border: '0',
                        outline: '0'
                    }} onClick={(e) => {
                        e.preventDefault()
                        $('#errorMess').text('...')
                        const success = (resp) => {
                            const data = resp.data
                            $('#errorMess').text('!')
                            console.log(data)
                            History.push({
                                pathname: '/' + data.role,
                                state: {
                                    ...data
                                }
                            })
                        }
                        const error = (err) => {
                            $('#errorMess').text(' !')
                            console.error(err)
                        }

                        $.ajax({
                            method: 'POST',
                            url: 'https://php.mindclickon.com/app/login.php',
                            success: success,
                            error: error,
                            dataType: 'json',
                            data: {
                                'email': email,
                                'password': password
                            }
                        })
                    }}>
                        
                    </button>
                    <button style={{
                        width: 300,
                        height: 50,
                        borderRadius: 5,
                        backgroundColor: 'orangered',
                        color: '#fff',
                        fontSize: 13,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        border: '0',
                        outline: '0'
                    }} onClick={(e) => {
                        e.preventDefault()
                        History.push('/new-application')
                    }}>
                         ?  !
                    </button>
                    <h5 id="errorMess">{''}</h5>
                    {/* <a style={{
                        fontSize: 12,
                        color: 'dodgerblue',
                    }} onClick={e => {
                        e.preventDefault()
                        History.push('/new-application')
                    }}> ?  !</a> */}
                </form>
            </div>
        </div>
    )
}

export default Login