import React from 'react';

import Toast from '../../../components/Toast';

const SuccessDiaryContainer = () => {
    return (
        <div style={{ display: 'none' }} className="container-teacher" id="success-diary-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/teacher/user.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">My Success Diary</label>
                        <label className="translate">A success diary where you can write the success history of your students.</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content">
                
            </div>
        </div>
    )
}

export default SuccessDiaryContainer;