import React from 'react';

import $ from 'jquery';

const ApplicationsContainer = ({application, applications, setAppData}) => {
    return (
        <div style={{
            display: 'none'
        }} className="container-teacher" id="applications-container">
            <div className="header-title">
                <div className="left">
                    <span className="icon">
                        <img src="/images/edit/construct-outline.svg" width="25" height="25" />
                    </span>
                    <span className="titles">
                        <label className="translate">Requests</label>
                        <label className="translate">Accept your requests!</label>
                    </span>
                </div>
                <div className="right">
                    <div data-bottom="1" data-title="Make the default" className="favorite-button translate">
                        <img src="/images/teacher/star.svg" width="15" height="15" />
                    </div>
                </div>
            </div>
            <div className="content" style={{
                padding: 10,
                margin: 0,
                width: '100%',
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'flex-start',
                justifyContent: 'flex-start',
            }}>
                {applications.length <= 0 ?
                    <div style={{
                        width: '100%',
                        height: '100%',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}>
                        <label style={{
                            fontSize: 19,
                            fontWeight: 600,
                            color: '#bbb',
                        }}>    !<br />  .</label>
                    </div> :
                    applications.map((app, index) => {
                        return (
                            <div key={index} style={{
                                width: 200,
                                height: 250,
                                backgroundColor: '#fff',
                                borderRadius: 10,
                                boxShadow: '0 0 5px rgba(20, 20, 20, .4)',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'space-evenly',
                                position: 'relative',
                                marginRight: 30
                            }}>
                                <div data-title={app.status === 'wait' ? '' : app.status === 'work' ? ' ' : ''} data-bottom="1" style={{
                                    width: 15,
                                    height: 15,
                                    borderRadius: '50%',
                                    backgroundColor: app.status === 'wait' ? '#00ef99' : app.status === 'work' ? 'yellow' : 'tomato',
                                    position: 'absolute',
                                    left: 15,
                                    top: 15,
                                    boxShadow: '0 0 3px rgba(20, 20, 20, .4)',
                                }}></div>
                                <img src="/images/edit/person-add-outline.svg" width="50" />
                                <div style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center'
                                }}>
                                    <h4 style={{
                                        fontSize: 16
                                    }}>{app.name} {app.surname ? app.surname : app.lastname}</h4>
                                    <div style={{
                                        marginTop: 10,
                                        width: 135,
                                        height: 35,
                                        backgroundColor: '#00ef99',
                                        boxShadow: '0 0 5px rgba(20, 20, 20, .4)',
                                        borderRadius: 5,
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        color: '#fff',
                                        fontWeight: 500,
                                        fontSize: 14,
                                        cursor: 'pointer',
                                        userSelect: 'none'
                                    }} id={`detail-${index}`} onClick={e => {
                                        e.preventDefault()
                                        console.log(e.target.id.replace('detail-', ''))
                                        application.current = {
                                            ...applications[Number(e.target.id.replace('detail-', ''))],
                                            id: Number(e.target.id.replace('detail-', ''))
                                        }
                                        setAppData();
                                        $('#application-modal').css({ display: 'flex' })
                                    }}></div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}

export default ApplicationsContainer;