import React, { useRef, useState } from 'react'

import $ from 'jquery'

import Loader from 'react-loader-spinner';
import Button from '../../components/Button'

const Application = () => {
    const [tariff, setTariff] = useState(1);
    const [ok, setOk] = useState(false);
    const [error, setError] = useState(false);

    const [code, setCode] = useState('')
    const secondsToNew = useRef(0)
    const interval = useRef(null)

    const updateTariff = (index) => {
        setTariff(index);
        $('#tariff-' + (index === 2 ? 1 : 2)).css({
            outline: '0'
        })
        $('#tariff-' + index).css({
            outline: '3px solid dodgerblue'
        })
    }

    return (
        <div style={{
            width: '100%',
            height: '100%',
            backgroundColor: '#fff',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            overflowY: 'auto'
        }}>
            <div style={{
                width: '100%',
                height: 70,
                backgroundColor: '#333',
                display: 'flex',
                alignItems: 'center',
                padding: '0 10px',
                justifyContent: 'space-between'
            }}>
                <a style={{
                    textDecoration: 'none',
                }} href="https://mindclickon.com">
                    <img src="/images/mindclickon.svg" height="60" />
                </a>
                <label style={{
                    color: '#fff',
                    fontWeight: 500,
                    fontSize: 25
                }}></label>
                <ul style={{
                    listStyle: 'none',
                    display: 'flex',
                    alignItems: 'center',
                    color: '#fff'
                }}>
                    <li style={{
                        marginRight: 20
                    }}><a style={{
                        textDecoration: 'none',
                        color: '#fff'
                    }} href="https://mindclickon.com"></a></li>
                    <li style={{
                        marginRight: 20
                    }}><a style={{
                        textDecoration: 'none',
                        color: '#fff'
                    }} href="/about"> </a></li>
                </ul>
            </div>
            <h2 style={{
                marginTop: 30
            }}>  :</h2>
            <hr color="#ccc" style={{
                width: '80%',
                marginTop: 40
            }} />
            <div style={{
                width: '60%',
                margin: '0 auto',
                display: 'flex',
                justifyContent: 'space-between',
                backgroundColor: '#fff',
                marginBottom: 30
            }}>
                <div style={{
                    width: 300,
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                    marginTop: 30,
                    overflow: 'hidden',
                    borderRadius: 10,
                    boxShadow: '0 0 10px #ccc',
                    padding: 20
                }}>
                    <img src="/images/edit/teaching.jpg" width="250" />
                    <h2 style={{
                        marginBottom: 15,
                        marginTop: 12.51
                    }}> </h2>
                    <p style={{
                        fontWeight: 500,
                        fontSize: 16,
                        textAlign: 'center'
                    }}>          .    .              ,      .</p>
                    <div style={{
                        height: 40,
                        width: 150,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        borderRadius: 5,
                        boxShadow: '0 0 10px #ccc',
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        marginTop: 15,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#tariff-menu').css({ display: 'flex' })
                        $('.tariff-block').css({ display: 'flex' })
                    }}>
                        
                    </div>
                </div>
                <div style={{
                    width: 300,
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                    overflow: 'hidden',
                    borderRadius: 10,
                    boxShadow: '0 0 10px #ccc',
                    padding: 20,
                    marginTop: 30
                }}>
                    <img src="/images/edit/alone.jpg" width="300" />
                    <h2 style={{
                        marginBottom: 15
                    }}></h2>
                    <p style={{
                        fontWeight: 500,
                        fontSize: 16,
                        textAlign: 'center'
                    }}>    ,      ,    .            .</p>
                    <div style={{
                        height: 40,
                        width: 150,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        borderRadius: 5,
                        boxShadow: '0 0 10px #ccc',
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        marginTop: 52.5,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        // $('#tariff-menu').css({ display: 'flex' })
                        // $('.tariff-block').css({ display: 'none' })
                        $('#coming-soon-modal').css({ display: 'flex' })
                    }}>
                        
                    </div>
                </div>
            </div>
            {/* Coming Soon Modal */}
            <div id="coming-soon-modal" style={{
                width: '100%',
                height: '100%',
                position: 'absolute',
                top: 0,
                left: 0,
                backgroundColor: 'rgba(0, 0, 0, .2)',
                display: 'none',
                justifyContent: 'center',
                alignItems: 'center',
                zIndex: 10
            }}>
                <div style={{
                    width: '45%',
                    height: '80%',
                    borderRadius: 10,
                    backgroundColor: '#FFF6D9',
                    boxShadow: '0 0 5px rgba(20, 20, 20, .4)',
                    display: 'flex',
                    alignItems: 'center',
                    paddingTop: 20,
                    flexDirection: 'column'
                }}>
                    <h2>...</h2>
                    <img src="/images/edit/coming-soon.jpg" width="500" />
                    <Button
                        text={''}
                        onClick={e => {
                            e.preventDefault()
                            $('#coming-soon-modal').css({
                                display: 'none'
                            })
                        }}
                    />
                </div>
            </div>
            <div style={{
                position: 'absolute',
                zIndex: 20,
                width: '90%',
                height: '95%',
                minHeight: '95%',
                maxHeight: '95%',
                overflowX: 'hidden',
                overflowY: 'auto',
                borderRadius: 10,
                left: '5%',
                top: '2.5%',
                backgroundColor: '#fff',
                boxShadow: '0 0 5px rgba(30, 30, 30, .4)',
                display: 'none',
                flexDirection: 'column',
                alignItems: 'center',
                padding: 20,
            }} id="tariff-menu">
                <h2 style={{
                    color: '#ddd',
                    fontWeight: 'bold',
                    fontSize: 35,
                    padding: 5,
                    alignSelf: 'flex-end',
                    cursor: 'pointer'
                }} onClick={e => {
                    e.preventDefault()
                    $('#tariff-menu').css({ display: 'none' })
                }}>&times;</h2>
                <h2 className="tariff-block"> :</h2>
                <div style={{
                    width: '100%',
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    flexWrap: 'wrap',
                    marginTop: 30
                }} className="tariff-block">
                    <div style={{
                        width: 200,
                        height: 300,
                        backgroundColor: '#fff',
                        borderRadius: 10,
                        boxShadow: '0 0 5px rgba(30, 30, 30, .4)',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'space-evenly',
                        padding: 20,
                        outline: '3px solid dodgerblue'
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            outline: '3px solid dodgerblue'
                        })
                    }} onMouseLeave={e => {
                        if (tariff !== 1) $(e.target).css({
                            outline: '0'
                        })
                    }} id="tariff-1" onClick={e => {
                        e.preventDefault()
                        updateTariff(1)
                    }}>
                        <img src="/images/edit/disconnect.jpg" width="150" style={{
                            pointerEvents: 'none'
                        }} />
                        <h3 style={{
                            pointerEvents: 'none'
                        }}> 1</h3>
                        <p style={{
                            width: '100%',
                            textAlign: 'center',
                            fontSize: 13,
                            pointerEvents: 'none'
                        }}>
                                      
                        </p>
                    </div>
                    <div style={{
                        width: 200,
                        height: 300,
                        backgroundColor: '#fff',
                        borderRadius: 10,
                        boxShadow: '0 0 5px rgba(30, 30, 30, .4)',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'space-evenly',
                        padding: 20,
                    }} onMouseEnter={e => {
                        $(e.target).css({
                            outline: '3px solid dodgerblue'
                        })
                    }} onMouseLeave={e => {
                        if (tariff !== 2) $(e.target).css({
                            outline: '0'
                        })
                    }} id="tariff-2" onClick={e => {
                        e.preventDefault()
                        updateTariff(2)
                    }}>
                        <img src="/images/edit/talking.jpg" width="150" style={{
                            pointerEvents: 'none'
                        }} />
                        <h3 style={{
                            pointerEvents: 'none'
                        }}> 2</h3>
                        <p style={{
                            width: '100%',
                            textAlign: 'center',
                            fontSize: 13,
                            pointerEvents: 'none'
                        }}>
                                      
                        </p>
                    </div>
                </div>
                <div style={{
                    width: '80%',
                    display: 'flex',
                    flexDirection: 'column',
                    marginTop: 30
                }}>
                    <h2 style={{
                        marginBottom: 15
                    }}>:</h2>
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}> :</h3>
                    <input type="text" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder="" id="data-name" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}> :</h3>
                    <input type="text" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder="" id="data-surname" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}> :</h3>
                    <input type="text" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder="" id="data-fathername" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}>  (email):</h3>
                    <input type="email" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder="garrypotter@hogwards.com" id="data-email" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}>  :</h3>
                    <input type="tel" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder="+1 (123) 121 2313" id="data-tel" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}>  :</h3>
                    <input type="text" maxLength="100" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} placeholder=":     WhatsApp  ,     " id="data-tel" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} id="data-comment" />
                    <small style={{
                        color: '#aaa',
                        marginLeft: 15
                    }}>
                          ?      ,           .          ,   ..
                    </small>
                    <h3 style={{
                        marginTop: 10,
                        marginLeft: 15
                    }}>  :</h3>
                    <input type="date" maxLength="30" style={{
                        width: '100%',
                        height: 50,
                        marginLeft: 15,
                        marginTop: 5,
                        border: '1px solid #ccc',
                        outline: '0',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 15px',
                        fontSize: 16,
                        borderRadius: 5,
                    }} id="data-birthDate" onChange={e => {
                        $(e.target).css({
                            outline: '0'
                        })
                    }} />
                    <div style={{
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        width: '100%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 16,
                        margin: '20px 0 20px 15px',
                        borderRadius: 5,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        fontWeight: 600,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        if (!$('#data-name').val()) {
                            $('#data-name').css({
                                'outline': '1px solid red'
                            })
                        } else if (!$('#data-surname').val()) {
                            $('#data-surname').css({
                                'outline': '1px solid red'
                            })
                        } else if (!$('#data-fathername').val()) {
                            $('#data-fathername').css({
                                'outline': '1px solid red'
                            })
                        } else if (!$('#data-email').val()) {
                            $('#data-email').css({
                                'outline': '1px solid red'
                            })
                        } else if (!$('#data-tel').val()) {
                            $('#data-tel').css({
                                'outline': '1px solid red'
                            })
                        } else if (!$('#data-birthDate').val()) {
                            $('#data-birthDate').css({
                                'outline': '1px solid red'
                            })
                        } else {
                            $('#confirmation-code').css({ display: 'flex' })
                            $('#confirm-status').text(' ...')
                            fetch('https://crm.mindclickon.com/confirm', {
                                method: 'POST',
                                body: JSON.stringify({
                                    "name": $('#data-name').val(),
                                    "email": $('#data-email').val()
                                }),
                                headers: {
                                    'Content-Type': 'application/json'
                                }
                            })
                                .then(response => response.json())
                                .then(json => {
                                    console.log(json)
                                    if (json.status === true) {
                                        $('#confirm-status').text(`   ${$('#data-email').val()[0]}***${$('#data-email').val().replace(/\@.*/g, '')[$('#data-email').val().replace(/\@.*/g, '').length - 1]}@${$('#data-email').val().replace(/.*\@/g, '')}`)
                                        setCode(json.code)
                                        secondsToNew.current = 60
                                        interval.current = setInterval(() => {
                                            secondsToNew.current--
                                            $('#confirm-resend').text(`  ( ${secondsToNew.current})`)
                                            if (secondsToNew.current <= 0) {
                                                clearInterval(interval.current)
                                                interval.current = null
                                                secondsToNew.current = 0
                                                $('#confirm-resend').text(`  `)
                                            }
                                        }, 1000)
                                    } else {
                                        $('#confirmation-code').css({ display: 'none' })
                                        alert(' !')
                                    }
                                })
                        }
                    }}> </div>
                </div>
            </div>
            <div style={{
                position: 'absolute',
                top: '0',
                left: '0',
                width: '100%',
                height: '100%',
                backgroundColor: 'rgba(20, 20, 20, .3)',
                display: 'none',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 30,
            }} id="teachers-loader">
                <div style={{
                    width: 400,
                    height: 300,
                    backgroundColor: '#fff',
                    borderRadius: 10,
                    boxShadow: '0 0 10px rgba(30, 30, 30, .4)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    flexDirection: 'column'
                }}>
                    <h3 id="status-title">  ...</h3>
                    {ok === true ?
                        <img src="/images/edit/success.jpg" width="200" style={{
                            pointerEvents: 'none',
                            userSelect: 'none'
                        }} draggable="false" /> :
                        error ?
                            <img src="/images/edit/error.jpg" width="200" /> :
                            <Loader
                                type="MutatingDots"
                                width={100}
                                height={100}
                                visible={true}
                                color={'#00ef99'}
                                secondaryColor={'dodgerblue'}
                            />}
                    <h4 id="status">    .</h4>
                    <div style={{
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        width: '80%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 16,
                        borderRadius: 5,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        fontWeight: 600,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        $('#teachers-loader').css({
                            display: 'none'
                        })
                        setOk(false)
                        setError(false)
                        $('#status').text('    .')
                        $('#status-title').text('  ...')
                    }}></div>
                </div>
            </div>
            {/* Code Modal */}
            <div style={{
                position: 'absolute',
                top: '0',
                left: '0',
                width: '100%',
                height: '100%',
                backgroundColor: 'rgba(20, 20, 20, .3)',
                display: 'none',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 30,
            }} id="confirmation-code">
                <div style={{
                    width: 400,
                    height: 350,
                    backgroundColor: '#fff',
                    borderRadius: 10,
                    boxShadow: '0 0 10px rgba(30, 30, 30, .4)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-evenly',
                    flexDirection: 'column'
                }}>
                    <h3 id="confirm-status-title">   :</h3>
                    <input id="confirm-code" type="number" pattern="\\d+" placeholder=" " style={{
                        width: 275,
                        height: 50,
                        border: '1px solid #ccc',
                        borderRadius: 5,
                        fontSize: 16,
                        textAlign: 'center',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        outline: '0'
                    }} onChange={e => {
                        e.preventDefault()
                        $(e.target).val($(e.target).val().slice(0, 6))
                        $(e.target).css({ border: '1px solid #ccc' })
                    }} />
                    <h4 id="confirm-status"></h4>
                    <div style={{
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        width: '80%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 16,
                        borderRadius: 5,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        fontWeight: 600,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        if (!$('#confirm-code').val() || $('#confirm-code').val().length !== 6) {
                            $('#confirm-code').css({
                                border: '1px solid red'
                            })
                        } else {
                            if (!code) return;
                            if (String(code) === $('#confirm-code').val().replace(/\s*/g, '')) {
                                secondsToNew.current = 0;
                                if (interval.current) clearInterval(interval)
                                interval.current = null
                                $('#confirmation-code').css({ display: 'none' })
                                $('#teachers-loader').css({ display: 'flex' })
                                fetch('https://crm.mindclickon.com/application', {
                                    method: 'POST',
                                    body: JSON.stringify({
                                        "authenticationID": "1_mc_1_0x111",
                                        "requirements": {
                                            "calls": tariff === 2 ? true : false
                                        }
                                    }),
                                    headers: {
                                        'Content-Type': 'application/json'
                                    }
                                })
                                    .then(response => response.json())
                                    .then(data => {
                                        console.log(data)
                                        if (!data.status)
                                            return $('#status').text('  !')
                                        $('#status').text(` ${data.teachers.length} ...`)
                                        $('#status-title').text(` ...`)
                                        setTimeout(() => {
                                            $('#status-title').text(` ...`)
                                            var sended = 0
                                            var isOk = true
                                            for (var i = 0; i < data.teachers.length; i++) {
                                                console.log(i)
                                                fetch('https://crm.mindclickon.com/invite', {
                                                    method: 'POST',
                                                    body: JSON.stringify({
                                                        teacher: data.teachers[i],
                                                        form: {
                                                            name: $('#data-name').val(),
                                                            surname: $('#data-surname').val(),
                                                            fathername: $('#data-fathername').val(),
                                                            email: $('#data-email').val(),
                                                            tel: $('#data-tel').val(),
                                                            comment: $('#data-comment').val(),
                                                            birthDate: $('#data-birthDate').val()
                                                        }
                                                    }),
                                                    headers: {
                                                        'Content-Type': 'application/json'
                                                    }
                                                })
                                                    .then(response => response.json())
                                                    .then(json => {
                                                        if (json.status === true) {
                                                            if (!isOk) return;
                                                            sended++;
                                                            $('#status').text(` ${sended}  ${data.teachers.length} ...`)
                                                            if (sended >= data.teachers.length) {
                                                                setOk(true)
                                                                $('#status-title').text('!')
                                                                $('#status').text(' !')
                                                            }
                                                        } else {
                                                            isOk = false
                                                            if (!error) setError(true)
                                                            $('#status-title').text('!')
                                                            $('#status').text(json.reason === 'data-exist' ? '   !' : json.reason === 'tel' ? '  !' : '-  !')
                                                        }
                                                    })
                                            }
                                        }, 2500)
                                    })
                                    .catch(err => console.log(err));
                            } else {
                                $('#confirm-code').css({
                                    border: '1px solid red'
                                })
                                $('#confirm-status').text(' ...')
                            }
                        }
                    }}></div>
                    <div style={{
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        width: '80%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 16,
                        borderRadius: 5,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        fontWeight: 600,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        if (secondsToNew.current <= 0 && !interval.current) {
                            $('#confirm-status').text(' ...')
                            fetch('https://crm.mindclickon.com/confirm', {
                                method: 'POST',
                                body: JSON.stringify({
                                    "name": $('#data-name').val(),
                                    "email": $('#data-email').val()
                                }),
                                headers: {
                                    'Content-Type': 'application/json'
                                }
                            })
                                .then(response => response.json())
                                .then(json => {
                                    console.log(json)
                                    if (json.status === true) {
                                        $('#confirm-status').text(`   ${$('#data-email').val()[0]}***${$('#data-email').val().replace(/\@.*/g, '')[$('#data-email').val().replace(/\@.*/g, '').length - 1]}@${$('#data-email').val().replace(/.*\@/g, '')}`)
                                        setCode(json.code)
                                        secondsToNew.current = 60
                                        interval.current = setInterval(() => {
                                            secondsToNew.current--
                                            $('#confirm-resend').text(`  ( ${secondsToNew.current})`)
                                            if (secondsToNew.current <= 0) {
                                                clearInterval(interval.current)
                                                interval.current = null
                                                secondsToNew.current = 0
                                                $('#confirm-resend').text(`  `)
                                            }
                                        }, 1000)
                                    } else {
                                        $('#confirmation-code').css({ display: 'none' })
                                        alert(' !')
                                    }
                                })
                        }
                    }} id="confirm-resend">  </div>
                    <div style={{
                        backgroundColor: '#00ef99',
                        color: '#fff',
                        width: '80%',
                        height: 50,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: 16,
                        borderRadius: 5,
                        boxShadow: '0 0 5px rgba(40, 40, 40, .4)',
                        fontWeight: 600,
                        cursor: 'pointer'
                    }} onClick={e => {
                        e.preventDefault()
                        secondsToNew.current = 0;
                        if (interval.current) clearInterval(interval)
                        interval.current = null
                        $('#confirmation-code').css({ display: 'none' })
                    }} id="confirm-resend"></div>
                </div>
            </div>
        </div>
    )
}

export default Application