import React from 'react';

import $ from 'jquery';
import Button from '../../../components/Button';

const ComingSoonModal = () => {
    return (
        <div id="coming-soon-modal" style={{
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: 0,
            left: 0,
            backgroundColor: 'rgba(0, 0, 0, .2)',
            display: 'none',
            justifyContent: 'center',
            alignItems: 'center',
            zIndex: 10
        }}>
            <div style={{
                width: '45%',
                height: '80%',
                borderRadius: 10,
                backgroundColor: '#FFF6D9',
                boxShadow: '0 0 5px rgba(20, 20, 20, .4)',
                display: 'flex',
                alignItems: 'center',
                paddingTop: 20,
                flexDirection: 'column'
            }}>
                <h2>...</h2>
                <img src="/images/edit/coming-soon.jpg" width="500" />
                <Button
                    text={''}
                    onClick={e => {
                        e.preventDefault()
                        $('#coming-soon-modal').css({
                            display: 'none'
                        })
                    }}
                />
            </div>
        </div>
    )
}

export default ComingSoonModal;